import React from 'react';
import { Link } from 'react-router-dom';

import dummyImage from '../../../assets/img/tool-new.svg';
import Approve from '../../../assets/img/approve.svg';
import Deny from '../../../assets/img/deny.svg';
import Requested from '../../../assets/img/requested.png';
import { toast } from 'react-toastify';

import { ApiRoutes, AppConfig } from '../../../utils/config';
import { FetchFromServer } from '../../../utils/helpers';


const MaterialRequest = (props) => {
  
  const { data } = props;
  const changeStatus = async (event,id,status) =>{
    event.preventDefault();
    console.log('testing',id);
    const taskData = {'status':status};
    const response = await FetchFromServer(
      ApiRoutes.CHANGE_TASK_MATERIAL_STATUS.service,
      ApiRoutes.CHANGE_TASK_MATERIAL_STATUS.url.replace(':id', id),
      ApiRoutes.CHANGE_TASK_MATERIAL_STATUS.method,
      ApiRoutes.CHANGE_TASK_MATERIAL_STATUS.authenticate,
      undefined,
      taskData
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      props.getLiveFeed();
      props.refreshMaterial();
      toast.success('Status has been changed successfully');
    }
  }

  return (
    <>
      { data.type==='Admin' ?
      (<div className="material_request">
        <div className="card">
          <div className="card-header">
            <h6 className="m-0 text-center">MATERIAL REQUEST</h6>
          </div>
          <div className="card-body">
            <div className="d-flex align-items-center p-3 row_1">
              <div className="img_box mr-2">
                <img
                  src={
                    data.materialImage
                      ? data.materialImage.startsWith('https://')
                        ? data.materialImage
                        : `${AppConfig.FILES_ENDPOINT}${data.materialImage}`
                      : dummyImage
                  }
                  onError={(e) => {
                    e.target.onerror = null;
                    e.target.src = dummyImage;
                  }}
                  // width={42}
                  // height={29}
                  width={'100%'}
                  alt={data.materialImage}
                />
              </div>

              <p className="m-0">{data.materialDescription}</p>
            </div>

            <table className="table table-responsive m-0">
              <tbody>
                <tr>
                  <td>USER</td>
                  <td className="value text_blue">
                    {data.firstName} {data.lastName}
                  </td>
                </tr>
                <tr>
                  <td>QTY</td>
                  <td className="value">{data.materialUsed} units</td>
                </tr>
                <tr>
                  <td>PART ID</td>
                  <td className="value">{data.salesPartNumber}</td>
                </tr>
                <tr>
                  <td>IN STOCK</td>
                  <td className="value">{data.inStock} units</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="card-footer"> {/* add class when user only request > .requested */}
            <Link to="#" onClick={(e) => changeStatus(e, data._id,'Approved')}>
              <img src={Approve} alt={Approve} /> Approve
            </Link>
            <Link to="#" onClick={(e) => changeStatus(e, data._id,'Deny')}>
              <img src={Deny} alt={Deny} /> Deny
            </Link>

            {/* <Link to="#" onClick={(e) => changeStatus(e, data._id,'Requested')}>
              <img src={Requested} alt={Requested} style={{ width: "8px" }} /> Requested
            </Link> */}
          </div>
        </div>
      </div>) : (
        <div className="material_request">
        <div className="card">
          <div className="card-header exchange_request_div">
            <h6 className="m-0 text-center">USER EXCHANGE REQUEST</h6>
          </div>
          <div className="card-body">
            <div className="d-flex align-items-center p-3 row_1">
              <div className="img_box mr-2">
                <img
                  src={
                    data.materialImage
                      ? data.materialImage.startsWith('https://')
                        ? data.materialImage
                        : `${AppConfig.FILES_ENDPOINT}${data.materialImage}`
                      : dummyImage
                  }
                  onError={(e) => {
                    e.target.onerror = null;
                    e.target.src = dummyImage;
                  }}
                  // width={42}
                  // height={29}
                  width={'100%'}
                  alt={data.materialImage}
                />
              </div>
              <p className="m-0">{data.materialDescription}</p>
            </div>
            <table className="table table-responsive m-0">
              <tbody>
                <tr>
                  <td>FROM</td>
                  <td className="value text_blue">
                    {data.firstName} {data.lastName}
                  </td>
                </tr>
                <tr>
                  <td>TO</td>
                  <td className="value text_blue">
                    {data.colleaguefirstName} {data.colleaguelastName}
                  </td>
                </tr>
                <tr>
                  <td>QTY</td>
                  <td className="value">{data.materialUsed} units</td>
                </tr>
                <tr>
                  <td>PART ID</td>
                  <td className="value">{data.salesPartNumber}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="card-footer">
            <Link to="#">
              <img src={Approve} alt={Approve} /> Requested
            </Link>
            
          </div>
        </div>
      </div>
      )
      }
    </>
  );
};

export { MaterialRequest };
