import React, { useState, useEffect } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import Skeleton from 'react-loading-skeleton';

import { Col, Container, Row, Card, Button } from 'react-bootstrap';
import { InnerPageHeader } from '../../components/Rate/InnerPageHeader';
import eyeIcon from '../../../assets/img/eyeIcon.svg';
import NotFound from '../../../assets/img/on_data_found.svg';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

const RuleSchedule = (props) => {
  const { setScheduleName } = props;
  const { id } = useParams();
  const history = useHistory();
  const [technologyData, setTechnologyData] = useState(false);
  const [ruleData, setRuleData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [technologyId, setTechnologyId] = useState('');
  const [colors] = useState([
    'primary',
    'orange',
    'yellow',
    'primary',
    'orange',
    'yellow',
  ]);
  const [tableFilter, setTableFilter] = useState({
    schedule: id,
    technology: '',
  });

  useEffect(() => {
    setScheduleName('');
    const getTechnologyList = async (schedule = '') => {
      const response = await FetchFromServer(
        ApiRoutes.TECHNOLOGY_LIST.service,
        ApiRoutes.TECHNOLOGY_LIST.url,
        ApiRoutes.TECHNOLOGY_LIST.method,
        ApiRoutes.TECHNOLOGY_LIST.authenticate,
        { schedule: schedule },
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTechnologyData(response.data.data);
      }
    };
    const getScheduleInfo = async () => {
      const response = await FetchFromServer(
        ApiRoutes.SCHEDULE_INFO.service,
        ApiRoutes.SCHEDULE_INFO.url.replace(':id', id),
        ApiRoutes.SCHEDULE_INFO.method,
        ApiRoutes.SCHEDULE_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: '/rates/rules',
        });
      } else {
        let data = response.data.data;
        setScheduleName(data.scheduleName);
      }
    };
    getTechnologyList(id);
    getScheduleInfo();
  }, []);

  useEffect(() => {
    const getRuleList = async () => {
      setIsLoading(true);
      const response = await FetchFromServer(
        ApiRoutes.BUSINESS_RULE_LIST.service,
        ApiRoutes.BUSINESS_RULE_LIST.url,
        ApiRoutes.BUSINESS_RULE_LIST.method,
        ApiRoutes.BUSINESS_RULE_LIST.authenticate,
        tableFilter,
        undefined
      );
      setIsLoading(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        let data = response.data.data;
        setRuleData(data);
      }
    };
    getRuleList();
  }, [tableFilter]);

  const handleSearch = async (technology) => {
    if (technologyId === technology) {
      setTechnologyId('');
      setTableFilter({
        ...tableFilter,
        technology: '',
      });
    } else {
      setTechnologyId(technology);
      setTableFilter({
        ...tableFilter,
        technology: technology,
      });
    }
  };
  return (
    <>
      <InnerPageHeader activeTab="businessRules" />
      <Container fluid className="businessSchedule">
        <Row className="mt-4 pb-5">
          <Col sm={12}>
            <div className="d-flex align-items-center mb-5 overflow-auto ruleSchedule_filter">
              <p class="mb-0 mr-3">Filter by</p>
              {technologyData &&
                technologyData.map((data, key) => {
                  return (
                    <Button
                      variant=""
                      className={`bg-white btn mr-4 ${
                        technologyId === data._id ? 'active' : ''
                      }`}
                      onClick={() => handleSearch(data._id)}
                    >
                      {data.technologyName}
                    </Button>
                  );
                })}
            </div>
          </Col>

          {isLoading === true ? (
            <>
              <Col sm={12} className="mb-2">
                <h6
                  class={`font-weight-bold pl-4 headTitle position-relative primary`}
                >
                  <Skeleton style={{ width: 100 }} />
                </h6>
              </Col>
              <Col sm={12}>
                <Row>
                  <Col sm={4} md={3} lg={3} xl={2}>
                    <Card className="d-flex align-items-center justify-content-center my-2">
                      <Skeleton style={{ height: 170, width: 134 }} />
                    </Card>
                  </Col>
                  <Col sm={4} md={3} lg={3} xl={2}>
                    <Card className="d-flex align-items-center justify-content-center my-2">
                      <Skeleton style={{ height: 170, width: 134 }} />
                    </Card>
                  </Col>
                  <Col sm={4} md={3} lg={3} xl={2}>
                    <Card className="d-flex align-items-center justify-content-center my-2">
                      <Skeleton style={{ height: 170, width: 134 }} />
                    </Card>
                  </Col>
                  <Col sm={4} md={3} lg={3} xl={2}>
                    <Card className="d-flex align-items-center justify-content-center my-2">
                      <Skeleton style={{ height: 170, width: 134 }} />
                    </Card>
                  </Col>
                  <Col sm={4} md={3} lg={3} xl={2}>
                    <Card className="d-flex align-items-center justify-content-center my-2">
                      <Skeleton style={{ height: 170, width: 134 }} />
                    </Card>
                  </Col>
                  <Col sm={4} md={3} lg={3} xl={2}>
                    <Card className="d-flex align-items-center justify-content-center my-2">
                      <Skeleton style={{ height: 170, width: 134 }} />
                    </Card>
                  </Col>
                </Row>
              </Col>
            </>
          ) : (
            ''
          )}

          {isLoading === false
            ? ruleData.map((item, key) => {
                return (
                  <>
                    <Col sm={12} className="mb-2" key={key}>
                      <h6
                        class={`font-weight-bold pl-4 headTitle position-relative ${colors[key]}`}
                      >
                        {item.categoryName}
                      </h6>
                    </Col>

                    <Col sm={12}>
                      <Row>
                        {item.businessrules.map((item1, key1) => {
                          return (
                            <Col sm={4} md={3} lg={3} xl={2} key={key1}>
                              <Card className="d-flex align-items-center justify-content-center my-2">
                                <p className="mb-0 text-center">{item1.name}</p>
                                <Link
                                  to={`/rates/rules/setrule/${item1._id}`}
                                  className="d-contents"
                                >
                                  <img
                                    className="eyeIcon"
                                    src={eyeIcon}
                                    alt={eyeIcon}
                                  />{' '}
                                </Link>
                              </Card>
                            </Col>
                          );
                        })}
                      </Row>
                    </Col>
                    {ruleData.length - 1 === key ? (
                      ''
                    ) : (
                      <Col sm={12} className="my-4">
                        <hr />
                      </Col>
                    )}
                  </>
                );
              })
            : ''}
          <Col sm={12} className="mb-2">
            {ruleData.length === 0 && isLoading === false ? (
              <Card.Body className="py-3 px-4">
                <div className="d-flex align-items-center justify-content-center py-5">
                  <div className="position-relative noDataFound">
                    <img src={NotFound} alt={NotFound} />
                    <h6 className="mb-0">No Data Available</h6>
                    <p>The data you're looking for is not available</p>
                  </div>
                </div>
              </Card.Body>
            ) : (
              ''
            )}
          </Col>
        </Row>
      </Container>
    </>
  );
};

export { RuleSchedule };
