import React, { useState, useRef } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';

import {
  Nav,
  Tab,
  Form,
  Button,
  Col,
  Container,
  Row,
  Card,
} from 'react-bootstrap';
import gobackImg from '../../../assets/img/goback.svg';
import Reset from '../../../assets/img/Reset.svg';

import { AddToolSchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { ToolInformation } from '../../components/User';

const AddTool = () => {
  const { id } = useParams();
  const form = useRef(null);
  const history = useHistory();
  const [isBtnDisable, setDisable] = useState(false);
  const { handleSubmit, handleChange, values, errors, touched, handleReset } =
    useFormik({
      initialValues: {
        manufacturer: '',
        description: '',
        serialNumber: '',
        lastCalibration: '',
        nextCalibration: '',
        certification: '',
      },
      validationSchema: AddToolSchema,
      onSubmit: async (values) => {
        const formData = new FormData(form.current);

        // return false;
        setDisable(true);
        const response = await FetchFromServer(
          ApiRoutes.ADD_TOOL.service,
          ApiRoutes.ADD_TOOL.url.replace(':id', id),
          ApiRoutes.ADD_TOOL.method,
          ApiRoutes.ADD_TOOL.authenticate,
          undefined,
          formData
        );
        setDisable(false);
        if (response && response.isError) {
          toast.error(response.messages[0]);
        } else {
          history.push({
            pathname: '/users/tools/' + id,
          });
        }
      },
    });

  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link
          to={`/users/tools/${id}`}
          className="btn btn_goback p-0"
          role="button"
        >
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>

      <Container fluid className="addNewUserPage">
        <Row>
          <Col
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            className="mb-3 mt-4 addToolPage"
          >
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form ref={form} onSubmit={handleSubmit}>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="profile"
                    >
                      <div className="d-flex align-items-center justify-content-between position-relative">
                        <Nav
                          as="ul"
                          className="nav-pills mb-3 position-relative w-100"
                        >
                          <Nav.Item as="li">
                            <Nav.Link eventKey="profile">
                              Tool Information
                            </Nav.Link>
                          </Nav.Item>
                        </Nav>
                        <Button
                          variant=""
                          className="toolResetBtn"
                          onClick={handleReset}
                        >
                          {' '}
                          <img className="mr-1" src={Reset} /> Reset
                        </Button>
                      </div>
                      <Tab.Content>
                        <Tab.Pane eventKey="profile">
                          <ToolInformation
                            handleChange={handleChange}
                            errors={errors}
                            values={values}
                            touched={touched}
                            handleSubmit={handleSubmit}
                            isBtnDisable={isBtnDisable}
                            isAdd={true}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export { AddTool };
