import React, { useEffect, useState, useRef } from 'react';
import { Link, useParams, useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';
import InnerPageHeader from '../../components/User/InnerPageHeader';
import '../../../assets/css/Custom.css';

import plusImg from '../../../assets/img/plus.svg';


import previewEyeImg from '../../../assets/img/previewEye.svg';
import editImg from '../../../assets/img/edit.svg';
import galleryImg from '../../../assets/img/gallery.svg';
import addCardImg from '../../../assets/img/addCard.svg';
import uploadCloudImg from '../../../assets/img/uploadCloud.svg';
import popupClose from './../../../assets/img/popupClose.svg';
import handCard from './../../../assets/img/handCard.svg';
import dummyImage from '../../../assets/img/noimage.jpeg';
import modalDeleteBtn from './../../../assets/img/modaldelete_btn.svg';

import moment from 'moment';
import DatePicker from 'react-datepicker';
import { Document, Page, pdfjs } from "react-pdf";


import {
  Container,
  Row,
  Col,
  Card,
  Button,
  Tab,
  Form,
  Nav,
  Spinner,
  Modal,
} from 'react-bootstrap';

import {
  ProfileInformation,
  EmergencyInformation,
} from '../../components/User';

import { AddUserSchema, AddComplianceSchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes, AppConfig } from '../../../utils/config';

const EditUser = () => {
  const form = useRef(null);
  const complianceForm = useRef(null);
  const { id } = useParams();
  const history = useHistory();
  const [show, setShow] = useState(false);
  const [userData, setUserData] = useState({});
  const [complianceData, setComplianceData] = useState();
  const [isBtnDisable, setDisable] = useState(false);
  const [isCardBtnDisable, setCardBtnDisable] = useState(false);
  const [isDeleteBtnDisable, setDeleteBtnDisable] = useState(false);
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const handleDeleteClose = () => setDeleteModalShow(false);
  const [successModalShow, setSuccessModalShow] = useState(false);
  const handleSuccessClose = () => setSuccessModalShow(false);
  const [documentShow, setDocumentShow] = useState(false);
  const handleDocumentClose = () => setDocumentShow(false);
  const [documentTitle, setDocumentTitle] = useState('');
  const [documentFiles, setDocumentFiles] = useState();
  const [isEdit, setIsEdit] = useState(-1);
  const [cardBackImageName, setCardBackImageName] = useState('Back-image');
  const [cardFrontImageName, setCardFrontImageName] = useState('Front-image');
  const [statementName, setStatementName] = useState(
    'Click or drag to upload files'
  );

  useEffect(() => {
    
    getUser();
    getCompliance();
    // eslint-disable-next-line
  }, []);

  const { handleSubmit, handleChange, values, errors, setFieldValue, touched } =
    useFormik({
      initialValues: {},
      validationSchema: AddUserSchema,
      onSubmit: async (values) => {
        setDisable(true);
        const formData = new FormData(form.current);
        formData.append('complianceData', JSON.stringify(complianceData));
        const response = await FetchFromServer(
          ApiRoutes.EDIT_USER.service,
          ApiRoutes.EDIT_USER.url.replace(':id', id),
          ApiRoutes.EDIT_USER.method,
          ApiRoutes.EDIT_USER.authenticate,
          undefined,
          formData
        );
        setDisable(false);
        if (response && response.isError) {
          toast.error(response.messages[0]);
        } else {
          history.push({
            pathname: '/users',
          });
        }
      },
    });
  
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  const {
    handleSubmit: handleComplianceSubmit,
    handleChange: handleComplianceChange,
    values: complianceValues,
    errors: complianceErrors,
    touched:complianceTouched,
    setFieldValue: complianceSetFieldValue,
  } = useFormik({
    initialValues: {
      cardName: '',
      cardNumber: '',
      issueDate: '',
      expiryDate: '',
    },
    validationSchema: AddComplianceSchema,
    onSubmit: async (values, { resetForm }) => {
      setCardBtnDisable(true);
      const formData = new FormData(complianceForm.current);
      const response = await FetchFromServer(
        ApiRoutes.ADD_COMPLIANCE_TEMP.service,
        ApiRoutes.ADD_COMPLIANCE_TEMP.url,
        ApiRoutes.ADD_COMPLIANCE_TEMP.method,
        ApiRoutes.ADD_COMPLIANCE_TEMP.authenticate,
        undefined,
        formData
      );
      setCardBtnDisable(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        let data = complianceData;
        if (isEdit === -1) {
          data.unshift(response.data.data);
          setSuccessModalShow(true);
        } else {
          data[isEdit] = response.data.data;
          if (data[isEdit].statementImage === '')
            data[isEdit].statementImage = complianceValues.statementImage;

          if (data[isEdit].cardFrontImage === '')
            data[isEdit].cardFrontImage = complianceValues.cardFrontImage;

          if (data[isEdit].cardBackImage === '')
            data[isEdit].cardBackImage = complianceValues.cardBackImage;
          setIsEdit(-1);
        }
        setComplianceData(data);
        handleClose();
        // setShow(false);
      }
    },
  });

  async function getUser() {
    const response = await FetchFromServer(
      ApiRoutes.USER_INFO.service,
      ApiRoutes.USER_INFO.url.replace(':id', id),
      ApiRoutes.USER_INFO.method,
      ApiRoutes.USER_INFO.authenticate,
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
      history.push({
        pathname: '/users',
      });
    } else {
      let data = response.data.data;
      console.log('data', data);
      values.firstName = data.firstName;
      values.lastName = data.lastName;
      values.email = data.email;
      values.phoneNumber = data.phoneNumber;
      values.dateOfBirth = data.dateOfBirth ? moment(data.dateOfBirth).format('YYYY-MM-DD') :'';
      values.contractorID = data.contractorID;
      values.enableID = data.enableID;
      values.profilePicture = data.profilePicture
        ? `${AppConfig.FILES_ENDPOINT}${data.profilePicture}`
        : '';
        
      values.emergency = [
        {
          firstName: data.emergency.firstName,
          lastName: data.emergency.lastName,
          relationship: data.emergency.relationship,
          email: data.emergency.email,
          phoneNumber: data.emergency.phoneNumber,
        },
      ];
      setUserData(values);
    }
  }
  async function getCompliance() {
    const complianceResponse = await FetchFromServer(
      ApiRoutes.COMPLIANCE_LIST.service,
      ApiRoutes.COMPLIANCE_LIST.url.replace(':id', id),
      ApiRoutes.COMPLIANCE_LIST.method,
      ApiRoutes.COMPLIANCE_LIST.authenticate,
      undefined
    );
    if (complianceResponse && complianceResponse.isError) {
      toast.error(complianceResponse.messages[0]);
    } else {
      setComplianceData(complianceResponse.data.data);
    }
  }

  

  const handleClose = () => {
    complianceValues.cardName = '';
    complianceValues.cardNumber = '';
    complianceValues.issueDate = '';
    complianceValues.naExpiry = false;
    complianceValues.expiryDate = '';
    complianceValues.naCardImage = false;
    complianceValues.cardFrontImage = '';
    complianceValues.cardBackImage = '';
    complianceValues.naStatement = false;
    complianceValues.statementImage = '';
    setCardBackImageName('Back-image');
    setCardFrontImageName('Front-image');
    setStatementName('Click or drag to upload files');
    setShow(false);
  };

  const deleteUser = async () => {
    setDeleteBtnDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.DELETE_USER.service,
      ApiRoutes.DELETE_USER.url.replace(':id', id),
      ApiRoutes.DELETE_USER.method,
      ApiRoutes.DELETE_USER.authenticate,
      undefined
    );
    setDeleteBtnDisable(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      toast.error(response.messages[0]);
      history.push({
        pathname: '/users',
      });
    }
  };

  const handleShow = () => {
    setIsEdit(-1);
    setShow(true);
  };

  const viewDocument = async (title, files) => {
    /*if (title === 'Statement of Attainment') {
      window.location.href = AppConfig.FILES_ENDPOINT + files;
      return;
    }*/
    pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;
    setDocumentTitle(title);
    setDocumentFiles(files);
    setDocumentShow(true);
  };

  const editCard = async (index) => {

    setDocumentTitle(complianceData[index].cardName);
    setDocumentFiles([
      complianceData[index].cardFrontImage,
      complianceData[index].cardBackImage
    ]);
    
    complianceValues.cardName = complianceData[index].cardName;
    complianceValues.cardNumber = complianceData[index].cardNumber;
    complianceValues.naExpiry = complianceData[index].naExpiry;
    complianceValues.issueDate = complianceData[index].issueDate ? moment(complianceData[index].issueDate).format(
      'YYYY-MM-DD'
    ) :'';
    complianceValues.expiryDate = complianceData[index].expiryDate ? moment(
      complianceData[index].expiryDate
    ).format('YYYY-MM-DD') :'';
    complianceValues.naCardImage = complianceData[index].naCardImage;
    complianceValues.cardFrontImage = complianceData[index].cardFrontImage;
    complianceValues.cardBackImage = complianceData[index].cardBackImage;
    complianceValues.naStatement = complianceData[index].naStatement;
    complianceValues.statementImage = complianceData[index].statementImage;
    setIsEdit(index);
    setShow(true);
  };
  const removeFile = async (key) => {
    complianceSetFieldValue(key, '');
  };

  const cardFrontImageChangedHandler = (event) => {
    const file = event.target.files;
    if (file.length === 0) {
      setCardFrontImageName('Front-image');
    } else {
      setCardFrontImageName(file[0].name);
    }
  };
  const cardBackImageChangedHandler = (event) => {     
    const file = event.target.files;
    if (file.length === 0) {
      setCardBackImageName('Back-image');
    } else {
      setCardBackImageName(file[0].name);
    }
  };
  const statementChangedHandler = (event) => {
    const file = event.target.files;
    if (file.length === 0) {
      setStatementName('Click or drag to upload files');
    } else {
      setStatementName(file[0].name);
    }
  };
  return (
    <>
      <InnerPageHeader
        id={id}
        email={userData ? userData.email : ''}
        mobile={userData ? userData.phoneNumber : ''}
        activeTab="editProfile"
      />
      <Container fluid className="addNewUserPage">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3 mt-4">
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form
                    ref={form}
                    enablereinitialize="true"
                    onSubmit={handleSubmit}
                  >
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="profile"
                    >
                      <Nav as="ul" className="nav-pills mb-3 position-relative">
                        <Nav.Item as="li">
                          <Nav.Link eventKey="profile">
                            Profile Information
                          </Nav.Link>
                        </Nav.Item>
                        <Nav.Item as="li">
                          <Nav.Link eventKey="emergency">
                            Emergency Information
                          </Nav.Link>
                        </Nav.Item>
                      </Nav>
                      <Tab.Content>
                        <Tab.Pane eventKey="profile">
                          <ProfileInformation
                            handleChange={handleChange}
                            errors={errors}
                            values={values}
                            touched={touched}
                            setFieldValue={setFieldValue}
                          />
                        </Tab.Pane>
                        <Tab.Pane eventKey="emergency">
                          <EmergencyInformation
                            handleChange={handleChange}
                            errors={errors}
                            values={values}
                            touched={touched}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>

          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3">
            <Card>
              <Card.Body>
                <div className="p-24">
                  <ul className="nav nav-pills compliances position-relative mb-3">
                    <li className="nav-item">
                      <Link to="#" className="nav-link active">
                        Compliances
                      </Link>
                    </li>
                  </ul>

                  <div className="tab-content" id="pills-tabContent">
                    <div
                      className="tab-pane fade show active"
                      id="pills-compliances"
                      role="tabpanel"
                      aria-labelledby="pills-compliances-tab"
                    >
                      <Row>
                        {complianceData &&
                          complianceData.map((data, index) => {
                            return (
                              <Col
                                xs={12}
                                sm={12}
                                md={6}
                                lg={6}
                                xl={4}
                                key={data._id}
                              >
                                <Card className="addCardProfile">
                                  <Card.Body>
                                    <div className="p-24">
                                      <div className="d-flex align-items-start justify-content-between">
                                        <div className="d-flex flex-column">
                                          <h5 className="mb-0">
                                            {data.cardName}
                                          </h5>
                                          <p className="mb-0">
                                            {data.cardNumber}
                                          </p>
                                        </div>
                                        <div className="d-flex align-items-center">
                                          <img
                                            className="mr-2"
                                            src={galleryImg}
                                            alt={galleryImg}
                                            onClick={() =>
                                              viewDocument(data.cardName, [
                                                data.cardFrontImage,
                                                data.cardBackImage,
                                              ])
                                            }
                                          />

                                          <img
                                            src={editImg}
                                            alt={editImg}
                                            onClick={() => editCard(index)}
                                          />
                                        </div>
                                      </div>

                                      <div className="attach_preview">
                                        <p className="mb-0">
                                          Statement of attainment_Mark.PDF
                                        </p>
                                        {data.statementImage ? (
                                          <img
                                            src={previewEyeImg}
                                            alt={previewEyeImg}
                                            onClick={() =>
                                              viewDocument(
                                                'Statement of Attainment',
                                                data.statementImage
                                              )
                                            }
                                          />
                                        ) : (
                                          ''
                                        )}
                                      </div>

                                      <div className="validity">
                                        <div>
                                          <span>Issue Date</span>{' '}
                                          {data.issueDate
                                            ? moment(data.issueDate).format(
                                                'MM/DD/YYYY'
                                              )
                                            : 'N/A'}
                                        </div>
                                        <div>
                                          <span>Expiry Date</span>{' '}
                                          {data.expiryDate
                                            ? moment(data.expiryDate).format(
                                                'MM/DD/YYYY'
                                              )
                                            : 'N/A'}
                                        </div>
                                      </div>
                                    </div>
                                  </Card.Body>
                                </Card>
                              </Col>
                            );
                          })}

                        <Col md={12}>
                          <button
                            className="btn addCard mt-3 mb-3"
                            onClick={handleShow}
                          >
                            <img className="mr-2" src={plusImg} alt={plusImg} />{' '}
                            Add a Card
                          </button>
                        </Col>
                      </Row>
                    </div>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12} className="mb-5 text-right pageButtomBtn">
            <Button
              variant=""
              className="px-5 py-2 rounded addcard form_btn"
              style={{ background: '#E5EAEE', fontSize: '14px' }}
              onClick={() => setDeleteModalShow(true)}
            >
              Delete User
            </Button>
            <Button
              variant=""
              className="px-5 py-2 rounded btn-pink ml-3 form_btn"
              style={{ fontSize: '14px' }}
              onClick={handleSubmit}
              disabled={isBtnDisable}
            >
              {isBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Save Updates
            </Button>
          </Col>
        </Row>
      </Container>

      <Modal
        show={show}
        onHide={handleClose}
        className="addCardPopup d-flex align-items-center justify-content-center"
      >
        <span className="modelClose" closeButton onClick={handleClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body>
          <Form ref={complianceForm} onSubmit={handleComplianceSubmit}>
            <Row className="pt-5 pb-4 px-3 tab-content">
              <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4 mb-2">
                <Form.Group>
                  <label>Card Name</label>
                  <input
                    type="text"
                    placeholder="Item Name"
                    className="form-control"
                    name="cardName"
                    value={complianceValues ? complianceValues.cardName : ''}
                    onChange={handleComplianceChange}
                  />
                  <span class="errorMsg">
                    {complianceErrors.cardName && complianceTouched.cardName
                      ? complianceErrors.cardName
                      : null}
                  </span>
                </Form.Group>
              </div>
              <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4 mb-2">
                <Form.Group>
                  <label>Card Number</label>
                  <input
                    type="text"
                    placeholder="ID1029"
                    className="form-control"
                    name="cardNumber"
                    value={complianceValues ? complianceValues.cardNumber : ''}
                    onChange={handleComplianceChange}
                  />
                  <span class="errorMsg">
                    {complianceErrors.cardNumber && complianceTouched.cardNumber
                      ? complianceErrors.cardNumber
                      : null}
                  </span>
                </Form.Group>
              </div>
              <div className="col-xs-12 col-sm-12 col-md-6 col-lg-4 mb-2 front_back_card">
                <Form.Group className="w-100">
                  <div className="d-flex align-items-center justify-content-between mb-2">
                    <label className="mb-0">Upload Card Front and Back</label>
                    <div>
                      <Form.Group
                        controlId="formBasicCheckbox"
                        className="mb-0"
                      >
                        <Form.Check
                          type="checkbox"
                          name="naCardImage"
                          label="N/A"
                          onChange={handleComplianceChange}
                          value={true}
                          checked={
                            complianceValues
                              ? complianceValues.naCardImage
                              : false
                          }
                        />
                      </Form.Group>
                    </div>
                  </div>
                  <div className="d-flex two_uploads">
                    {complianceValues.cardFrontImage ? (
                      <div className="position-relative uploadCloud w-100 mr-2">
                        <p className="mb-0">
                          <img
                            className="mr-0"
                            width="20px"
                            src={
                              documentFiles && documentFiles[0] && documentFiles[0]!==''
                                ? documentFiles[0].startsWith('https://')
                                  ? documentFiles[0]
                                  : `${AppConfig.FILES_ENDPOINT}${documentFiles[0]}`
                                : dummyImage
                            }
                            
                          />{' '}
                          <span style={{width: '19px'}}>
                            <img src={modalDeleteBtn} alt={modalDeleteBtn}
                            onClick={() => removeFile('cardFrontImage')} />
                          </span>
                          <span>{cardFrontImageName}</span>
                        </p>
                      </div>
                    ) : (
                      <div className="position-relative uploadCloud w-100 mr-2 cardimage">
                        <input
                          type="file"
                          name="cardFrontImage"
                          accept="image/*"
                          onChange={cardFrontImageChangedHandler}
                          // multiple
                        />
                        <p className="mb-0">
                          <img
                            className="mr-0"
                            src={uploadCloudImg}
                            alt={uploadCloudImg}
                          />{' '}
                          <span>{cardFrontImageName}</span>
                        </p>
                      </div>
                    )}
                    <span class="errorMsg">
                      {complianceErrors && complianceErrors.cardFrontImage
                        ? complianceErrors.cardFrontImage
                        : null}
                    </span>

                    {complianceValues.cardBackImage ? (
                      <div className="position-relative uploadCloud w-100 mr-2">
                        <p className="mb-0">
                          <img
                            className="mr-0"
                            width="20px"
                            src={
                              documentFiles && documentFiles[1] && documentFiles[1]!==""
                                ? documentFiles[1].startsWith('https://')
                                  ? documentFiles[1]
                                  : `${AppConfig.FILES_ENDPOINT}${documentFiles[1]}`
                                : dummyImage
                            }
                          />{' '}
                           <span style={{width: '19px'}}>
                            <img src={modalDeleteBtn} alt={modalDeleteBtn}
                            onClick={() => removeFile('cardBackImage')} />
                          </span>
                          <span>{cardBackImageName}</span>
                        </p>
                      </div>
                    ) : (
                      <div className="position-relative uploadCloud w-100 cardimage">
                        <input
                          type="file"
                          name="cardBackImage"
                          accept="image/*"
                          onChange={cardBackImageChangedHandler}
                          id="cardback"
                          
                          // multiple
                        />
                        <p className="mb-0" for="cardback">
                          <img
                            className="mr-0"
                            src={uploadCloudImg}
                            alt={uploadCloudImg}
                          />{' '}
                          <span>{cardBackImageName}</span>
                        </p>
                      </div>
                    )}
                  </div>
                </Form.Group>
              </div>
              <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
                <Form.Group>
                  <div className="d-flex align-items-center justify-content-between mb-2">
                    <label className="mb-0">Issue Date</label>
                  </div>
                  <DatePicker
                    wrapperClassName="form-control date-icon"
                    //maxDate={new Date()}
                    minDate={new Date(moment().subtract(100, 'years'))}
                    className="form-control"
                    placeholderText="dd/mm/yyyy"
                    autoComplete={false}
                    name="issueDate"
                    dateFormat="dd/MM/yyyy"
                    onChange={(date) => {
                      if (date)
                        handleComplianceChange('issueDate')(
                          moment(date).format('YYYY-MM-DD')
                        );
                      else handleComplianceChange('issueDate')('');
                    }}
                    selected={
                      complianceValues && complianceValues.issueDate
                        ? new Date(
                            moment(complianceValues.issueDate).format(
                              'YYYY-MM-DD'
                            )
                          )
                        : ''
                    }
                    peekNextMonth
                    showMonthDropdown
                    showYearDropdown
                    dropdownMode="select"
                  />
                  <span class="errorMsg">
                    {complianceErrors.issueDate && complianceTouched.issueDate
                      ? complianceErrors.issueDate
                      : null}
                  </span>
                </Form.Group>
              </div>
              <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
                <Form.Group>
                  <div className="d-flex align-items-center justify-content-between mb-2">
                    <label className="mb-0">Expiry Date</label>
                    <div>
                      <Form.Group
                        controlId="formBasicCheckbox"
                        className="mb-0"
                      >
                        <Form.Check
                          type="checkbox"
                          label="N/A"
                          name="naExpiry"
                          onChange={handleComplianceChange}
                          value={true}
                          checked={
                            complianceValues ? complianceValues.naExpiry : false
                          }
                        />
                      </Form.Group>
                    </div>
                  </div>
                  {/* <div className="dateIcon">
                    <input
                      type="text"
                      className="form-control date"
                      placeholder="mm/dd/yyyy"
                      name="expiryDate"
                      value={
                        complianceValues ? complianceValues.expiryDate : ''
                      }
                      onChange={handleComplianceChange}
                      onFocus={(e) => {
                        e.currentTarget.type = 'date';
                      }}
                    />
                  </div> */}
                  <DatePicker
                    wrapperClassName="form-control date-icon"
                    //maxDate={new Date()}
                    minDate={new Date(moment().subtract(100, 'years'))}
                    className="form-control"
                    placeholderText="dd/mm/yyyy"
                    autoComplete={false}
                    name="expiryDate"
                    dateFormat="dd/MM/yyyy"
                    onChange={(date) => {
                      if (date)
                        handleComplianceChange('expiryDate')(
                          moment(date).format('YYYY-MM-DD')
                        );
                      else handleComplianceChange('expiryDate')('');
                    }}
                    selected={
                      complianceValues && complianceValues.expiryDate
                        ? new Date(
                            moment(complianceValues.expiryDate).format(
                              'YYYY-MM-DD'
                            )
                          )
                        : ''
                    }
                    peekNextMonth
                    showMonthDropdown
                    showYearDropdown
                    dropdownMode="select"
                  />
                  {/* <label></label> */}
                  <span class="errorMsg">
                    {complianceErrors.expiryDate && complianceTouched.expiryDate
                      ? complianceErrors.expiryDate
                      : null}
                  </span>
                </Form.Group>
              </div>
              <div className="col-xs-12 col-sm-12 col-md-6 col-lg-4 front_back_card">
                <Form.Group className="w-100 one_uploads">
                  <div className="d-flex align-items-center justify-content-between mb-2">
                    <label className="mb-0">Statement of Attainment</label>
                    <div>
                      <Form.Group
                        controlId="formBasicCheckbox2"
                        className="mb-0"
                      >
                        <Form.Check
                          type="checkbox"
                          label="N/A"
                          name="naStatement"
                          onChange={handleComplianceChange}
                          value={true}
                          checked={
                            complianceValues
                              ? complianceValues.naStatement
                              : false
                          }
                        />
                      </Form.Group>
                    </div>
                  </div>
                  {complianceValues.statementImage ? (
                    <div className="position-relative uploadCloud w-100 mr-2">
                      {/* <input
                      type="file"
                      name="cardImages"
                      accept="image/*"
                      multiple
                    /> */}
                      <p className="mb-0">
                        <img
                          className="mr-0"
                          src={modalDeleteBtn}
                          alt={modalDeleteBtn}
                          onClick={() => removeFile('statementImage')}
                        />{' '}
                        <span style={{width: '50px', cursor: 'pointer'}} onClick={() =>
                          viewDocument(
                            'Statement of Attainment',
                            complianceValues.statementImage
                          )
                        }>View</span>
                        <span>Statement of attainment.PDF</span>
                      </p>
                    </div>
                  ) : (
                    <div className="position-relative uploadCloud cardimage">
                      <input
                        type="file"
                        name="statementImage"
                        accept="application/pdf"
                        onChange={statementChangedHandler}
                        // multiple
                      />
                      <p className="mb-0">
                        <img
                          className="mr-2"
                          src={uploadCloudImg}
                          alt={uploadCloudImg}
                        />{' '}
                        <span>{statementName}</span>
                      </p>
                    </div>
                  )}
                </Form.Group>
              </div>

              <div className="col-xs-12 col-sm-12 col-md-6 col-lg-4">
                <button
                  type="submit"
                  className="btn btn-pink mt-2 form_btn"
                  disabled={isCardBtnDisable}
                  style={{ width: 192 }}
                >
                  {isCardBtnDisable ? (
                    <Spinner
                      as="span"
                      animation="border"
                      size="sm"
                      role="status"
                      aria-hidden="true"
                    />
                  ) : (
                    ''
                  )}{' '}
                  <img className="mr-2" src={addCardImg} alt={addCardImg} />{' '}
                  {isEdit === -1 ? 'Add Card' : 'Update Card'}
                </button>
              </div>
            </Row>
          </Form>
        </Modal.Body>
      </Modal>
      <Modal
        show={documentShow}
        onHide={handleDocumentClose}
        className="addCardPopup pdfPopup"
      >
        <Modal.Header>
          <Modal.Title>{documentTitle}</Modal.Title>
          <span className="modelClose" onClick={handleDocumentClose}>
            <img src={popupClose} alt={popupClose} />
          </span>
        </Modal.Header>
        <Modal.Body>
        { documentTitle === 'Statement of Attainment' ? 
          (
            <Row>
              <div>
              
              <Document
                file={
                  documentFiles
                    ? documentFiles.startsWith('https://')
                      ? documentFiles
                      : `${AppConfig.FILES_ENDPOINT}${documentFiles}`
                    : ''
                }
                onLoadSuccess={onDocumentLoadSuccess}
              >
                <Page pageNumber={pageNumber} />
              </Document>
              <p>Page {pageNumber} of {numPages}</p>
              </div>
            </Row>
          ) :
          ( <Row className="px-3">
            <div className="col-md-6">
              FRONT
              <img
                src={
                  documentFiles && documentFiles[0]
                    ? documentFiles[0].startsWith('https://')
                      ? documentFiles[0]
                      : `${AppConfig.FILES_ENDPOINT}${documentFiles[0]}`
                    : dummyImage
                }
                onError={(e) => {
                  e.target.onerror = null;
                  e.target.src = dummyImage;
                }}
                alt="Front"
                width="100%"
              />
            </div>
            <div className="col-md-6">
              BACK
              <img
                src={
                  documentFiles && documentFiles[1]
                    ? documentFiles[1].startsWith('https://')
                      ? documentFiles[1]
                      : `${AppConfig.FILES_ENDPOINT}${documentFiles[1]}`
                    : dummyImage
                }
                onError={(e) => {
                  e.target.onerror = null;
                  e.target.src = dummyImage;
                }}
                alt="Back"
                width="100%"
              />
            </div>
          </Row> )  }
        </Modal.Body>
      </Modal>
      <Modal
        className="profileEditModel two"
        show={deleteModalShow}
        onHide={handleDeleteClose}
        backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleDeleteClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <div className="secondSection px-5 pb-3 pt-5 text-center">
            <h3 className="font-weight-600">
              Are you sure you want to delete this user?
            </h3>
          </div>
          <div className="px-3 pb-5 d-flex align-items-center justify-content-center flex-wrap">
            <Button
              variant="light"
              className="mr-3 px-4 py-2 popupBtn"
              onClick={deleteUser}
              disabled={isDeleteBtnDisable}
            >
              {isDeleteBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Yes, Delete
            </Button>
            <Button
              variant=""
              className="btn-pink px-4 py-2 popupBtn"
              onClick={handleDeleteClose}
            >
              No, Go Back
            </Button>
          </div>
        </Modal.Body>
      </Modal>
      <Modal
        className="profileEditModel"
        show={successModalShow}
        onHide={handleSuccessClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Body className="p-0">
          <div className="firstSection d-flex align-item-end justify-content-center px-3 pt-4">
            <img src={handCard} alt={handCard} className="img-responsive" />
          </div>
          <div className="secondSection px-4 py-3 text-center">
            <h2 className="font-weight-600">Your card was added!</h2>
            <p className="px-4">
              You can click on the button below to continue to add staff cards
            </p>
          </div>
          <div className="px-3 pb-5 d-flex align-items-center justify-content-center flex-wrap">
            <Button
              variant=""
              className="btn-pink mr-3 px-4 py-2 popupBtn"
              onClick={() => {
                setShow(true);
                setSuccessModalShow(false);
              }}
            >
              Add Another
            </Button>
            <Button
              variant="light"
              className="px-4 py-2 popupBtn font-weight-600"
              onClick={handleSuccessClose}
            >
              I’m Done
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export { EditUser };
