import React, { useLayoutEffect,useEffect, useState, useRef } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Sidebar from '../components/Sidebar';
import Header from '../components/Header';
import logoGray from '../../assets/img/logo-w-fade.svg';

import Home from '../pages/Home';
import {
  Users,
  UserOverview,
  AddUser,
  JobsHistory,
  EditUser,
  Inventory,
  Tools,
  AddTool,
  EditTool,
  Calender,
} from '../pages/Users';

import {
  TaskList,
  UnitOfMeasure,
  AddTask,
  EditTask,
  RuleList,
  AddRule,
  TechnologyList,
  AddTechnology,
  EditTechnology,
  SubcategoryList,
  AddSubcategory,
  EditSubcategory,
  RuleSchedule,
  SetRule,
} from '../pages/Rate';

import { MaterialsList, AddMaterial, EditMaterial } from '../pages/Warehouse';
import AdminProfile from '../pages/AdminProfile';
import Settings from '../pages/Settings';
import { Route } from 'react-router-dom';
import OpenJobs from '../components/User/OpenJob';
import { JobProgress,ReportList } from '../pages/Reports';
import { Notification } from '../pages/Notification';

import { Chat } from '../pages/Chat';

const AuthenticatedLayout = () => {
  const [collapseClass, setCollapseClass] = useState('');
  const [toggleClass, setToggleClass] = useState('');
  const [mobileScreen, setMobileScreen] = useState('');
  const [userName, setUserName] = useState('');
  const [userId, setUserId] = useState('');
  const [scheduleName, setScheduleName] = useState('');
  const collapseSideBar = () => {
    if (collapseClass === 'collapse-side-bar') setCollapseClass('');
    else setCollapseClass('collapse-side-bar');
  };
  const myRef = useRef(null)
  const executeScroll = () => scrollToRef(myRef)
  //const messagesStartRef = useRef(null);
  const scrollToRef = (ref) => window.scrollTo(0, ref.current.offsetTop); 
  useEffect(() => {
    executeScroll();
  }, []);
  useLayoutEffect(() => {
    const updateSize = () => {
      let innerWidth = 1024;
      if (window.innerWidth <= innerWidth) {
        setMobileScreen('collapse-side-bar-0');
      } else {
        setMobileScreen('');
      }
      // console.log('here', window.innerWidth);
    };
    updateSize();
    window.addEventListener('resize', updateSize);
    
  }, []);

  const handleHamburger = () => {
    if (toggleClass === 'mobile_sidebar') setToggleClass('');
    else setToggleClass('mobile_sidebar');
  };

  return (
    <div id="wrapper" ref={myRef}>
      <Sidebar
        collapseSideBar={collapseSideBar}
        collapseClass={collapseClass}
        mobileScreen={mobileScreen}
        toggleClass={toggleClass}
        handleHamburger={handleHamburger}
      />

      <div
        id="content-wrapper"
        className={`d-flex flex-column ${
          collapseClass === 'collapse-side-bar' ? 'contentWidth' : ''
        } ${mobileScreen === 'collapse-side-bar-0' ? 'contentWidth-0' : ''}`}
      >
        <div id="content" >
          <Route path="/" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Overview"
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="User Profile"
            />
            <Home />
          </Route>
          <Route path="/home" exact={true}>
            <Header handleHamburger={handleHamburger} headerTitle="Overview" />
            <Home />
          </Route>
          <Route path="/users" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Users"
              logoPath={logoGray}
              pathOne="Users"
              pathTwo=""
            />
            <Users />
          </Route>
          <Route path="/users/openjobs-detail" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="JW001"
              logoPath={logoGray}
              pathOne="Jobs History"
              pathTwo=""
            />
            <OpenJobs />
          </Route>
          <Route path="/users/overview/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="User Profile"
              linkOne="/users"
            />
            <UserOverview setUserName={setUserName} setUserId={setUserId} />
          </Route>
          <Route path="/users/add" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Add New User"
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="Add New User"
              linkOne="/users"
            />
            <AddUser />
          </Route>
          <Route path="/users/jobs-history/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="User Profile"
              linkOne="/users"
              linkTwo={`/users/overview/${userId}`}
            />
            <JobsHistory setUserName={setUserName} setUserId={setUserId} />
          </Route>
          <Route path="/users/inventory/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="User Profile"
              linkOne="/users"
              linkTwo={`/users/overview/${userId}`}
            />
            <Inventory />
          </Route>
          <Route path="/users/tools/:id" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="User Profile"
              linkOne="/users"
              linkTwo={`/users/overview/${userId}`}
            />
            <Tools />
          </Route>
          <Route path="/users/edit/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="User Profile"
              linkOne="/users"
              linkTwo={`/users/overview/${userId}`}
            />
            <EditUser />
          </Route>
          <Route path="/users/calender/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="User Calender"
              linkOne="/users"
              linkTwo={`/users/calender/${userId}`}
            />
            <Calender />
          </Route>
          <Route path="/warehouse">
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Warehouse"
              logoPath={logoGray}
              pathOne="Warehouse"
            />
            <MaterialsList />
          </Route>
          <Route path="/material/add">
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Add Material"
              logoPath={logoGray}
              pathOne="Warehouse"
              pathTwo="Add Material"
              linkOne="/warehouse"
            />
            <AddMaterial />
          </Route>
          <Route path="/material/edit/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Edit Material"
              logoPath={logoGray}
              pathOne="Warehouse"
              pathTwo="Edit Material"
            />
            <EditMaterial />
          </Route>
          <Route path="/users/tools/add/:id" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Add Tool"
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="Tools"
              pathThree="Add Tool"
              linkOne="/users"
              linkTwo={`/users/tools/${userId}`}
            />
            <AddTool />
          </Route>
          <Route path="/users/tools/edit/:id" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Edit Tool"
              logoPath={logoGray}
              pathOne="Users"
              pathTwo="Tools"
              pathThree="Edit Tool"
            />
            <EditTool />
          </Route>
          <Route path="/rates" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Schedule of Rates"
              logoPath={logoGray}
              pathOne="Tasks"
              pathTwo="Rates"
              linkOne="/users"
              linkTwo="/users"
            />
            <TaskList />
          </Route>
          <Route path="/rates/uom" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Unit Of Measure"
              logoPath={logoGray}
              pathOne="Rates"
              pathTwo="UoM"
              linkOne="/rates"
            />
            <UnitOfMeasure />
          </Route>
          <Route path="/rates/add" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Add New Task"
              logoPath={logoGray}
              pathOne="Rates"
              linkOne="/rates"
            />
            <AddTask />
          </Route>
          <Route path="/rates/edit/:id" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Edit Rate"
              logoPath={logoGray}
              pathOne="Rates"
              pathTwo="Edit Rate"
            />
            <EditTask />
          </Route>
          <Route path="/rates/rules" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Business Rules"
              logoPath={logoGray}
              pathOne="Rates"
              // pathTwo="Edit Rate"
              linkOne="/rates"
            />
            <RuleList />
          </Route>
          <Route path="/rates/rules/add" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Add Business Rules"
              logoPath={logoGray}
              pathOne="Business Rules"
              pathTwo="Add Business Rules"
            />
            <AddRule />
          </Route>
          <Route path="/rates/technology" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Technology Application"
              logoPath={logoGray}
              pathOne="Rates"
              pathTwo="Technology Application"
            />
            <TechnologyList />
          </Route>
          <Route path="/rates/technology/add" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Add Technology"
              logoPath={logoGray}
              pathOne="Technology Application"
              pathTwo="Add Technology"
            />
            <AddTechnology />
          </Route>
          <Route path="/rates/technology/edit/:id" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Edit Technology"
              logoPath={logoGray}
              pathOne="Technology Application"
              pathTwo="Edit Technology"
            />
            <EditTechnology />
          </Route>
          <Route path="/rates/subcategory" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Subcategory"
              logoPath={logoGray}
              pathOne="Rates"
              pathTwo="Subcategory"
              linkOne="/rates"
            />
            <SubcategoryList />
          </Route>
          <Route path="/rates/subcategory/add" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Add Subcategory"
              logoPath={logoGray}
              pathOne="Subcategory"
              pathTwo="Add Subcategory"
              linkOne="/rates/subcategory"
            />
            <AddSubcategory />
          </Route>
          <Route path="/rates/subcategory/edit/:id" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Edit Subcategory"
              logoPath={logoGray}
              pathOne="Subcategory"
              pathTwo="Edit Subcategory"
              linkOne="/rates/subcategory"
            />
            <EditSubcategory />
          </Route>
          <Route path="/rates/rules/schedule/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={`Business Rule ${scheduleName}`}
              logoPath={logoGray}
              pathOne="Business Rules"
              pathTwo={scheduleName}
              linkOne="/rates/rules"
            />
            <RuleSchedule setScheduleName={setScheduleName} />
          </Route>
          <Route path="/rates/rules/setrule/:id" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Set Business Rules"
              logoPath={logoGray}
              pathOne="Rates"
              pathTwo="Set Business Rules"
              linkOne="/rates"
              linkTwo="/rates/rules"
            />
            <SetRule />
          </Route>
          <Route path="/report/:id">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Jobs History"
              pathTwo={`JOB ID ${userName}`}
              linkOne="/rates"
            />
            <JobProgress setUserName={setUserName} />
          </Route>
          <Route path="/reports">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Jobs History"
              pathTwo={`JOB ID ${userName}`}
              linkOne="/rates"
            /> 
            <ReportList />
          </Route>
          <Route path="/notification">
            <Header
              handleHamburger={handleHamburger}
              headerTitle={userName}
              logoPath={logoGray}
              pathOne="Notifications List"
              pathTwo=""
              linkOne=""
            /> 
            <Notification />
          </Route>
          <Route path="/chat">
            <Header
              handleHamburger={handleHamburger}
              headerTitle=""
              logoPath={logoGray}
              pathOne="Chat"
            />
            <Chat />
          </Route>
          <Route path="/profile" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Admin Profile"
              logoPath={logoGray}
            />
            <AdminProfile />
          </Route>
          <Route path="/settings" exact={true}>
            <Header
              handleHamburger={handleHamburger}
              headerTitle="Settings"
              logoPath={logoGray}
            />
            <Settings />
          </Route>
        </div>
      </div>
    </div>
  );
};

export default AuthenticatedLayout;
