import React, { useEffect, useState } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import {
  Card,
  Col,
  Container,
  Row,
  Button,
  Modal,
  Table,
  Carousel,
  Accordion
} from "react-bootstrap";

import eye from "../../../assets/img/eye.svg";
import NotFound from "../../../assets/img/on_data_found.svg";
import exportIcon from "../../../assets/img/exportJob.svg";
import CheckMark from "../../../assets/img/CheckMark.svg";
import timeTaken from "../../../assets/img/timeTaken.svg";
import Performance from "../../../assets/img/Performance.svg";
import jobNotes from "../../../assets/img/jobNotes.svg";
import popupClose from "./../../../assets/img/popupClose.svg";
import previewEye from "../../../assets/img/previewEye.svg";
import product from "../../../assets/img/item_mr.svg";
import userAvtar from "../../../assets/img/user_avtar.svg";
import next from "../../../assets/img/next.svg";
import cunstruction from "../../../assets/img/cunstruction.jpg";


import { FetchFromServer } from "../../../utils/helpers";
import { ApiRoutes, AppConfig } from "../../../utils/config";
import moment from "moment";
import NoDataCard from "../../components/NoDataCard";
import JobHistotyLoader from "./JobHistoryLoader";
import Skeleton from "react-loading-skeleton";


const JobProgress = ({ setUserName }) => {
  const { id } = useParams();
  const history = useHistory();

  const [show, setShow] = useState(false);
  const [show2, setShow2] = useState(false);
  const [show3, setShow3] = useState(false);
  const [show4, setShow4] = useState(false);
  const [showDuring, setDuringShow] = useState(false);
  const [showAfter, setAfterShow] = useState(false);
  const [isMaterialLoader, setIsMaterialLoader] = useState(true);

  const [beforeDate, setBeforeDate] = useState('');
  const [duringImage, setDuringImage] = useState('');
  const [afterImage, setAfterImage] = useState('');
  const [additionalImages, setAdditionalImages] = useState([]);
  const [dynamicImage, setdynamicImage] = useState([]);
  const [usedMaterials, setusedMaterials] = useState([]);
  const [itemId, setItemId] = useState();
  const [taskDate, setTaskDate] = useState();



  const handleClose = () => setShow(false);
  const handleShow = async (jobId, taskId, itemId) => {
    setItemId(itemId);
    setIsMaterialLoader(true);
    const response = await FetchFromServer(
      ApiRoutes.USED_MATERIALS.service,
      ApiRoutes.USED_MATERIALS.url.replace(":id", id),
      ApiRoutes.USED_MATERIALS.method,
      ApiRoutes.USED_MATERIALS.authenticate,
      { "jobId": jobId, "taskId": taskId },
      undefined
    );
    setIsLoading(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setusedMaterials(response.data.data);
    }
    setIsMaterialLoader(false);
    setShow(true);
  }
  const handleClose2 = () => setShow2(false);
  const handleClose4 = () => setShow4(false);
  const handleShow4 = (before, itemId, date) => {
    setItemId(itemId);
    setTaskDate(date);
    setBeforeDate(before);
    setShow4(true);
  }
  const handleDuringClose = () => setDuringShow(false);
  const handleDuringShow = (during, itemId, date) => {
    setItemId(itemId);
    setTaskDate(date);
    setDuringImage(during);
    setDuringShow(true);
  }
  const handleAfterClose = () => setAfterShow(false);
  const handleAfterShow = (after, itemId, date) => {
    setItemId(itemId);
    setTaskDate(date);
    setAfterImage(after);
    setAfterShow(true);
  }
  const handleShow2 = () => setShow2(true);
  const handleClose3 = () => setShow3(false);
  const handleShow3 = (additional, itemId, date) => {
    console.log(itemId);
    setItemId(itemId);
    setTaskDate(date);
    let gy = additional;
    setdynamicImage(gy);
    setShow3(true);
  }
  const [isLoading, setIsLoading] = useState(true);
  const [jobData, setJobData] = useState([]);

  const getJobList = async () => {
    setIsLoading(true);
    const response = await FetchFromServer(
      ApiRoutes.JOB_INFO.service,
      ApiRoutes.JOB_INFO.url.replace(":id", id),
      ApiRoutes.JOB_INFO.method,
      ApiRoutes.JOB_INFO.authenticate,
      undefined
    );
    setIsLoading(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setUserName(response.data.data.jobId);
      setJobData(response.data.data);
    }
  };

  useEffect(() => {
    window.scrollTo(0, 0)
    getJobList();

  }, []);

  return (
    <>
      <Container fluid className="jobReportPage">
        <Row className="mt-4">

          <Col sm={12} md={12} lg={8} xl={8} className="order-2 order-lg-1">
            {isLoading ? (<JobHistotyLoader />) :
              jobData.task && jobData.task.length ? (jobData.task.map((item, key) => {
                return (
                  <Card className="job_history_Card">
                    <Card.Body>
                      <div class="box-one d-flex align-items-center justify-content-between pr-3">
                        <div>
                          <p className="mb-0">{item.taskId.itemId}</p>
                        </div>
                        <div className="materialUsed">
                          <span onClick={() => handleShow(jobData._id, item.taskId._id, item.taskId.itemId)}>Materials used</span>
                        </div>
                      </div>
                      <div class="box-two d-flex align-items-center justify-content-between py-3 px-3">
                        <div className="d-flex align-items-center width-80Percent">
                          <p className="mb-0">{item.taskId.description}</p>
                        </div>
                        <div>
                          <span className="countNo">1</span>
                        </div>
                        <div className="d-flex align-items-center">
                          <h6 className="mb-0 Cardprice">
                            ${item.taskId.rates}
                          </h6>
                        </div>
                      </div>
                      <hr className="m-0" />

                      <div className="box-three py-3 px-3 flex-wrap">
                        <div className="d-flex flex-column width155px position-relative">
                          <p className="mb-0">BEFORE</p>
                          <div className="innerBox d-flex align-items-center justify-content-between">
                            <span className="mb-0">
                              {item.imageProof.beforeOriginal ? item.imageProof.beforeOriginal : "Before.jpg"}
                            </span>
                            <img src={eye} alt={eye} onClick={() => handleShow4(item.imageProof.before, item.taskId.itemId, item.modifiedAt)} />
                          </div>
                        </div>
                        <div className="d-flex flex-column width155px position-relative">
                          <p className="mb-0">DURING</p>
                          <div className="innerBox d-flex align-items-center justify-content-between">
                            <span className="mb-0">
                              {item.imageProof.duringOriginal ? item.imageProof.duringOriginal : "During.jpg"}
                            </span>
                            <img
                              src={eye}
                              alt={eye}
                              onClick={() => handleDuringShow(item.imageProof.during, item.taskId.itemId, item.modifiedAt)}
                            />
                          </div>
                        </div>
                        <div className="d-flex flex-column width155px position-relative">
                          <p className="mb-0">AFTER</p>
                          <div className="innerBox d-flex align-items-center justify-content-between">
                            <span className="mb-0">

                              {item.imageProof.afterOriginal ? item.imageProof.afterOriginal : "After.jpg"}
                            </span>
                            <img
                              src={eye}
                              alt={eye}
                              onClick={() => handleAfterShow(item.imageProof.after, item.taskId.itemId, item.modifiedAt)}
                            />
                          </div>
                        </div>
                        <div className="d-flex flex-column width155px position-relative">
                          <p className="mb-0">OTHER</p>
                          <div className="innerBox d-flex align-items-center justify-content-between">
                            <span className="mb-0">
                              {item.imageProof.additionalOriginal ? item.imageProof.additionalOriginal : "Additional.jpg"}
                            </span>
                            <img src={eye} alt={eye} onClick={() => handleShow3(item.imageProof.additional, item.taskId.itemId, item.modifiedAt)} />
                          </div>
                        </div>
                      </div>
                    </Card.Body>
                  </Card>
                );
              })) : (
                <Col xs={12} sm={12} md={12} lg={6} xl={12} key="2">
                  <NoDataCard />
                </Col>
              )
            }

            {/* <Card>
              <Card.Body className="py-3 px-4">
                <div className="d-flex align-items-center justify-content-center py-5">
                  <div className="position-relative noDataFound">
                    <img src={NotFound} alt={NotFound} />
                    <h6 className="mb-0">No Data Available</h6>
                    <p>The data you're looking for is not available</p>
                  </div>
                </div>
              </Card.Body>
            </Card> */}
          </Col>

          <Col sm={12} md={12} lg={4} xl={4} className="order-1 order-lg-2">
            {jobData.status === "Completed" ? (
              <Card className="jobStatusCard">
                <Card.Body className="text-center">
                  <p className="status completed m-0">
                    <img src={CheckMark} /> Job completed
                  </p>
                </Card.Body>
              </Card>
            ) : (
              ""
            )}
            <Card className="mb-0 userInfo">
              <Card.Body className="py-3 px-4">
                <div className="d-flex align-items-center">
                  <div className="img_box">
                    <img
                      width="64"
                      className="rounded-circle"
                      src={
                        jobData.user && jobData.user.profilePicture
                          ? jobData.user.profilePicture.startsWith("https://")
                            ? jobData.user.profilePicture
                            : `${AppConfig.FILES_ENDPOINT}${jobData.user.profilePicture}`
                          : userAvtar
                      }
                      alt="alt"
                    />
                  </div>
                  <div className="d-flex flex-column ml-2 nameBox">
                    <h6 class="mb-0">
                      {jobData.user && jobData.user.firstName ? jobData.user.firstName : ""}
                      {jobData.user && jobData.user.lastName ? jobData.user.lastName : ''}
                    </h6>
                    <Link
                      to={`/users/overview/${jobData.user && jobData.user._id}`}
                      className=""
                    >
                      view profile
                    </Link>
                  </div>
                </div>
              </Card.Body>
            </Card>
            <Card className="exportCard">
              <Card.Body className="text-center">
                <Link to="#" className="exportJob">
                  <img src={exportIcon} /> Export Job Report
                </Link>
              </Card.Body>
            </Card>

            <Card className="totalEarn">
              <Card.Body className="py-3 px-4">
                <div className="d-flex align-items-center justify-content-center flex-column">
                  <p>TOTAL EARNED</p>
                  <h4>
                    <span>$</span>
                    {jobData.totalAmount}
                  </h4>
                </div>
              </Card.Body>
            </Card>

            <Card className="job_compliances marginB-8">
              <Card.Body className="text-center">
                <p className="mb-0 text-left heading">JOB COMPLIANCES</p>
                <div class="d-flex align-items-center justify-content-between">
                  <p className="mb-0">PRESTART</p> <span>|</span>
                  <p className="mb-0">JSEA</p> <span>|</span>
                  <p className="mb-0 mr-4">SWMS</p>
                </div>
              </Card.Body>
            </Card>

            <Card className="timeTaken marginB-8">
              <Card.Body className="text-center d-flex align-items-center">
                <div className="imgBox">
                  <img src={timeTaken} />
                </div>
                <div className="d-flex justify-content-start align-items-start flex-column paddingL-24px">
                  <p className="mb-0">Time Taken</p>
                  <p className="mb-0">{
                    jobData.startTime ?
                      (Math.round(((new Date()).getTime() - (new Date(jobData.startTime)).getTime()) / 60000)) : '0'
                  }/{jobData.totalTime ? jobData.totalTime : "0"}</p>
                </div>
              </Card.Body>
            </Card>

            <Card className="timeTaken Performance marginB-8">
              <Card.Body className="text-center d-flex align-items-center">
                <div className="imgBox">
                  <img src={Performance} />
                </div>
                <div className="d-flex justify-content-start align-items-start flex-column paddingL-24px">
                  <p className="mb-0">Performance</p>
                  <p className="mb-0">20%</p>
                </div>
              </Card.Body>
            </Card>

            <Card className="timeTaken jobNotes">
              <Card.Body className="text-center d-flex align-items-center">
                <div className="imgBox">
                  <img src={jobNotes} />
                </div>
                <div
                  className="d-flex justify-content-start align-items-start flex-column paddingL-24px"
                  onClick={handleShow2}
                >
                  <p className="mb-0">JOB NOTES</p>
                  <Link to="#" className="mb-0">
                    {jobData.notes}
                  </Link>
                </div>
              </Card.Body>
            </Card>

            <Card className="information">
              <Card.Body className="text-center d-flex align-items-center">
                <div className="innerBox">
                  <p className="mb-0">ADDRESS</p>
                  <p className="mb-0">{jobData.jobAddress}</p>
                </div>
                <div className="innerBox">
                  <p className="mb-0">PARENT ID</p>
                  <p className="mb-0">{jobData.parentId}</p>
                </div>
                <div className="innerBox">
                  <p className="mb-0">TASK ID</p>
                  <p className="mb-0">{jobData.taskId}</p>
                </div>
                <div className="innerBox">
                  <p className="mb-0">JOB TYPE</p>
                  <p className="mb-0">{jobData.technologyName}</p>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>

      <Modal className="materialsUserPopup" show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <h4>{itemId ? itemId : ''} - Materials Used</h4>
          <span className="modelClose" closeButton onClick={handleClose}>
            <img src={popupClose} alt={popupClose} />
          </span>
        </Modal.Header>
        <Modal.Body>
          <table className="table">
            <tr>
              <th>IMAGE</th>
              <th>Description</th>
              <th>QTY USED</th>
            </tr>
            {isMaterialLoader ?
              (<tr>
                <td class="productItem position-relative">
                  <div className="innerBox">
                    <Skeleton style={{ width: 60, height: 60 }} />
                  </div>
                </td>
                <td className="centerTd d-flex flex-column">
                  <Skeleton style={{ width: 280, height: 10 }} />
                  <Skeleton style={{ width: 280, height: 10 }} />
                  <Skeleton style={{ width: 280, height: 10 }} />
                </td>
                <td className="pt-30"><Skeleton style={{ width: 10, height: 20 }} /></td>
              </tr>)
              :
              usedMaterials && usedMaterials.length > 0 ?
                usedMaterials.map((material, keys) => {
                  return (

                    <tr key={keys}>
                      <td class="productItem position-relative">
                        <div className="innerBox">
                          <img className="material-iamge" src={`${AppConfig.FILES_ENDPOINT}${material.materialImage}`} alt={material.materialImage} />
                          <div className="eyeBox">
                            <img src={previewEye} alt={previewEye} />
                          </div>
                        </div>
                      </td>
                      <td className="centerTd">{material.description}</td>
                      <td className="pt-30">{material.materialUsed}</td>
                    </tr>
                  )
                }) : (
                  <Col xs={12} sm={12} md={12} lg={12} xl={12} key="2">
                    <NoDataCard />
                  </Col>
                )
            }

          </table>
        </Modal.Body>
      </Modal>

      <Modal className="jobNotePopup stepsShots" show={show2} onHide={handleClose2}>
        <Modal.Header className="position-relative">
          <h4>Job Notes</h4>
          <span className="modelClose" closeButton onClick={handleClose2}>
            <img src={popupClose} alt={popupClose} />
          </span>
        </Modal.Header>
        <Modal.Body>
          <p className="mb-0">{jobData.notes}</p>
        </Modal.Body>
      </Modal>

      <Modal className="jobNotePopup stepsShots" show={show4} onHide={handleClose4}>
        <Modal.Header className="position-relative">
          <h4> {itemId ? itemId : ''} Photo Taken</h4>
          <span className="modelClose" closeButton onClick={handleClose4}>
            <img src={popupClose} alt={popupClose} />
          </span>
        </Modal.Header>
        <Modal.Body>
          <div className="d-flex align-items-center justify-content-between status">
            <p className="mb-0">DURING</p>
            <p className="mb-0">{taskDate ? moment(taskDate).format("DD MMMM YYYY hh:mm A") : ''}</p>
          </div>
          <p className="mb-0">
            <img src={`${AppConfig.FILES_ENDPOINT}${beforeDate}`} alt={beforeDate} ></img>
          </p>
        </Modal.Body>
      </Modal>

      <Modal
        className="jobNotePopup stepsShots"
        show={showDuring}
        onHide={handleDuringClose}
      >
        <Modal.Header className="position-relative">
          <h4> {itemId ? itemId : ''} Photo Taken</h4>
          <span className="modelClose" closeButton onClick={handleDuringClose}>
            <img src={popupClose} alt={popupClose} />
          </span>
        </Modal.Header>
        <Modal.Body>
          <div className="d-flex align-items-center justify-content-between status">
            <p className="mb-0">DURING</p>
            <p className="mb-0">{taskDate ? moment(taskDate).format("DD MMMM YYYY hh:mm A") : ''}</p>
          </div>
          <p className="mb-0">
            <img src={`${AppConfig.FILES_ENDPOINT}${duringImage}`} alt={duringImage} ></img>
          </p>
        </Modal.Body>
      </Modal>

      <Modal
        className="jobNotePopup stepsShots"
        show={showAfter}
        onHide={handleAfterClose}
      >
        <Modal.Header className="position-relative">
          <h4> {itemId ? itemId : ''} Photo Taken</h4>
          <span className="modelClose" closeButton onClick={handleAfterClose}>
            <img src={popupClose} alt={popupClose} />
          </span>
        </Modal.Header>
        <Modal.Body>
          <div className="d-flex align-items-center justify-content-between status">
            <p className="mb-0">AFTER</p>
            <p className="mb-0">{taskDate ? moment(taskDate).format("DD MMMM YYYY hh:mm A") : ''}</p>
          </div>
          <p className="mb-0">
            <img src={`${AppConfig.FILES_ENDPOINT}${afterImage}`} alt={duringImage} ></img>
          </p>
        </Modal.Body>
      </Modal>
      {jobData.task != undefined &&
        <Modal className="jobNotePopup otherPopup" show={show3} onHide={handleClose3}>
          <Modal.Header className="position-relative">
            <h4> {itemId ? itemId : ''} Photos Taken</h4>
            <span classNa me="modelClose" closeButton onClick={handleClose3}>
              <img src={popupClose} alt={popupClose} />
            </span>
          </Modal.Header>
          <Modal.Body className="py-0">
            <div className="d-flex align-items-center justify-content-between status">
              <p className="mb-0">OTHER</p>
              <p className="mb-0">{taskDate ? moment(taskDate).format("DD MMMM YYYY hh:mm A") : ''}</p>
            </div>

            <Carousel className="reportCarousel">

              {dynamicImage.map((items, keys) => {
                return (
                  <Carousel.Item>
                    <img
                      className="d-block w-100"
                      src={`${AppConfig.FILES_ENDPOINT}${items}`} alt={duringImage}
                      alt="First slide"
                    />

                  </Carousel.Item>
                )
              })
              }

              {/* <Button varient="" className="nextButton"> 
                    <p className="mb-0">NEXT</p>
                    <img src={next} />
                  </Button> */}

            </Carousel>
          </Modal.Body>

          <Modal.Footer>
            <Button varient="" className="next">
              <p className="mb-0">NEXT</p>
              <img src={next} />
            </Button>
          </Modal.Footer>
        </Modal>
      }
    </>
  );
};

export { JobProgress };
