import React from 'react';
import { useFormik } from 'formik';
import { Form, Row, Col, Button, Spinner } from 'react-bootstrap';

import { AddUomSchema } from '../../../validators';

const EditUom = (props) => {
  const {
    isUpdateBtnDisable,
    initialValues,
    id,
    handleDelete,
    handleEditSubmit,
  } = props;
  const { handleSubmit, handleChange, values, errors } = useFormik({
    initialValues,
    validationSchema: AddUomSchema,
    onSubmit: async (values) => {
      handleEditSubmit(values, id);
    },
  });
  return (
    <Row className="tab-content">
      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group>
          <label className="mb-0">Name</label>
          <Form.Control
            type="text"
            name="uomName"
            placeholder="Enter Name"
            onChange={handleChange}
            value={values.uomName}
          />
          <span className="errorMsg">
            {' '}
            {errors.uomName ? errors.uomName : null}
          </span>
        </Form.Group>
      </Col>

      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group>
          <label className="mb-0">Increment Type</label>
          <Form.Control
            as="select"
            name="incrementType"
            onChange={handleChange}
            value={values.incrementType}
          >
            <option hidden value="">
              Select increment type
            </option>
            <option>Yes/No</option>
            <option>Units</option>
          </Form.Control>
          <span className="errorMsg">
            {' '}
            {errors.incrementType ? errors.incrementType : null}
          </span>
        </Form.Group>
      </Col>

      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group>
          <label className="mb-0">Set Maximum Value</label>
          <Form.Control
            type="text"
            name="maximumValue"
            placeholder="Set value associated"
            onChange={handleChange}
            value={values.maximumValue}
          />
          <span className="errorMsg">
            {' '}
            {errors.maximumValue ? errors.maximumValue : null}
          </span>
        </Form.Group>
      </Col>
      <Col md={12} className="text-left pageButtomBtn">
        <Button
          variant=""
          className="px-5 py-2 rounded uomUpdateBtn"   
          style={{ fontSize: '14px' }}
          onClick={handleSubmit}
          disabled={isUpdateBtnDisable === id ? true : false}
        >
          {isUpdateBtnDisable === id ? (
            <Spinner
              as="span"
              animation="border"
              size="sm"
              role="status"
              aria-hidden="true"
            />
          ) : (
            ''
          )}{' '}
          Save Updates
        </Button>
        <Button
          variant=""
          className="px-5 py-2 rounded ml-3"
          style={{ background: '#E5EAEE', fontSize: '14px' }}
          onClick={() => handleDelete(id)}
        >
          Delete UoM
        </Button>
      </Col>
    </Row>
  );
};

export { EditUom };
