import React, { useState, useEffect } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';

import {
  Nav,
  Tab,
  Form,
  Button,
  Spinner,
  Col,
  Container,
  Row,
  Card,
  Modal,
} from 'react-bootstrap';
import gobackImg from '../../../assets/img/goback.svg';
import popupClose from './../../../assets/img/popupClose.svg';

import { AddTechnologySchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { TechnologyInformation } from '../../components/Rate';

const EditTechnology = () => {
  const { id } = useParams();
  const history = useHistory();
  const [isBtnDisable, setDisable] = useState(false);
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const handleDeleteClose = () => setDeleteModalShow(false);
  const [isDeleteBtnDisable, setDeleteBtnDisable] = useState(false);
  const {
    handleSubmit,
    handleChange,
    values,
    errors,
    setFieldValue,
    touched,
  } = useFormik({
    initialValues: {},
    validationSchema: AddTechnologySchema,
    onSubmit: async (values) => {
      setDisable(true);
      const response = await FetchFromServer(
        ApiRoutes.EDIT_TECHNOLOGY.service,
        ApiRoutes.EDIT_TECHNOLOGY.url.replace(':id', id),
        ApiRoutes.EDIT_TECHNOLOGY.method,
        ApiRoutes.EDIT_TECHNOLOGY.authenticate,
        undefined,
        values
      );
      setDisable(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        history.push({
          pathname: '/rates/technology',
        });
      }
    },
  });

  const deleteTechnology = async () => {
    setDeleteBtnDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.DELETE_TECHNOLOGY.service,
      ApiRoutes.DELETE_TECHNOLOGY.url.replace(':id', id),
      ApiRoutes.DELETE_TECHNOLOGY.method,
      ApiRoutes.DELETE_TECHNOLOGY.authenticate,
      undefined
    );
    setDeleteBtnDisable(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      toast.success(response.messages[0]);
      history.push({
        pathname: '/rates/technology',
      });
    }
  };

  useEffect(() => {
    const getTechnologyInfo = async () => {
      const response = await FetchFromServer(
        ApiRoutes.TECHNOLOGY_INFO.service,
        ApiRoutes.TECHNOLOGY_INFO.url.replace(':id', id),
        ApiRoutes.TECHNOLOGY_INFO.method,
        ApiRoutes.TECHNOLOGY_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: '/rates/technology',
        });
      } else {
        let data = response.data.data;
        values.technologyName = data.technologyName;
        values.technologyDescription = data.technologyDescription;
        setFieldValue({ technologyName: '' });
      }
    };
    getTechnologyInfo();
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link
          to="/rates/technology"
          className="btn btn_goback p-0"
          role="button"
        >
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>

      <Container fluid className="addNewUserPage">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3 mt-4">
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form onSubmit={handleSubmit}>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="profile"
                    >
                      <Nav as="ul" className="nav-pills mb-3 position-relative">
                        <Nav.Item as="li">
                          <Nav.Link eventKey="profile">
                            Edit Technology Information
                          </Nav.Link>
                        </Nav.Item>
                      </Nav>
                      <Tab.Content>
                        <Tab.Pane eventKey="profile">
                          <TechnologyInformation
                            handleChange={handleChange}
                            errors={errors}
                            values={values}
                            touched={touched}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12} className="mb-5 text-right pageButtomBtn">
            <Button
              variant=""
              className="px-5 py-2 rounded addcard"
              style={{ background: '#E5EAEE', fontSize: '14px' }}
              onClick={() => setDeleteModalShow(true)}
            >
              Delete Task
            </Button>
            <Button
              variant=""
              className="px-5 py-2 rounded btn-pink ml-3"
              style={{ fontSize: '14px' }}
              onClick={handleSubmit}
              disabled={isBtnDisable}
            >
              {isBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Save Updates
            </Button>
          </Col>
        </Row>
      </Container>
      <Modal
        className="profileEditModel two"
        show={deleteModalShow}
        onHide={handleDeleteClose}
        backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleDeleteClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <div className="secondSection px-5 pb-3 pt-5 text-center">
            <h3 className="font-weight-bold">
              Are you sure you want to delete this technology?
            </h3>
          </div>
          <div className="px-3 pb-5 d-flex align-items-center justify-content-center flex-wrap">
            <Button
              variant="light"
              className="mr-3 px-4 py-2 popupBtn"
              onClick={deleteTechnology}
              disabled={isDeleteBtnDisable}
            >
              {isDeleteBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Yes, Delete
            </Button>
            <Button
              variant=""
              className="btn-pink px-4 py-2 popupBtn"
              onClick={() => {
                history.push({
                  pathname: '/rates/technology',
                });
              }}
            >
              No, Go Back
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export { EditTechnology };
