import React, { useState, useRef, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';

import {
  Nav,
  Tab,
  Form,
  Button,
  Spinner,
  Col,
  Container,
  Card,
  Row,
} from 'react-bootstrap';
import gobackImg from '../../../assets/img/goback.svg';

import { AddMaterialSchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { AddMaterial as AddMaterialComponent } from '../../components/Warehouse';

const AddMaterial = () => {
  const form = useRef(null);
  const history = useHistory();
  const [isBtnDisable, setDisable] = useState(false);
  const [uomData, setUomData] = useState(false);
  const { handleSubmit, handleChange, values, errors, touched, resetForm,setFieldValue } = useFormik({
    initialValues: {
      salesPartNumber: '',
      materialDescription: '',
      inStock: '',
      uom: '',
      lowMarginAdmin: '',
      lowMarginUser: '',
    },
    validationSchema: AddMaterialSchema,
    onSubmit: async (values) => {
      setDisable(true);
      const formData = new FormData(form.current);

      // return false;
      const response = await FetchFromServer(
        ApiRoutes.ADD_MATERIAL.service,
        ApiRoutes.ADD_MATERIAL.url,
        ApiRoutes.ADD_MATERIAL.method,
        ApiRoutes.ADD_MATERIAL.authenticate,
        undefined,
        formData
      );
      setDisable(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        toast.success(response.messages[0]);
        // history.push({
        //   pathname: '/warehouse',
        // });
        resetForm({
          salesPartNumber: '',
          materialDescription: '',
          inStock: '',
          uom: '',
          lowMarginAdmin: '',
          lowMarginUser: '',
        });
      }
    },
  });

  useEffect(() => {
    const getUomList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.UOM_LIST.service,
        ApiRoutes.UOM_LIST.url,
        ApiRoutes.UOM_LIST.method,
        ApiRoutes.UOM_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setUomData(response.data.data);
      }
    };
    getUomList();
  }, []);

  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link to="/warehouse" className="btn btn_goback p-0" role="button">
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>

      <Container fluid className="addNewUserPage">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3 mt-4">
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form ref={form} onSubmit={handleSubmit}>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="profile"
                    >
                      <Nav as="ul" className="nav-pills mb-3 position-relative">
                        <Nav.Item as="li">
                          <Nav.Link eventKey="profile">
                            New Material Information
                          </Nav.Link>
                        </Nav.Item>
                      </Nav>
                      <Tab.Content>
                        <Tab.Pane eventKey="profile">
                          <AddMaterialComponent
                            handleChange={handleChange}
                            setFieldValue={setFieldValue}
                            uomData={uomData}
                            errors={errors}
                            values={values}
                            touched={touched}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12} className="mb-5 text-right pageButtomBtn">
            <Button
              variant=""
              className="px-5 py-2 rounded btn-pink ml-3 form_btn"
              style={{ fontSize: '14px' }}
              onClick={handleSubmit}
              disabled={isBtnDisable}
            >
              {isBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Add Material
            </Button>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export { AddMaterial };
