import HelloContext from '../../context';
import React, { useState, useEffect,forwardRef } from 'react';
import { Link } from 'react-router-dom';
import {
  Col,
  Container,
  Row,
  Modal, 
  Spinner,
  Form,
  Tab,
  Nav
} from 'react-bootstrap';
import Bell from '../../assets/img/bell.svg';
import NavUser from '../../assets/img/user-2.svg';
import Calendar from '../../assets/img/calendar.svg';
import Userchat from '../../assets/img/userchat.png';
import Userchat2 from '../../assets/img/userchat2.png';
import Settings from '../../assets/img/setting_cog.svg';
import User from '../../assets/img/user_black.svg';
import Logout from '../../assets/img/logout.svg';
import hamburger from '../../assets/img/hamburger.svg';
import Phone_dt from '../../assets/img/phone_dt.svg';   
import Msg_dt from '../../assets/img/msg_dt.svg';
import { logout } from '../../utils/helpers';
import { Dropdown } from 'react-bootstrap';
import moment from 'moment';
import DatePicker from 'react-datepicker';
import popupClose from './../../assets/img/popupClose.svg';
import { FetchFromServer } from '../../utils/helpers';
import { ApiRoutes, AppConfig } from '../../utils/config';
import Pagination from 'react-js-pagination';

import NoDataCard from '../components/NoDataCard';
import { useHistory } from "react-router-dom";
import TableLoader from './TableLoader';
import useSocket from "../../utils/socket/soket";

//import "../../assets/css/Datepicker/datepicker.css";
const defaultDatepicker = React.createContext('testing');


const Header = (props) => {
  const history = useHistory();
  const socket = useSocket();
  const {
    headerTitle,
    logoPath,
    pathOne,
    pathTwo,
    pathThree,
    linkOne = '#',
    linkTwo = '#',
    handleHamburger,
  } = props;
  const [activePage, setActivePage] = useState(1);
  const [startDate, setStartDate] = useState(new Date());
  const [userListData, setUserListData] = useState([]);
  const [checkType, setCheckType] = useState(0);
  const [actualKey, setActualKey] = useState('');
  const [tableMessage, setTableMessage] = useState("No Data Available");
  const [isOpen, setIsOpen] = useState(false);
  const [show_assign, setShowAssign] = useState(false);
  const handleCloseAssign = () => setShowAssign(false);

 
  const [userTableFilter, setUserTableFilter] = useState({
    search: '',
    material: '',
    user: '',
    skip: 0,
    limit: 10,
    date:'',
    type:'daily',
    usersType:'available'
  });
  const [isAssignedUser, setAssignedUser] = useState(false);
  const [totalRecord, setTotalRecord] = useState(0);
  const [notificationCount, setNotificationCount] = useState(0);
  
  const [showFromDate, setShowFromDate] = useState('');
  const [weeklyDate, setWeeklyDate] = useState('');
  const [notifications, setNotifications] = useState('');
  
  

  // useEffect(()=>{
  // 	window.matchMedia("(min-width: 768px)").addListener(handler);
  // },[])

  // const handler = () =>{
  //     if(window.width < 767){
  // 		console.log('787')
  // 	}
  //     if(window.width > 767){
  // 		console.log('78frd7')
  // 	}
  // }
  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === "pageSize") {
      setUserTableFilter({
        ...userTableFilter,
        skip: 0,
        limit: value,
      });
    }
    if (name === "search") {
      setUserTableFilter({
        ...userTableFilter,
        skip: 0,
        search: value,
      });
    }
    if (name === "status") {
      setUserTableFilter({
        ...userTableFilter,
        skip: 0,
        status: value,
      });
    }
  };
  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setUserTableFilter({
        ...userTableFilter,
        skip: (pageNumber - 1) * userTableFilter.limit,
      });
    }
  };

  const selectGlobalDatepicker = (date) => {
    setStartDate(date);
    setUserTableFilter({
      ...userTableFilter,
      date: date,
      type: 'daily',
      usersType: 'available',
    });
    getUserList();
    setShowAssign(true);
    const fromDate = moment(date).format('DD-MMM-YYYY');
    console.log(fromDate,'fromDate');
    setShowFromDate(fromDate);
    /*<HelloContext.Provider
        value={ {date: date} }>
          
    </HelloContext.Provider>*/
  };

  
  const toggleActive = (e,type) => {
    e.preventDefault();
    console.log('fdgsdf',type);
    if(checkType==1){
      setUserTableFilter({
        ...userTableFilter,
        type: type,
      });
      
      if(type=='daily'){
        const fromDate = moment(startDate).format('DD-MMM-YYYY');
        setShowFromDate(fromDate);
      }else{
        const toDay = new Date(startDate).getDay();
        const startOfWeek = moment(new Date(new Date(startDate).getTime() - ((toDay) * 24 * 60 * 60 * 1000))).format('DD-MMM-YYYY');
        const strtweek =new Date(new Date(startDate).getTime() - ((toDay) * 24 * 60 * 60 * 1000));
        const endOfWeek = moment(new Date(new Date(strtweek).getTime() + ((6) * 24 * 60 * 60 * 1000))).format('DD-MMM-YYYY');
        const finalDateToShow = '('+startOfWeek+') To ('+endOfWeek+')';
        setWeeklyDate(finalDateToShow);
      }
      

    }
  };

  const toggleAvailability = (e,type) => {
    e.preventDefault();
    console.log('fdgsdf',type);
    if(checkType==1){
      setUserTableFilter({
        ...userTableFilter,
        usersType: type,
      });
    }
  };

  const openCalendar = (e,key) => {
    e.preventDefault();
    let newData=[];
    let date;
    
    
    let data = userListData.slice();

    for(let i=0;i<data[key].blockedDates.length;i++){
      date=new Date(
        moment(data[key].blockedDates[i].day).format('YYYY-MM-DD')
      );
      newData.push(date);
    }


    if(actualKey && actualKey!=''){
      data[actualKey].isOpen = false;
    }
    data[key].isOpen = true;
    data[key].blockedData = newData;
    setUserListData(data);
    console.log('userListData',userListData);
  };

  const closeIsOpen = (e,key) => {
    let data = userListData.slice();
    data[key].isOpen = false;
    setUserListData(data);
  };
  

  useEffect(() => {
    getUserList();
    getNotifications();
  }, [userTableFilter]);

  useEffect(() => {
    socket.on("pushnotification", (data) => {
      let notification = notificationCount;  
      console.log(notification,'notification');
      console.log('push came----->>>>>>>>>>');
        setNotificationCount(notification+1);
        getNotifications();
     });
  },[socket,notificationCount]);

  const handleUserSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    if (name === 'search') {
      setUserTableFilter({
        ...userTableFilter,
        skip: 0,
        search: value,
      });
    }
  };

  const getUserList = async () => {
    setAssignedUser(true);
    setUserListData([]);
    setTotalRecord(0);
      const response = await FetchFromServer(
        ApiRoutes.USER_AVAILABLE_LIST.service,
        ApiRoutes.USER_AVAILABLE_LIST.url,
        ApiRoutes.USER_AVAILABLE_LIST.method,
        ApiRoutes.USER_AVAILABLE_LIST.authenticate,
        userTableFilter,
        undefined
      );
      if (response && response.isError) {
        //toast.error(response.messages[0]);
      } else {
        setCheckType(1);
        setUserListData(response.data.data);
        setTotalRecord(response.data.totalRecords);
      }
      setAssignedUser(false);
  };

  
  const changeNotificationCount = async () => {
    const response = await FetchFromServer(
        ApiRoutes.CHANGE_NOTIFICATIONS_COUNT.service,
        ApiRoutes.CHANGE_NOTIFICATIONS_COUNT.url,
        ApiRoutes.CHANGE_NOTIFICATIONS_COUNT.method,
        ApiRoutes.CHANGE_NOTIFICATIONS_COUNT.authenticate,
        undefined,
        undefined
      );
      if (response && response.isError) {
        //toast.error(response.messages[0]);
      } else {
        setNotificationCount(0);
      }
      
  };

  const getNotifications = async () => {
     const response = await FetchFromServer(
        ApiRoutes.NOTIFICATIONS_LIST.service,
        ApiRoutes.NOTIFICATIONS_LIST.url,
        ApiRoutes.NOTIFICATIONS_LIST.method,
        ApiRoutes.NOTIFICATIONS_LIST.authenticate,
        undefined,
        undefined
      );
      if (response && response.isError) {
        //toast.error(response.messages[0]);
      } else {
        setNotifications(response.data.data);
        let count = response.data.notificationCount[0]?response.data.notificationCount[0].count:0;
        setNotificationCount(count);
      }
  };

  const ExampleCustomInput = forwardRef(({ value, onClick }, ref) => (
    <button className="calendar" onClick={onClick} ref={ref}>
      <div>
        <img src={Calendar} alt={Calendar} />
      </div>
      <p>{moment(value).format('dddd')}</p>
      <p>{moment(value).format('MMM D')}</p>
    </button>
  ));
  let urlTo='';
  return (
    <>
    <div>
      <nav className="navbar navbar-expand navbar-light bg-white topbar static-top">
        <button
          id="sidebarToggleTop"
          className="btn btn-link d-md-none rounded-circle mr-0 pl-0"
          onClick={handleHamburger}
        >
          <img width="40" src={hamburger} alt={hamburger} />
        </button>

        <div className="d-flex align-items-center w-100">
          <h6 className="mb-0 view_text">{headerTitle}</h6>
          <img className="ml-3" src={logoPath} alt="" />
          <Link
            to={linkOne}
            className={`m-0 navPath ${pathTwo ? 'headerPath' : ''}`}
          >
            {pathOne}
          </Link>
          <Link
            to={linkTwo}
            className={`m-0 navPath ${pathThree ? 'headerPath' : ''}`}
          >
            {pathTwo}
          </Link>
          {pathThree ? <Link className="m-0 navPath">{pathThree}</Link> : ''}
        </div>

        <div className="d-flex align-items-center justify-content-end w-100">
          {/* <div className="calendar">
            <div>
              <img src={Calendar} alt={Calendar} />
            </div>
            <p>{moment().format('dddd')}</p>
            <p>{moment().format('D MMM')}</p>
          </div> */}
          <DatePicker
            selected={startDate}
            //dateFormat="dd/MM/yyyy"
            onChange={(date) => selectGlobalDatepicker(date)}
            customInput={<ExampleCustomInput />}
            //peekNextMonth
            showMonthDropdown
            showYearDropdown
            dropdownMode="select"
          />

          <ul className="navbar-nav">
            <li className="nav-item dropdown no-arrow ml_50px">
              <Dropdown>
                <Dropdown.Toggle
                  variant="primary"
                  as="a"
                  href="#"
                  className="nav-link dropdown-toggle position-relative"
                  
                >
                  <img style={{ width: 13 }} src={Bell} alt={Bell} onClick={() => changeNotificationCount()}/>
                  {notificationCount!=0 ?
                  (<span>{notificationCount}</span>) : ""}
                </Dropdown.Toggle>
                <Dropdown.Menu className="dropdown-list dropdown-menu-right shadow animated--grow-in notificationsBox">
                  <Dropdown.Header as="h6" className="border-bottom">
                    Notifications
                  </Dropdown.Header>
                  <div className="scroll-box">
                  {
                  notifications &&
                      notifications.map((item, key) => {
                          if(item.type=='requestNotification'){
                            urlTo='/warehouse';
                          }else{
                            urlTo='/chat';
                          }
                          return (
                    
                      <Dropdown.Item className="d-flex align-items-center py-2" key={key}>
                        <Link to={urlTo}>
                      <div className="mr-3">
                        <img src={
                            item && item.profilePicture
                                ? item.profilePicture.startsWith("https://")
                                  ? item.profilePicture
                                  : `${AppConfig.FILES_ENDPOINT}${item.profilePicture}`
                                : Userchat
                            }
                            onError={(e) => {
                              e.target.onerror = null;
                              e.target.src = Userchat;
                            }}
                            alt={Userchat} />
                      </div>
                      <div className="d-flex flex-column notify">
                        <div className="small text-gray-500">
                          
                          {item.createdAt ? moment(item.createdAt).format("DD MMMM YYYY hh:mm A") : ""}
                        </div>
                        <span className="font-weight-bold title">
                          
                          {item.message}
                        </span>
                        <span className="discription">
                        {item.content ? item.content : "" }
                        </span>
                      </div>
                      </Link>
                      
                    </Dropdown.Item> 
                   
                    )})}
                  </div>
                  
                  <Dropdown.Item className="text-center small viewAll border-top">
                  <Link
                    to='/notification'
                   >
                    View all notifications
                    </Link>
                  </Dropdown.Item>
                  
                </Dropdown.Menu>
              </Dropdown>
            </li>

            <li className="nav-item dropdown userHead">
              <Dropdown>
                <Dropdown.Toggle
                  variant="primary"
                  as="a"
                  href="#"
                  id="dropdown-basic"
                  className="nav-link d-flex align-items-center justify-content-between flex-row-reverse"
                >
                  <img
                    className="img-profile rounded-circle"
                    src={NavUser}
                    alt={NavUser}
                  />
                  <span className="ml-1 mr-2 d-none d-lg-inline text-gray-600 small">
                    Hi, <span class="headerUserName">Ethan</span>
                  </span>
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Link to="/profile" className="dropdown-item">
                    <img className="mr-2" src={User} alt={User} /> Admin Profile
                  </Link>
                  <Link to="/settings" className="dropdown-item">
                    <img className="mr-2" src={Settings} alt={Settings} />{' '}
                    Settings{' '}
                  </Link>
                  <Dropdown.Item onClick={() => logout()}>
                    {' '}
                    <img
                      className="mr-2"
                      src={Logout}
                      alt={Logout}
                    /> Logout{' '}
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            </li>
          </ul>
        </div>
      </nav>
    </div>
    <Modal
    show={show_assign}
    onHide={handleCloseAssign}
    className="addCardPopup material_assignModal d-flex align-items-center justify-content-center datePickerModel"
  >
    <span className="modelClose" closeButton onClick={handleCloseAssign}>
      <img src={popupClose} alt={popupClose} />
    </span>
    <Modal.Body className="p-0">
      <Container fluid className="px-0">
        <Row className="no-gutters">
          <Col md={12} className="p-4">
            <Row>
              <Col md={12}>
                <Tab.Container className="tabbings" id="left-tabs-example" defaultActiveKey="first">
                      {/*<Nav variant="pills" className="">
                        <Nav.Item>
                          <Nav.Link eventKey="first" onClick={(e) => toggleActive(e,'daily')}>Daily</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                          <Nav.Link eventKey="second" onClick={(e) => toggleActive(e,'weekly')} >Weekly</Nav.Link>
                        </Nav.Item>
                      </Nav>*/}
                      <Nav variant="pills" className="">
                        <Nav.Item>
                          <Nav.Link eventKey="first" onClick={(e) => toggleAvailability(e,'available')}>Available</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                          <Nav.Link eventKey="second" onClick={(e) => toggleAvailability(e,'notavailable')} >Not Available</Nav.Link>
                        </Nav.Item>
                      </Nav>

                </Tab.Container>
              </Col>
              <Col md={5}>
                <div class="mb-0 form-group searchBar">
                  <input
                    type="text"
                    placeholder="Search"
                    class="form-control"
                    name="search"
                    onChange={handleUserSearch}
                  />
                  <label class="mb-0">
                    <span>Search</span> in all fields
                  </label>
                </div>
              </Col>

              <Col md={7} className="text-right d-flex align-items-end justify-content-end"> 
              {/*<p className="mb-0 date">{userTableFilter.type=='daily' ? showFromDate : weeklyDate }</p>*/}
              
                {/*<p className="mb-0 date">(21-Jun-2021) To (29-Jun-2021)</p>*/}
              </Col>

              <Col md={12}>
                <div class="table-responsive scrollBox">
                  <table class="mt-3 table table-hover">
                    
                    <tbody>
                      <tr class="first-child">
                        <th class="border-0">ID</th>
                        <th class="border-0 minWidth100px">FIRST NAME</th>
                        <th class="border-0 minWidth100px">LAST NAME</th>
                        {/*<th class="border-0 width100px">Status</th>*/}
                        
                        <th class="border-0 width100px"></th>
                      </tr>
                      {userListData &&
                        userListData.map((item, key) => {
                          return (
                            <tr key={key}>
                              <td>{item.userId}</td>
                              <td>{item.firstName}</td>
                              <td>{item.lastName}</td>
                             {/* <td><span className="userAvailable">Available</span></td>*/}
                              {/*<td className="action-td">
                                <div className="text-right">
                                <a href={`tel:${item.phoneNumber}`}><img src={Phone_dt} alt={Phone_dt} /></a>
                                <img onClick={() => history.push({pathname: "/chat",state: item._id})} className="ml-3" src={Msg_dt} alt={Msg_dt} />
                                </div>
                          </td>*/}
                              <td>
                              <img src={Calendar} alt={Calendar} onClick={(e) => openCalendar(e,key)}/>
                                
                              {(item.blockedData && item.blockedData.length > 0 ) ? (
              
                                    <DatePicker
                                    selected={startDate}
                                    onInputClick={(e) => closeIsOpen(e,key)}
                                    onClickOutside={(e) => closeIsOpen(e,key)}
                                    open={item.isOpen ? item.isOpen :false}
                                    //autoFocus={true}
                                    wrapperClassName="form-control"
                                    className="form-control calenderDP"
                                    placeholderText="Select Dates"
                                    dateFormat="dd/MM/yyyy"
                                    peekNextMonth
                                    showMonthDropdown
                                    showYearDropdown
                                    dropdownMode="select"
                                    /*excludeDates={[
                                      new Date(
                                        moment('2021-07-12').format('YYYY-MM-DD')
                                      ),
                                      new Date(
                                        moment('2021-07-21').format('YYYY-MM-DD')
                                      )
                                    ]}*/
                                    highlightDates={item.blockedData}
                                    
                                />
                                ) : (
                                  <DatePicker
                                    selected={startDate}
                                    onInputClick={(e) => closeIsOpen(e,key)}
                                    onClickOutside={(e) => closeIsOpen(e,key)}
                                    open={item.isOpen ? item.isOpen :false}
                                    //autoFocus={true}
                                    wrapperClassName="form-control"
                                    className="form-control calenderDP"
                                    placeholderText="Select Dates"
                                    dateFormat="dd/MM/yyyy"
                                    peekNextMonth
                                    showMonthDropdown
                                    showYearDropdown
                                    dropdownMode="select"
                                />
                                    
                                ) }
                              </td>
                            </tr>
                          );
                        })}
                        {userListData.length === 0 && isAssignedUser === false ? (
                          <tr>
                            <td colSpan="6">
                              <NoDataCard title={tableMessage} />
                            </td>
                          </tr>
                        ) : (
                          ''
                        )}
                        {isAssignedUser==true ? <TableLoader numOfrows={10} cols={6} /> : ''}
                    </tbody>
                  </table>
                  <div className="bg-white py-2 px-0 rounded jobHistoryPage mt-3">
                    {totalRecord && userTableFilter.limit !== 'all' ? (
                      <Row className="no-gutters">
                        <Col sm={12} md={6} lg={7} className="paginationBox">
                          {totalRecord > userTableFilter.limit ? (
                            <Pagination
                              activePage={activePage}
                              itemsCountPerPage={userTableFilter.limit}
                              totalItemsCount={totalRecord}
                              // pageRangeDisplayed={5}
                              onChange={handlePageChange}
                              hideFirstLastPages="false"
                            />
                          ) : (
                            ''
                          )}
                        </Col>
                        <Col sm={12} md={6} lg={5}>
                          <div className="d-flex align-items-center justify-content-end sortBox">
                            <Form.Control
                              as="select"
                              className="mr-0"
                              style={{ width: '100px', background: '#F4F6F9' }}
                              name="pageSize"
                              onChange={handleSearch}
                            >
                              <option>10</option>
                              <option>25</option>
                              <option>50</option>
                            </Form.Control>
                            <p className="mb-0 ml-2">
                              showing rows {userTableFilter.skip + 1} to{' '}
                              {Number(userTableFilter.skip) + Number(userTableFilter.limit) >
                              totalRecord
                                ? totalRecord
                                : Number(userTableFilter.skip) + Number(userTableFilter.limit)}{' '}
                              of {totalRecord}
                            </p>
                          </div>
                        </Col>
                      </Row>
                    ) : (
                      ''
                    )}
                  </div>
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>
    </Modal.Body>
  </Modal>
  </>
  );
};

export default Header;
