import React from "react";
import { Link } from "react-router-dom";
import { Table } from "react-bootstrap";

import openJobsList from "../../../assets/img/open_jobs_list.svg";
import clockGreen from "../../../assets/img/clock_green.svg";
import moment from "moment";

const OpenJobs = (props) => {
  return (
    <div className="p-16 position-relative">
      <Link to={`/report/${props.id}`}>
        <div className="tagStrip m-0">
          <span>Job ID:</span> {props.jobID}
        </div>
        <div className="d-flex align-items-center justify-content-end mb-3 one">
          <h5 className="mb-0">$ {props.amount}</h5>
          <img className="ml-4" src={openJobsList} alt={openJobsList} />
        </div>
        <h5 className="mb-3 two">{props.title}</h5>
        <Table className="table">
          <tbody>
            <tr>
              <td className="border-0">PARENT ID</td>
              <td className="border-0">
                <span>{props.parentID}</span>
              </td>
            </tr>
            <tr>
              <td>TASK ID</td>
              <td>
                <span>{props.taskID}</span>
              </td>
            </tr>
            <tr>
              <td>Job Type</td>
              <td>
                <span>{props.jobType}</span>
              </td>
            </tr>
            <tr>
              <td>START TIME</td>
              <td>
                <span>
                  {props.time === null
                    ? "Not started yet"
                    : moment(props.time).format("DD-MM-YYYY hh:mm A")}
                </span>
              </td>
            </tr>
          </tbody>
        </Table>
        <div className="progressBox d-flex align-items-center justify-content-start">
          <div className="progress w-100 position-relative" >
            <div
              className="progress-bar"
              role="progressbar"
              style={{ width: props.timePercentage ,backgroundColor:props.color }}
              aria-valuenow="50"
              aria-valuemin="0"
              aria-valuemax="100"
            >
              {props.spentTime ? props.spentTime+'min' :''}
            </div>
            <img src={clockGreen} alt={clockGreen} />
          </div>
          <span className="">{props.totalTime}min</span>
        </div>
      </Link>
    </div>
  );
};
export default OpenJobs;
