import React, { useState, useEffect } from "react";
import { Form, Button, Spinner } from "react-bootstrap";
import cameraChooseFileImg from "../../../assets/img/camera_chooseFile.svg";
import dummyImage from "../../../assets/img/tool-new.svg";
import modalDeleteBtn from "./../../../assets/img/modaldelete_btn.svg";
import uploadCloudImg from "../../../assets/img/uploadCloud.svg";
import DatePicker from "react-datepicker";
import moment from "moment";

const ToolInformation = (props) => {
  const {
    handleChange,
    errors,
    values,
    touched,
    removeFile,
    handleSubmit,
    isBtnDisable,
    isAdd,
    setDeleteModalShow,
  } = props;
  const [profilePicture, setProfilePicture] = useState(dummyImage);
  const [certificationName, setCertificationName] = useState(
    "Click or drag to upload files"
  );
  const fileChangedHandler = (event) => {
    const file = event.target.files[0];
    const src = URL.createObjectURL(file);
    setProfilePicture(src);
  };
  const certificationChangedHandler = (event) => {
    const file = event.target.files;
    if (file.length === 0) {
      setCertificationName("Click or drag to upload files");
    } else {
      setCertificationName(file[0].name);
    }
  };
  useEffect(() => {
    if (values)
      if (values.profilePicture) setProfilePicture(values.profilePicture);
    // eslint-disable-next-line
  }, [values.profilePicture]);
  return (
    <div className="row">
      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="input-file position-relative d-flex align-items-center">
          <div className="position-relative">
            <img
              id="file_upload"
              src={profilePicture}
              alt={profilePicture}
              className="upload-img"
            />
            <span className="upload-label">
              <img src={cameraChooseFileImg} alt={cameraChooseFileImg} />
            </span>
          </div>
          <p className="mb-0">Click to add the tool image</p>
          <div className="input-file-upload">
            <input
              type="file"
              name="toolPicture"
              onChange={fileChangedHandler}
              accept="image/*"
            />
          </div>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <Form.Label className="mb-0">Manufacturer</Form.Label>
          <Form.Control
            type="text"
            name="manufacturer"
            placeholder="scredrivers in paradise"
            onChange={handleChange}
            value={values ? values.manufacturer : ""}
          />
          <span className="errorMsg">
            {" "}
            {errors.manufacturer && touched.manufacturer
              ? errors.manufacturer
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Description</label>
          <Form.Control
            type="text"
            name="description"
            placeholder="screwdriver"
            onChange={handleChange}
            value={values ? values.description : ""}
          />
          <span className="errorMsg">
            {" "}
            {errors.description && touched.description
              ? errors.description
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Serial Number</label>
          <Form.Control
            type="text"
            name="serialNumber"
            placeholder="ID928392"
            onChange={handleChange}
            value={values ? values.serialNumber : ""}
          />
          <span className="errorMsg">
            {" "}
            {errors.serialNumber && touched.serialNumber
              ? errors.serialNumber
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <div className="d-flex justify-content-between">
            <label className="mb-0">Date of Last Calibration</label>
            <div>
              <Form.Group controlId="formBasicCheckbox" className="mb-0">
                <Form.Check
                  type="checkbox"
                  label="N/A"
                  name="naLastCalibration"
                  onChange={handleChange}
                  value={true}
                  checked={values ? values.naLastCalibration : false}
                />
              </Form.Group>
            </div>
          </div>

          <DatePicker
            wrapperClassName="form-control date-icon"
            //maxDate={new Date()}
            minDate={new Date(moment().subtract(100, 'years'))}
            className="form-control"
            placeholderText="dd/mm/yyyy"
            autoComplete={false}
            name="lastCalibration"
            dateFormat="dd/MM/yyyy"
            onChange={(date) => {
              if (date)
                handleChange("lastCalibration")(
                  moment(date).format("YYYY-MM-DD")
                );
              else handleChange("lastCalibration")("");
            }}
            selected={
              values && values.lastCalibration
                ? new Date(moment(values.lastCalibration).format("YYYY-MM-DD"))
                : ""
            }
            peekNextMonth
            showMonthDropdown
            showYearDropdown
            dropdownMode="select"
          />
          {/* <Form.Control
            type={values.lastCalibration ? 'date' : 'text'}
            placeholder="dd/mm/yyyy"
            className="date"
            name="lastCalibration"
            onChange={handleChange}
            value={values ? values.lastCalibration : ''}
            onFocus={(e) => {
              e.currentTarget.type = 'date';
            }}
            onBlur={(e) => {
              if (e.currentTarget.value === '') {
                e.currentTarget.type = 'text';
              }
            }}
          />
          <label></label> */}
          <span className="errorMsg">
            {" "}
            {errors.lastCalibration && touched.lastCalibration
              ? errors.lastCalibration
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <div className="d-flex justify-content-between">
            <label className="mb-0">Date of Next Calibration</label>
            <div>
              <Form.Group controlId="formBasicCheckbox2" className="mb-0">
                <Form.Check
                  type="checkbox"
                  label="N/A"
                  name="naNextCalibration"
                  onChange={handleChange}
                  value={true}
                  checked={values ? values.naNextCalibration : false}
                />
              </Form.Group>
            </div>
          </div>
          <DatePicker
            wrapperClassName="form-control date-icon"
            //maxDate={new Date()}
            minDate={new Date(moment().subtract(100, "years"))}
            className="form-control"
            placeholderText="dd/mm/yyyy"
            autoComplete={false}
            name="nextCalibration"
            dateFormat="dd/MM/yyyy"
            onChange={(date) => {
              if (date)
                handleChange("nextCalibration")(
                  moment(date).format("YYYY-MM-DD")
                );
              else handleChange("nextCalibration")("");
            }}
            selected={
              values && values.nextCalibration
                ? new Date(moment(values.nextCalibration).format("YYYY-MM-DD"))
                : ""
            }
            peekNextMonth
            showMonthDropdown
            showYearDropdown
            dropdownMode="select"
          />
          {/* <Form.Control
            type={values.nextCalibration ? 'date' : 'text'}
            placeholder="dd/mm/yyyy"
            className="date"
            name="nextCalibration"
            onChange={handleChange}
            value={values ? values.nextCalibration : ''}
            onFocus={(e) => {
              e.currentTarget.type = 'date';
            }}
            onBlur={(e) => {
              if (e.currentTarget.value === '') {
                e.currentTarget.type = 'text';
              }
            }}
          />
          <label></label> */}
          <span className="errorMsg">
            {" "}
            {errors.nextCalibration && touched.nextCalibration
              ? errors.nextCalibration
              : null}
          </span>
        </div>
      </div>

      {/* <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Upload Tool Certification</label>
          <Form.Control
            type="file"
            name="certification"
            onChange={handleChange}
            value={values ? values.certification : ''}
            accept="application/pdf"
          />
          <span className="errorMsg">
            {' '}
            {errors.certification && touched.certification
              ? errors.certification
              : null}
          </span>
        </div>
      </div> */}

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4 addCardPopup">
        <Form.Group className="w-100">
          <label>Upload Tool Certification</label>
          {values.certification ? (
            <div className="position-relative uploadCloud w-100 mr-2">
              {/* <input
                      type="file"
                      name="cardImages"
                      accept="image/*"
                      multiple
                    /> */}
              <p className="mb-0 d-flex align-items-center justify-content-between">
                <span>Certification.PDF</span>
                <img
                  className="mr-0"
                  src={modalDeleteBtn}
                  alt={modalDeleteBtn}
                  onClick={() => removeFile()}
                />{" "}
              </p>
            </div>
          ) : (
            <div className="position-relative uploadCloud">
              <input
                type="file"
                name="certification"
                accept="application/pdf"
                onChange={certificationChangedHandler}
              />
              <p className="mb-0">
                <img
                  className="mr-2"
                  src={uploadCloudImg}
                  alt={uploadCloudImg}
                />{" "}
                <span>{certificationName}</span>
              </p>
            </div>
          )}
        </Form.Group>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-8 pageButtomBtn">
        <label className="invisible mb-0">don't deleted this</label>

        <div className="d-flex align-items-center justify-content-end">
          {isAdd ? (
            <Button
              variant=""
              className="px-5 py-2 rounded btn-pink ml-3 ToolAdd_form_btn"
              style={{ fontSize: "14px" }}
              onClick={handleSubmit}
              disabled={isBtnDisable}
            >
              {isBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ""
              )}{" "}
              Add Tool
            </Button>
          ) : (
            <>
              <Button
                variant=""
                className="px-5 py-2 rounded addcard form_btn"
                style={{ background: "#E5EAEE", fontSize: "14px" }}
                onClick={() => setDeleteModalShow(true)}
              >
                Delete Tool
              </Button>

              <Button
                variant=""
                className="px-5 py-2 rounded btn-pink ml-3 form_btn btn"
                style={{ background: "rgb(250 70 89)", fontSize: "14px" }}
                onClick={handleSubmit}
                disabled={isBtnDisable}
              >
                {isBtnDisable ? (
                  <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                  />
                ) : (
                  ""
                )}{" "}
                Save Updates
              </Button>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export { ToolInformation };
