import React from 'react';
import { Form, Row, Col } from 'react-bootstrap';

const RuleInformation = (props) => {
  const {
    handleChange,
    errors,
    values,
    technologyData,
    scheduleData,
    touched,
  } = props;
  return (
    <Row className="arrow_Dropdown">
      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group className="mb-3">
          <label className="mb-0">Select Schedule</label>
          <Form.Control
            as="select"
            name="schedule"
            onChange={handleChange}
            value={values.schedule ? values.schedule : ''}
          >
            <option hidden value="">
              Select the schedule
            </option>
            {scheduleData &&
              scheduleData.map((data, i) => (
                <option value={data._id} key={i}>
                  {data.scheduleName}
                </option>
              ))}
          </Form.Control>
          <span className="errorMsg">
            {' '}
            {errors.schedule && touched.schedule ? errors.schedule : null}
          </span>
        </Form.Group>
      </Col>
      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group className="mb-3">
          <label className="mb-0">Select Job Type</label>
          <Form.Control
            as="select"
            name="technology"
            onChange={handleChange}
            value={values.technology ? values.technology : ''}
          >
            <option hidden value="">
              Select job type
            </option>
            {technologyData &&
              technologyData.map((data, i) => (
                <option value={data._id} key={i}>
                  {data.technologyName}
                </option>
              ))}
          </Form.Control>
          <span className="errorMsg">
            {' '}
            {errors.technology && touched.technology ? errors.technology : null}
          </span>
        </Form.Group>
      </Col>

      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group className="mb-3">
          <label className="mb-0">Add Time Expected (min)</label>
          <Form.Control
            type="text"
            name="name"
            placeholder="Add maximum time expected"
            onChange={handleChange}
            value={values ? values.name : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.name && touched.name ? errors.name : null}
          </span>
        </Form.Group>
      </Col>
    </Row>
  );
};

export { RuleInformation };
