import socketIOClient from "socket.io-client";
let socket = null;

const Socket = () => {
  const token = JSON.parse(localStorage.getItem("wyredUserData")).token;
  return (
    /*socket ||
    (socket = socketIOClient("http://54.206.106.119:8080", { 

      query: `token=${token}`,
    }))*/
    socket ||
    (socket = socketIOClient("https://app.wyred.com.au", {
      query: `token=${token}`,
    },{'force new connection': true }))
    
  );

};

export default Socket;
