import React, { useEffect, useState, useRef } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { toast } from 'react-toastify';
import {
  Accordion,
  Card,
  Col,
  Container,
  FormGroup,
  Row,
  Table,
  Button,
  Modal,
  Spinner,
} from 'react-bootstrap';

import previewEye from '../../../assets/img/previewEye.svg';
import assignImg from '../../../assets/img/assign.svg';
import dummyImage from '../../../assets/img/tool-new.svg';
import arrowup from '../../../assets/img/arrow-up-navigate.svg';
import arrowdown from '../../../assets/img/arrow-down-navigate.svg';
import NotFound from '../../../assets/img/on_data_found.svg';
import popupClose from './../../../assets/img/popupClose.svg';
import edit from './../../../assets/img/edit-warehouse.svg';

import { ExchangeRequest, MaterialRequest } from '../../components/Home';
import NoDataCard from '../../components/NoDataCard';
import TableFooter from '../../components/TableFooter';
import { TableLoader } from '../../components/loader/Resusable/Table';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes, AppConfig } from '../../../utils/config';
import Skeleton from "react-loading-skeleton";

const MaterialsList = () => {
  const query = new URLSearchParams(useLocation().search);
  const [materialData, setMaterialData] = useState([]);
  const [userListData, setUserListData] = useState([]);
  const [isAssign, setIsAssign] = useState('');
  const [materialInfoData, setMaterialInfoData] = useState();
  const [totalRecord, setTotalRecord] = useState(0);
  const [activePage, setActivePage] = useState(1);
  const [isFeedLoading, setIsFeedLoading] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  const [tableMessage, setTableMessage] = useState('No Data Available');
  const [imagePreviewPath, setImagePreviewPath] = useState('');
  const [show, setShow] = useState(false);
  const [toggleStatus, setToggleStatus] = useState(false);
  const [isAssignedUser, setAssignedUser] = useState(false);
  const [liveFeedData, setLiveFeedData] = useState([]);
  const [userTableFilter, setUserTableFilter] = useState({
    search: '',
    material: '',
    user: '',
    skip: 0,
    limit: 10,
  });
  let activePageStored = localStorage.getItem('warehouseActivePage');
  let storedtableFilters = JSON.parse(localStorage.getItem('warehousetableFilter'));
  const [tableFilter, setTableFilter] = useState({
    search: '',
    skip: 0,
    limit: 10,
  });
  const [asignProductQty, setAsignProductQty] = useState([]);

  useEffect(() => {
    window.scrollTo(0, 0)
    const material = query.get('material');
    const user = query.get('user');

    if (material) handleShowAssign(material, user);
    let renderStatus = localStorage.getItem('warehouserenderStatus');
    if(renderStatus==1 && activePageStored){
      setActivePage(parseInt(activePageStored));
      setTableFilter({
        ...tableFilter,
        skip: (parseInt(activePageStored) - 1) * storedtableFilters.limit,
        search: storedtableFilters.search? storedtableFilters.search :'',
      });
      localStorage.setItem('warehouserenderStatus',0);
    }
  }, []);

  useEffect(() => {
    const getMaterial = async () => {
      console.log('coming in first request');
      setIsLoading(true);
      
      const response = await FetchFromServer(
        ApiRoutes.MATERIAL_LIST.service,
        ApiRoutes.MATERIAL_LIST.url,
        ApiRoutes.MATERIAL_LIST.method,
        ApiRoutes.MATERIAL_LIST.authenticate,
        tableFilter,
        undefined
      );
      setIsLoading(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTableMessage(response.messages[0]);
        setMaterialData(response.data.data);
        setTotalRecord(response.data.totalRecords);
      }
    };
    getMaterial();
    // eslint-disable-next-line
  }, [tableFilter]);
  const refreshMaterial = async () =>{
    const getMaterial = async () => {

      console.log('coming in second list');
      setIsLoading(true);
      let renderStatus = localStorage.getItem('warehouserenderStatus');
      if(renderStatus==1 && activePageStored){
        console.log(parseInt(activePageStored));
        setActivePage(parseInt(activePageStored));
        
        setTableFilter({
          ...tableFilter,
          skip: (parseInt(activePageStored) - 1) * tableFilter.limit,
          search: storedtableFilters.search? storedtableFilters.search :'',
        });
        localStorage.setItem('warehouserenderStatus',0);
      }
      const response = await FetchFromServer(
        ApiRoutes.MATERIAL_LIST.service,
        ApiRoutes.MATERIAL_LIST.url,
        ApiRoutes.MATERIAL_LIST.method,
        ApiRoutes.MATERIAL_LIST.authenticate,
        tableFilter,
        undefined
      );
      setIsLoading(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTableMessage(response.messages[0]);
        setMaterialData(response.data.data);
        setTotalRecord(response.data.totalRecords);
      }
    };
    getMaterial();
  }
  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
      localStorage.setItem('warehouseActivePage', pageNumber);
	    localStorage.setItem('warehousetableFilter', JSON.stringify(tableFilter));
    }
  };
  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === 'pageSize') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
      storedtableFilters.skip=0;
	    storedtableFilters.limit=value;
    }
    if (name === 'search') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        search: value,
      });
      storedtableFilters.skip=0;
	    storedtableFilters.limit=value;
    }
    if (name === 'schedule') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        schedule: value,
      });
      storedtableFilters.skip=0;
	    storedtableFilters.schedule=value;
    }
    localStorage.setItem('tableFilter', JSON.stringify(storedtableFilters));
  };
  const handleClose = () => setShow(false);
  const handleImagePreview = (key) => {
    setImagePreviewPath(key);
    setShow(true);
  };

  const [show_assign, setShowAssign] = useState(false);
  const handleCloseAssign = () => setShowAssign(false);

  const getMaterial = async (id) => {
    const response = await FetchFromServer(
      ApiRoutes.MATERIAL_INFO.service,
      ApiRoutes.MATERIAL_INFO.url.replace(':id', id),
      ApiRoutes.MATERIAL_INFO.method,
      ApiRoutes.MATERIAL_INFO.authenticate,
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setMaterialInfoData(response.data.data);
    }
  };
  const getUserList = async (id) => {
    if(userTableFilter.material!=""){
      const response = await FetchFromServer(
        ApiRoutes.MATERIAL_USER_LIST.service,
        ApiRoutes.MATERIAL_USER_LIST.url,
        ApiRoutes.MATERIAL_USER_LIST.method,
        ApiRoutes.MATERIAL_USER_LIST.authenticate,
        userTableFilter,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setUserListData(response.data.data);
      }
    }
  };
  const getLiveFeed = async () => {
    
    const response = await FetchFromServer(
      ApiRoutes.LIVE_FEED.service,
      ApiRoutes.LIVE_FEED.url,
      ApiRoutes.LIVE_FEED.method,
      ApiRoutes.LIVE_FEED.authenticate,
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setIsFeedLoading(false);
      setLiveFeedData(response.data.data);
    }
  };
  useEffect(() => {
    getUserList();
    getLiveFeed();
    // eslint-disable-next-line
  }, [userTableFilter]);
  const handleShowAssign = async (id, user = '') => {
    setUserTableFilter({
      ...userTableFilter,
      user: user,
      skip: 0,
      limit:10000,
      search: '',
      material: id,
    });
    await getMaterial(id);
    // await getUserList();
    setShowAssign(true);
    redirect();
  };
  const redirect = () => {
    localStorage.setItem('warehouserenderStatus', 1); 
  }
  const handleUserSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    if (name === 'search') {
      setUserTableFilter({
        ...userTableFilter,
        skip: 0,
        search: value,
      });
    }
  };

  const handleAsignProductQty = (id, value) => {
    let data = asignProductQty;
    data[id] = value;
    setAsignProductQty(data);
  };
  
  const assignMaterial = async (userId) => {
    const data = asignProductQty;
    const quantity = data[userId];
    const material = userTableFilter.material;
    setAssignedUser(true);
    if (quantity > 0) {
      setIsAssign(userId);
      const response = await FetchFromServer(
        ApiRoutes.ASSIGN_USER_MATERIAL.service,
        ApiRoutes.ASSIGN_USER_MATERIAL.url,
        ApiRoutes.ASSIGN_USER_MATERIAL.method,
        ApiRoutes.ASSIGN_USER_MATERIAL.authenticate,
        undefined,
        { user: userId, material: material, quantity: quantity }
      );
      setIsAssign('');
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        data[userId] = '';
        setAsignProductQty(data);
        console.log(data,'aftre assign data');
        getUserList();
        refreshMaterial();
        await getMaterial(material);
        toast.success(response.messages[0]);
      }
      setAssignedUser(false);
      
    } else {
      toast.dismiss();
      toast.error('Please assign product quantity');
      setAssignedUser(false);
    }
  };

 

  const ReadMore = ({ children }) => {
    const text = children;
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
      setIsReadMore(!isReadMore);
    };
    if (text.length > 150) {
      return (
        <p className="text">
          {isReadMore ? text.slice(0, 150) : text}
          <span onClick={toggleReadMore} className="read-or-hide">
            {isReadMore ? '...read more' : ' show less'}
          </span>
        </p>
      );
    } else {
      return text;
    }
  };
  function toggleActive() {
    setToggleStatus(!toggleStatus);
  }
  return (
    <>
      <div className="inner_subheader" >
        <div className="d-flex align-items-center tabBtn">
          {/* <Link to="/rates" className="btn active">
            Rates
          </Link>
          <Link to="/rates/rules" className="btn ml-2">
            Business Rules
          </Link> */}
        </div>

        <Link to="/material/add" className="btn btn-pink" onClick={redirect} >
          Add New Material
        </Link>
      </div>
      <Container fluid>
        <Row>
          <Col md={12} className="mt-4 mb-4">
            <Accordion defaultActiveKey="0">
              <Card>
                <Card.Header className="py-3 bg-transparent d-flex align-items-center justify-content-between pink_btn">
                  <h6 className="m-0">Live Stock Feed</h6>
                  <p className="m-0 p-0 warehouse d-flex align-items-center">
                    <Accordion.Toggle
                      as={Button}
                      variant="" 
                      eventKey="0"
                      onClick={() => toggleActive()}
                    >
                      {toggleStatus ? (
                        <img src={arrowup} alt={arrowup} width="20" />
                      ) : (
                        <img src={arrowdown} alt={arrowdown} width="20" />
                      )}
                    </Accordion.Toggle>
                  </p> 
                </Card.Header>
                <Accordion.Collapse  eventKey="0">
                  <Card.Body>
                     {/***Loader */}
                     {isFeedLoading ? (
                       <>
                      <div class="card_flex">
                      <Row>
                        <Col md={12}>
                          <div className="d-flex align-items-center justify-content-start overflow-auto wareHousePageSlide">

                          <div className="material_request">
                            <div className="card">
                              <div className="card-header exchange_request_div">
                                <h6 className="m-0 text-center"><Skeleton style={{ width: 150, height: 10}} /></h6>
                              </div>
                              <div className="card-body">
                                <div className="d-flex align-items-center p-3 row_1">
                                  <div className="img_box mr-2">
                                    <Skeleton style={{ width: 64, height: 64 }} />
                                  </div>
                                  <p className="m-0">
                                  <Skeleton style={{ width: 70, height: 10}} />
                                  <Skeleton style={{ width: 100, height: 10}} />
                                  <Skeleton style={{ width: 128, height: 10}} />
                                  </p>
                                </div>
                                <table className="table table-responsive m-0">
                                  <tbody>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value text_blue">
                                        <Skeleton style={{ width: 100, height: 10}} />
                                      </td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value text_blue">
                                      <Skeleton style={{ width: 100, height: 10}} />
                                      </td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value"><Skeleton style={{ width: 100, height: 10}} /></td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value"><Skeleton style={{ width: 100, height: 10}} /></td>
                                    </tr>
                                  </tbody>
                                </table>
                              </div>
                              <div className="card-footer">
                                <Link to="#">
                                  <Skeleton style={{ width: 70, height: 10}} />
                                </Link>
                                
                              </div>
                            </div>
                            </div>

                            <div className="material_request">
                            <div className="card">
                              <div className="card-header exchange_request_div">
                                <h6 className="m-0 text-center"><Skeleton style={{ width: 150, height: 10}} /></h6>
                              </div>
                              <div className="card-body">
                                <div className="d-flex align-items-center p-3 row_1">
                                  <div className="img_box mr-2">
                                    <Skeleton style={{ width: 64, height: 64 }} />
                                  </div>
                                  <p className="m-0">
                                  <Skeleton style={{ width: 70, height: 10}} />
                                  <Skeleton style={{ width: 100, height: 10}} />
                                  <Skeleton style={{ width: 128, height: 10}} />
                                  </p>
                                </div>
                                <table className="table table-responsive m-0">
                                  <tbody>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value text_blue">
                                        <Skeleton style={{ width: 100, height: 10}} />
                                      </td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value text_blue">
                                      <Skeleton style={{ width: 100, height: 10}} />
                                      </td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value"><Skeleton style={{ width: 100, height: 10}} /></td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value"><Skeleton style={{ width: 100, height: 10}} /></td>
                                    </tr>
                                  </tbody>
                                </table>
                              </div>
                              <div className="card-footer">
                                <Link to="#">
                                  <Skeleton style={{ width: 70, height: 10}} />
                                </Link>
                                
                              </div>
                            </div>
                            </div>

                            <div className="material_request">
                            <div className="card">
                              <div className="card-header exchange_request_div">
                                <h6 className="m-0 text-center"><Skeleton style={{ width: 150, height: 10}} /></h6>
                              </div>
                              <div className="card-body">
                                <div className="d-flex align-items-center p-3 row_1">
                                  <div className="img_box mr-2">
                                    <Skeleton style={{ width: 64, height: 64 }} />
                                  </div>
                                  <p className="m-0">
                                  <Skeleton style={{ width: 70, height: 10}} />
                                  <Skeleton style={{ width: 100, height: 10}} />
                                  <Skeleton style={{ width: 128, height: 10}} />
                                  </p>
                                </div>
                                <table className="table table-responsive m-0">
                                  <tbody>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value text_blue">
                                        <Skeleton style={{ width: 100, height: 10}} />
                                      </td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value text_blue">
                                      <Skeleton style={{ width: 100, height: 10}} />
                                      </td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value"><Skeleton style={{ width: 100, height: 10}} /></td>
                                    </tr>
                                    <tr>
                                      <td><Skeleton style={{ width: 30, height: 10}} /></td>
                                      <td className="value"><Skeleton style={{ width: 100, height: 10}} /></td>
                                    </tr>
                                  </tbody>
                                </table>
                              </div>
                              <div className="card-footer">
                                <Link to="#">
                                  <Skeleton style={{ width: 70, height: 10}} />
                                </Link>
                                
                              </div>
                            </div>
                            </div>

                          </div>
                        </Col>
                      </Row>
                      </div></>) : 
                     
                    liveFeedData.length ? (
                      <div className="card_flex">
                        <Row>
                          <Col xs={12} sm={12} md={12} lg={12} xl={12}>
                            <div className="d-flex align-items-center justify-content-start overflow-auto wareHousePageSlide">
                              {/* <ExchangeRequest /> */}
                              {liveFeedData.map((item, key) => {
                                return (
                                  <MaterialRequest data={item} key={key}  getLiveFeed = {getLiveFeed} refreshMaterial = {refreshMaterial}/>
                                );
                              })}
                            </div>
                          </Col>
                        </Row>
                      </div>
                    ) :
                    (<div className="d-flex align-items-center justify-content-center py-5">
                        <div className="position-relative noDataFound">
                          <img src={NotFound} alt={NotFound} />
                          <h6 className="mb-0">No Data Available</h6>
                          <p>The data you're looking for is not available</p>
                        </div>
                      </div>
                    ) }
                  </Card.Body>
                </Accordion.Collapse>
              </Card>
            </Accordion>
          </Col>

          <Col md={12}>
            <Row className="jobHistoryPage inventory">
              {/*<Col xs={12} sm={6} md={4} lg={3} xl={2}>
                <select className="form-control">
                  <option>10</option>
                  <option>25</option>
                  <option>50</option>
                  <option>100</option>
                  <option value="all">All</option>
                </select>
                <label className="mb-0">
                  <span>Filter</span> by job type
                </label>
              </Col>
              <Col xs={12} sm={6} md={4} lg={3} xl={3}>
                <FormGroup className="mb-0">
                  <input
                    type="text"
                    placeholder="Select Type of Schedule"
                    className="form-control"
                  />
                </FormGroup>
                <label className="mb-0">
                  <span>Filter</span> by Schedule
                </label>
                    </Col>*/}
              <Col xs={12} sm={12} md={4} lg={4} xl={3}>
                <FormGroup className="mb-0">
                  <input
                    type="text"
                    placeholder="Search"
                    className="form-control"
                    name="search"
                    onChange={handleSearch}
                  />
                </FormGroup>
                <label className="mb-0">
                  <span>Search</span> in all fields
                </label>
              </Col>

              <Col md={12}>
                {isLoading ? <TableLoader columns={6} rows={10} /> : ''}
                <Table hover responsive className="mt-3 warehouse_table">
                  <tbody>
                    <tr class="first-child">
                      <th class="border-0" width="110">
                        Product
                      </th>
                      <th class="border-0" width="180">
                        Description
                      </th>
                      <th class="border-0" width="130">
                        SALES PART N
                      </th>
                      <th class="border-0" width="130">
                        QTY IN STOCK
                      </th>
                      <th class="border-0" width="130">
                        Status
                      </th>
                      <th class="border-0"></th>
                    </tr>
                    {materialData &&
                      isLoading === false &&
                      materialData.map((data, key) => {
                        return (
                          <tr key={key}>
                            <td class="productItem position-relative">
                              <img
                                src={
                                  data.materialImage
                                    ? data.materialImage.startsWith('https://')
                                      ? data.materialImage
                                      : `${AppConfig.FILES_ENDPOINT}${data.materialImage}`
                                    : dummyImage
                                }
                                onError={(e) => {
                                  e.target.onerror = null;
                                  e.target.src = dummyImage;
                                }}
                                alt={data.materialImage}
                                width="64"
                              />
                              <div
                                onClick={() =>
                                  handleImagePreview(data.materialImage)
                                }
                              >
                                <img src={previewEye} alt={previewEye} />
                              </div>
                            </td>
                            <td>
                              <ReadMore><p className="mb-0">{data.materialDescription}</p></ReadMore>
                              {/* <div className="description">
                                {data.materialDescription}
                              </div> */}
                            </td>
                            <td>{data.salesPartNumber}</td>
                            <td>{data.inStock}</td>
                            <td>
                              { 
                                data.averageMarginAdmin ? 
                                  ( 
                                    (parseInt(data.inStock))  > (parseInt(data.averageMarginAdmin)) ? 
                                      (<span className="high">High</span>) : 
                                      '' 
                                  ) : ""

                              }
                              {
                                data.averageMarginAdmin ? 
                                ( 
                                  (parseInt(data.inStock))  <= (parseInt(data.lowMarginAdmin)) ? 
                                    (<span className="low">Low</span>) : 
                                    '' 
                                ) : ""
                              }
                              
                              {
                                data.averageMarginAdmin ? 
                                ( 
                                  ((parseInt(data.inStock))  > (parseInt(data.lowMarginAdmin)) && 
                                  (parseInt(data.inStock))  <= (parseInt(data.averageMarginAdmin))
                                  ) ? 
                                    (<span className="average">Average</span>) : 
                                    '' 
                                ) : ""
                              }
                             
                            </td>
                            <td class="Width200 pr-4">
                              <Link
                                className="text-dark mr-4 font-weight-bold"
                                onClick={() => handleShowAssign(data._id)}
                              >
                                <img
                                  class="mr-2"
                                  src={assignImg}
                                  alt={assignImg}
                                />{' '}
                                Assign
                              </Link>
                              <Link
                                onClick={redirect}
                                className="text-dark font-weight-bold"
                                to={`/material/edit/${data._id}`}
                              >
                                <img class="mr-2" src={edit} alt={edit} /> Edit
                              </Link>
                            </td>
                          </tr>
                        );
                      })}
                    {materialData.length === 0 && isLoading === false ? (
                      <tr>
                        <td colSpan="7">
                          <NoDataCard title={tableMessage} />
                        </td>
                      </tr>
                    ) : (
                      ''
                    )}
                  </tbody>
                </Table>
              </Col>
            </Row>
            <TableFooter
              totalRecord={totalRecord}
              activePage={activePage}
              tableFilter={tableFilter}
              handlePageChange={handlePageChange}
              handleSearch={handleSearch}
            />
          </Col>
        </Row>
      </Container>
      <Modal
        className="profileEditModel two imgViewModal"
        show={show}
        onHide={handleClose}
        // backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <img
            src={
              imagePreviewPath
                ? imagePreviewPath.startsWith('https://')
                  ? imagePreviewPath
                  : `${AppConfig.FILES_ENDPOINT}${imagePreviewPath}`
                : dummyImage
            }
            onError={(e) => {
              e.target.onerror = null;
              e.target.src = dummyImage;
            }}
            width="100%"
            alt={imagePreviewPath}
          />
        </Modal.Body>
      </Modal>

      {/* Assign Material Modal */}

      <Modal
        show={show_assign}
        onHide={handleCloseAssign}
        className="addCardPopup material_assignModal d-flex align-items-center justify-content-center"
      >
        <span className="modelClose" closeButton onClick={handleCloseAssign}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <Container fluid className="px-0">
            <Row className="no-gutters">
              <Col md={3}>
                <div className="d-flex align-items-center justify-content-center border-bottom border-right py-5">
                  <img
                    height="150"
                    width="100%"
                    src={
                      materialInfoData && materialInfoData.materialImage
                        ? materialInfoData.materialImage.startsWith('https://')
                          ? materialInfoData.materialImage
                          : `${AppConfig.FILES_ENDPOINT}${materialInfoData.materialImage}`
                        : dummyImage
                    }
                    onError={(e) => {
                      e.target.onerror = null;
                      e.target.src = dummyImage;
                    }}
                    alt={materialInfoData && materialInfoData.materialImage}
                  />
                </div>
                <div className="productDetail border-right">
                  <div>
                    <h6 className="mb-50">
                      {materialInfoData && materialInfoData.salesPartNumber}
                    </h6>
                    <p>
                      {materialInfoData && materialInfoData.materialDescription}
                    </p>
                  </div>
                  <div class="table-responsive overFlow_auto">
                    <table>
                      <tr>
                        <td>
                          <span>ITEM ID</span>
                        </td>
                        <td>HAJSSdfdf</td>
                      </tr>
                      <tr>
                        <td>
                          <span>AVAILABLE</span>
                        </td>
                        <td>
                          {materialInfoData && materialInfoData.inStock} units
                        </td>
                      </tr>
                    </table>
                  </div>
                </div>
              </Col>
              <Col md={9} className="p-4">
                <Row>
                  <Col md={5}>
                    <div class="mb-0 form-group searchBar">
                      <input
                        type="text"
                        placeholder="Search"
                        class="form-control"
                        name="search"
                        onChange={handleUserSearch}
                      />
                      <label class="mb-0">
                        <span>Search</span> in all fields
                      </label>
                    </div>
                  </Col>

                  <Col md={12}>
                    <div class="table-responsive scrollBox">
                      <table class="mt-3 table table-hover">
                        {
                          isAssignedUser ==true ? 
                          (<Spinner
                          as="span"
                          animation="border"
                          size="sm"
                          role="status"
                          aria-hidden="true"
                          />) :''
                        }
                        <tbody>
                          <tr class="first-child">
                            <th class="border-0">ID</th>
                            <th class="border-0 minWidth100px">FIRST NAME</th>
                            <th class="border-0 minWidth100px">LAST NAME</th>
                            <th class="border-0 minWidth100px">USER QTY</th>
                            <th class="border-0 text-center minWidth150px">
                              ASSIGN PRODUCT QTY
                            </th>
                            <th class="border-0 minWidth130px"></th>
                          </tr>
                          {userListData &&
                            userListData.map((item, key) => {
                              return (
                                <tr key={key}>
                                  <td>{item.userId}</td>
                                  <td>{item.firstName}</td>
                                  <td>{item.lastName}</td>
                                  <td>{item.quantity}</td>
                                  <td className="text-center">
                                    { (asignProductQty[item._id] && asignProductQty[item._id]!='')  ? 
                                    ( <input
                                      type="number"
                                      min="0"
                                      value = ''
                                      onChange={(e) =>
                                        handleAsignProductQty(
                                          item._id,
                                          e.target.value
                                        )
                                      }
                                    /> ) :
                                    <input
                                        type="number"
                                        min="0"
                                        onChange={(e) =>
                                          handleAsignProductQty(
                                            item._id,
                                            e.target.value
                                          )
                                        }
                                      />
                                    }
                                  </td>
                                  <td className="text-right">
                                    {isAssign === item._id ? (
                                      <Link to="#">Assign Material</Link>
                                    ) : (
                                      <Link
                                        to="#"
                                        onClick={() => assignMaterial(item._id)}
                                      >
                                        Assign Material
                                      </Link>
                                    )}
                                  </td>
                                </tr>
                              );
                            })}
                        </tbody>
                      </table>
                      
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Container>
        </Modal.Body>
      </Modal>
    </>
  );
};

export { MaterialsList };
