import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import popupClose from './../../../assets/img/popupClose.svg';
import Skeleton from 'react-loading-skeleton';

import {
  Form,
  Button,
  Spinner,
  Col,
  Container,
  Row,
  Card,
  Modal,
} from 'react-bootstrap';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { AddUom, EditUom } from '../../components/Rate';
import NoDataCard from '../../components/NoDataCard';
import gobackImg from '../../../assets/img/goback.svg';

const UnitOfMeasure = () => {
  const history = useHistory();
  const [isBtnDisable, setDisable] = useState(false);
  const [uomData, setUomData] = useState();
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const handleDeleteClose = () => setDeleteModalShow(false);
  const [isDeleteBtnDisable, setDeleteBtnDisable] = useState(false);
  const [deleteItemId, setDeleteItemId] = useState(false);
  const [showAddSection, setShowAddSection] = useState(false);
  const [isDefaultLoading, setIsDefaultLoading] = useState(true);
  const [isUpdateBtnDisable, setIsUpdateBtnDisable] = useState('');

  const handleAddSubmit = async (values) => {
    setDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.ADD_UOM.service,
      ApiRoutes.ADD_UOM.url,
      ApiRoutes.ADD_UOM.method,
      ApiRoutes.ADD_UOM.authenticate,
      undefined,
      values
    );
    setDisable(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      getUomList();
      toast.success(response.messages[0]);
    }
  };
  const handleEditSubmit = async (values, id) => {
    setIsUpdateBtnDisable(id);
    setDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.EDIT_UOM.service,
      ApiRoutes.EDIT_UOM.url.replace(':id', id),
      ApiRoutes.EDIT_UOM.method,
      ApiRoutes.EDIT_UOM.authenticate,
      undefined,
      values
    );
    setIsUpdateBtnDisable('');
    setDisable(false);
    toast.dismiss();
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      toast.success(response.messages[0]);
    }
  };

  const getUomList = async () => {
    const response = await FetchFromServer(
      ApiRoutes.UOM_LIST.service,
      ApiRoutes.UOM_LIST.url,
      ApiRoutes.UOM_LIST.method,
      ApiRoutes.UOM_LIST.authenticate,
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setUomData(null);
      setShowAddSection(false);
      setUomData(response.data.data);
    }
  }; // eslint-disable-next-line
  useEffect(async () => {
    setIsDefaultLoading(true);
    await getUomList();
    setIsDefaultLoading(false);
  }, []);
  const handleDelete = async (id) => {
    setDeleteModalShow(true);
    setDeleteItemId(id);
  };

  const deleteUom = async () => {
    setDeleteBtnDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.DELETE_UOM.service,
      ApiRoutes.DELETE_UOM.url.replace(':id', deleteItemId),
      ApiRoutes.DELETE_UOM.method,
      ApiRoutes.DELETE_UOM.authenticate,
      undefined
    );
    setDeleteBtnDisable(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      getUomList();
      setDeleteModalShow(false);
      toast.success(response.messages[0]);
    }
  };
  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link to="/rates" className="btn btn_goback p-0" role="button">
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>
      <Container fluid className="uomPage">
        <Row>
          <Col
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            className="mb-3 mt-4 d-flex justify-content-between position-relative uom_line"
          >
            <div
              className="d-flex align-items-center tabBtn"
              style={{
                background: '#f5f6f9',
                'padding-right': '10px',
                'z-index': '9',
              }}
            >
              <b>Existing UoM</b>
            </div>
            <div className="position-relative" style={{ 'z-index': '9' }}>
              <Button
                variant=""
                className="btn btn-pink px-5"
                onClick={() => setShowAddSection(!showAddSection)}
              >
                New UoM
              </Button>
            </div>
          </Col>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3 mt-4">
            {isDefaultLoading ? (
              <Card>
                <Card.Body>
                  <div className="p-24">
                    <Row className="tab-content">
                      <Col xs={12} sm={6} md={6} lg={4}>
                        <Form.Group>
                          <label className="mb-0">
                            <Skeleton style={{ width: 100 }} />
                          </label>
                          <Skeleton className="form-control" />
                        </Form.Group>
                      </Col>

                      <Col xs={12} sm={6} md={6} lg={4}>
                        <Form.Group>
                          <label className="mb-0">
                            <Skeleton style={{ width: 150 }} />
                          </label>
                          <Skeleton className="form-control" />
                        </Form.Group>
                      </Col>

                      <Col xs={12} sm={6} md={6} lg={4}>
                        <Form.Group>
                          <label className="mb-0">
                            <Skeleton style={{ width: 150 }} />
                          </label>
                          <Skeleton className="form-control" />
                        </Form.Group>
                      </Col>
                      <Col md={12} className="text-left pageButtomBtn">
                        <Skeleton style={{ width: 222, height: 48 }} />
                      </Col>
                    </Row>
                    <Row className="tab-content">
                      <Col xs={12} sm={6} md={6} lg={4}>
                        <Form.Group>
                          <label className="mb-0">
                            <Skeleton style={{ width: 100 }} />
                          </label>
                          <Skeleton className="form-control" />
                        </Form.Group>
                      </Col>

                      <Col xs={12} sm={6} md={6} lg={4}>
                        <Form.Group>
                          <label className="mb-0">
                            <Skeleton style={{ width: 150 }} />
                          </label>
                          <Skeleton className="form-control" />
                        </Form.Group>
                      </Col>

                      <Col xs={12} sm={6} md={6} lg={4}>
                        <Form.Group>
                          <label className="mb-0">
                            <Skeleton style={{ width: 150 }} />
                          </label>
                          <Skeleton className="form-control" />
                        </Form.Group>
                      </Col>
                      <Col md={12} className="text-left pageButtomBtn">
                        <Skeleton style={{ width: 222, height: 48 }} />
                      </Col>
                    </Row>
                  </div>
                </Card.Body>
              </Card>
            ) : (
              ''
            )}

            {showAddSection ? (
              <Card>
                <Card.Body>
                  <div className="p-24">
                    <Form>
                      <AddUom
                        handleAddSubmit={handleAddSubmit}
                        isBtnDisable={isBtnDisable}
                      />
                    </Form>
                  </div>
                </Card.Body>
              </Card>
            ) : (
              ''
            )}

            {uomData &&
              uomData.map((data, i) => {
                return (
                  <Card key={i}>
                    <Card.Body>
                      <div className="p-24">
                        <Form>
                          <EditUom
                            initialValues={{
                              uomName: data.uomName,
                              incrementType: data.incrementType,
                              maximumValue: data.maximumValue,
                            }}
                            id={data._id}
                            handleEditSubmit={handleEditSubmit}
                            handleDelete={handleDelete}
                            isUpdateBtnDisable={isUpdateBtnDisable}
                          />
                        </Form>
                      </div>
                    </Card.Body>
                  </Card>
                );
              })}
            {uomData &&
            uomData.length === 0 &&
            isDefaultLoading === false &&
            showAddSection === false ? (
              <NoDataCard />
            ) : (
              ''
            )}
          </Col>
        </Row>
      </Container>
      <Modal
        className="profileEditModel two"
        show={deleteModalShow}
        onHide={handleDeleteClose}
        backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleDeleteClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <div className="secondSection px-5 pb-3 pt-5 text-center">
            <h3 className="font-weight-bold">
              Are you sure you want to delete this material?
            </h3>
          </div>
          <div className="px-3 pb-5 d-flex align-items-center justify-content-center flex-wrap">
            <Button
              variant="light"
              className="mr-3 px-4 py-2 popupBtn"
              onClick={deleteUom}
              disabled={isDeleteBtnDisable}
            >
              {isDeleteBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Yes, Delete
            </Button>
            <Button
              variant=""
              className="btn-pink px-4 py-2 popupBtn"
              onClick={() => {
                history.push({
                  pathname: '/rates',
                });
              }}
            >
              No, Go Back
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export { UnitOfMeasure };
