import React from 'react';
import { Form } from 'react-bootstrap';

const EmergencyInformation = (props) => {
  const { handleChange, values, errors, touched } = props;
  return (
    <div className="row arrow_Dropdown">
      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">First Name</label>
          <Form.Control
            type="text"
            name={`emergency.0.firstName`}
            onChange={handleChange}
            value={values.emergency ? values.emergency[0].firstName : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.emergency && touched.emergency
              ? errors.emergency[0].firstName
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Last Name</label>
          <Form.Control
            type="text"
            name={`emergency.0.lastName`}
            onChange={handleChange}
            value={values.emergency ? values.emergency[0].lastName : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.emergency && touched.emergency
              ? errors.emergency[0].lastName
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Relationship</label>
          <Form.Control
            className="option-select"
            as="select"
            name={`emergency.0.relationship`}
            onChange={handleChange}
            value={values.emergency ? values.emergency[0].relationship : ''}
          >
            <option hidden value="">
              Select relationship
            </option>
            <option>test 1</option>
            <option>test 2</option>
            <option>test 3</option>
          </Form.Control>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Email</label>
          <Form.Control
            type="text"
            name={`emergency.0.email`}
            onChange={handleChange}
            value={values.emergency ? values.emergency[0].email : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.emergency && touched.emergency
              ? errors.emergency[0].email
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Phone</label>
          <Form.Control
            type="text"
            name={`emergency.0.phoneNumber`}
            onChange={handleChange}
            value={values.emergency ? values.emergency[0].phoneNumber : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.emergency && touched.emergency
              ? errors.emergency[0].phoneNumber
              : null}
          </span>
        </div>
      </div>
    </div>
  );
};

export { EmergencyInformation };
