import React, { useEffect, useState } from 'react';
import { Link,useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import Skeleton from 'react-loading-skeleton';
import { Col, Container, FormGroup, Row, Form } from 'react-bootstrap';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { InnerPageHeader } from '../../components/Rate/';
import TableFooter from '../../components/TableFooter';
import NoDataCard from '../../components/NoDataCard';


const TaskList = () => {
  const [taskData, setTaskData] = useState([]);
  const [scheduleData, setScheduleData] = useState(false);
  const [technologyData, setTechnologyData] = useState(false);
  const [totalRecord, setTotalRecord] = useState(0);
  let activePageStored = localStorage.getItem('tasksActivePage');
  let storedtableFilters = JSON.parse(localStorage.getItem('tableFilter'));
  const [activePage, setActivePage] = useState(1);
  const [isLoading, setIsLoading] = useState(true);
  const [tableMessage, setTableMessage] = useState('No Data Available');
  // const [categoryName, setCategoryName] = useState('');
  const [tableFilter, setTableFilter] = useState({
    search: '',
    schedule: '',
    skip: 0,
    limit: 10,
    sortBy: '',
    order: '',
	technology:''
  });
  useEffect(() => {
	const getScheduleList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.SCHEDULE_LIST.service,
        ApiRoutes.SCHEDULE_LIST.url,
        ApiRoutes.SCHEDULE_LIST.method,
        ApiRoutes.SCHEDULE_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setScheduleData(response.data.data);
      }
    };
    const getTechnologyList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.TECHNOLOGY_LIST.service,
        ApiRoutes.TECHNOLOGY_LIST.url,
        ApiRoutes.TECHNOLOGY_LIST.method,
        ApiRoutes.TECHNOLOGY_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTechnologyData(response.data.data);
      }
    };
	
    getScheduleList();
    getTechnologyList();
	let renderStatus = localStorage.getItem('renderStatus');
	if(renderStatus==1 && activePageStored){
		console.log(parseInt(activePageStored));
		setActivePage(parseInt(activePageStored));
		
		setTableFilter({
			...tableFilter,
			skip: (parseInt(activePageStored) - 1) * tableFilter.limit,
			schedule: storedtableFilters.schedule,
			search: storedtableFilters.search? storedtableFilters.search :'',
			technology: storedtableFilters.technology?storedtableFilters.technology:'',
			order: storedtableFilters.order,
			sortBy: storedtableFilters.sortBy,
		});
		let renderStatus = localStorage.setItem('renderStatus',0);
	}
	
  }, []);
  useEffect(() => {
    const getTaskList = async () => {
	
      setIsLoading(true);
      const response = await FetchFromServer(
        ApiRoutes.TASK_LIST.service,
        ApiRoutes.TASK_LIST.url,
        ApiRoutes.TASK_LIST.method,
        ApiRoutes.TASK_LIST.authenticate,
        tableFilter,
        undefined
      );
      setIsLoading(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTableMessage(response.messages[0]);
        setTaskData(response.data.data);
        setTotalRecord(response.data.totalRecords);
      }
    };
	//localStorage.setItem('tableFilter', JSON.stringify(tableFilter));
    getTaskList();
  }, [tableFilter]);

  useEffect(() => {
    window.scrollTo(0, 0)
  }, []);
  
  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
	  localStorage.setItem('tasksActivePage', pageNumber);
	  localStorage.setItem('tableFilter', JSON.stringify(tableFilter));
    }
  };
  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === 'pageSize') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
      if(storedtableFilters){
        storedtableFilters.skip=0;
	      storedtableFilters.limit=value;
      }
	  
    }
    if (name === 'search') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        search: value
		});
    if(storedtableFilters){
		storedtableFilters.skip=0;
		storedtableFilters.search=value;}
    }
    if (name === 'schedule') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        schedule: value,
      });
      if(storedtableFilters){
	  storedtableFilters.skip=0;
	  storedtableFilters.schedule=value;}
    }
    if (name === 'technology') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        technology: value,
      });
      if(storedtableFilters){
	  storedtableFilters.skip=0;
	  storedtableFilters.technology=value;}
    }
	//console.log('testingdata',storedtableFilters);
	
	localStorage.setItem('tableFilter', JSON.stringify(storedtableFilters));
  };
  
  const redirect = () => {
	localStorage.setItem('renderStatus', 1); 
  }
  
  let categoryName = '';
  let showCategoryName = '';
  let subcategoryName = '';
  let showSubcategoryName = '';
  return (
    <>
      <InnerPageHeader
        activeTab="rates"
        btnText="Add New Task"
        btnLink="/rates/add"
      />
      <Container fluid>
        <Row className="jobHistoryPage inventory mt-4">
          <Col xs={12} sm={6} md={4} lg={3} xl={2}>
            <Form.Control as="select" name="technology" onChange={handleSearch}>
              <option value="">All</option>
              {technologyData &&
                technologyData.map((data, i) => (
                  <option value={data._id} key={i}>
                    {data.technologyName}
                  </option>
                ))}
            </Form.Control>
            <label className="mb-0">
              <span>Filter</span> by job type
            </label>
          </Col>

          <Col xs={12} sm={6} md={4} lg={3} xl={3}>
            <FormGroup className="mb-0">
              <Form.Control as="select" name="schedule" onChange={handleSearch}>
                <option value="">Select Type of Schedule</option>
                {scheduleData &&
                  scheduleData.map((item, key) => {
                    return (
                      <option key={key} value={item._id}>
                        {item.scheduleName}
                      </option>
                    );
                  })}
              </Form.Control>
            </FormGroup>
            <label className="mb-0">
              <span>Filter</span> by Schedule
            </label>
          </Col>

          <Col xs={12} sm={12} md={4} lg={4} xl={3}>
            <FormGroup className="mb-0">
              <Form.Control
                type="text"
                placeholder="Search"
                name="search"
                onChange={handleSearch}
              />
            </FormGroup>
            <label className="mb-0">
              <span>Search</span> in all fields
            </label>
          </Col>

          <Col sm={12}>
            <div className="table-responsive">
              <div className="table mt-3 rateTask_table position-relative">
                <div className="tr bg-transparent">
                  <div className="th">ITEM ID</div>
                  <div className="th">UNIT OF MEASURE</div>
                  <div className="th">APPLICATION</div>
                  <div className="th">RATES($)</div>
                  <div className="th">SCHEDULE</div>
                  <div className="th">BUSINESS HOURS</div>
                  <div className="th"></div>
                </div>
                {taskData &&
                  isLoading === false &&
                  taskData.map((data, id) => {
                    if (categoryName !== data.categoryName) {
                      categoryName = data.categoryName;
                      showCategoryName = data.categoryName;
                    } else {
                      showCategoryName = '';
                    }
                    if (subcategoryName !== data.subcategoryName) {
                      subcategoryName = data.subcategoryName;
                      showSubcategoryName = data.subcategoryName;
                    } else {
                      showSubcategoryName = '';
                    }
                    return (
                      <div
                        key={id}
                        class={`tr position-relative ${
                          showCategoryName && showSubcategoryName
                            ? 'twoCategory'
                            : ''
                        } ${
                          (!showCategoryName && showSubcategoryName) ||
                          (showCategoryName && !showSubcategoryName)
                            ? 'oneCategory'
                            : ''
                        } ${
                          !showCategoryName && !showSubcategoryName
                            ? 'noCategory'
                            : ''
                        }`}
                      >
                        <div className="td">{data.itemId}</div>
                        <div className="td">{data.uomName}</div>
                        <div className="td">{data.technologyName}</div>
                        <div className="td">{data.rates}</div>
                        <div className="td">{data.scheduleName}</div>
                        <div className="td">{data.workHours}</div>
                        <div className="td" onClick={redirect}>
                          <Link to={`/rates/edit/${data._id}`}>Edit Rates</Link>
                        </div>
                        {showSubcategoryName ? (
                          <span
                            className="title"
                            style={{ background: data.colorCode }}
                          >
                            {showSubcategoryName}
                          </span>
                        ) : (
                          ''
                        )}
                        {showCategoryName ? (
                          <span
                            className="title categoryName"
                            style={{ background: data.colorCode }}
                          >
                            {showCategoryName}
                          </span>
                        ) : (
                          ''
                        )}
                        <div className="description">
                          <p class="mb-0">
                            <span>Item Description:</span>
                            {data.description}
                          </p>
                        </div>
                      </div>
                    );
                  })}

                {taskData.length === 0 && isLoading === false ? (
                  <tr>
                    <td colSpan="7">
                      <NoDataCard title={tableMessage} />
                    </td>
                  </tr>
                ) : (
                  ''
                )}

                {isLoading ? (
                  <>
                    <div class="tr position-relative">
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Link to="#">
                          <Skeleton />
                        </Link>
                      </div>
                      <span className="title" style={{ width: 300 }}>
                        <Skeleton />
                      </span>
                      <span
                        className="title categoryName"
                        style={{ width: 300 }}
                      >
                        <Skeleton />
                      </span>
                      <div className="description">
                        <p class="mb-0">
                          <Skeleton />
                        </p>
                      </div>
                    </div>
                    <div class="tr position-relative">
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Link to="#">
                          <Skeleton />
                        </Link>
                      </div>
                      <span className="title" style={{ width: 300 }}>
                        <Skeleton />
                      </span>
                      <div className="description">
                        <p class="mb-0">
                          <Skeleton />
                        </p>
                      </div>
                    </div>
                    <div class="tr position-relative">
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Link to="#">
                          <Skeleton />
                        </Link>
                      </div>
                      <span className="title" style={{ width: 300 }}>
                        <Skeleton />
                      </span>
                      <div className="description">
                        <p class="mb-0">
                          <Skeleton />
                        </p>
                      </div>
                    </div>
                  </>
                ) : (
                  ''
                )}
              </div>
            </div>
          </Col>
        </Row>
        <TableFooter
          totalRecord={totalRecord}
          activePage={activePage}
          tableFilter={tableFilter}
          handlePageChange={handlePageChange}
          handleSearch={handleSearch}
        />
      </Container>
    </>
  );
};

export { TaskList };
