        ApiRoutes.RECEIVER_LIST.service,
        ApiRoutes.RECEIVER_LIST.url,
        ApiRoutes.RECEIVER_LIST.method,
        ApiRoutes.RECEIVER_LIST.authenticate,
        tableFilter,
        undefined
