import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Col, Container, Row, Table, Card } from 'react-bootstrap';

import { InnerPageHeader } from '../../components/Rate';
import TableLoader from '../../components/TableLoader';
import TableFooter from '../../components/TableFooter';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import NotFound from '../../../assets/img/on_data_found.svg';

const TechnologyList = () => {
  const [technologyData, setTechnologyData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [totalRecord, setTotalRecord] = useState(0);
  const [activePage, setActivePage] = useState(1);
  const [tableFilter, setTableFilter] = useState({
    skip: 0,
    limit: 10,
  });

  useEffect(() => {
    const getTaskList = async () => {
      setIsLoading(true);
      const response = await FetchFromServer(
        ApiRoutes.TECHNOLOGY_LIST.service,
        ApiRoutes.TECHNOLOGY_LIST.url,
        ApiRoutes.TECHNOLOGY_LIST.method,
        ApiRoutes.TECHNOLOGY_LIST.authenticate,
        undefined
      );
      setIsLoading(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTechnologyData(response.data.data);
        setTotalRecord(response.data.totalRecords);
      }
    };
    getTaskList();
  }, [tableFilter]);

  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
    }
  };
  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === 'pageSize') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
    }
  };
  return (
    <>
      <InnerPageHeader
        activeTab="technologyApplication"
        btnText="Add New Technology"
        btnLink="/rates/technology/add"
      />
      <Container fluid>
        <Row>
          <Col md={12} className="jobHistoryPage">
            <Table hover responsive className="mt-3">
              <tbody>
                <tr className="first-child">
                  <th className="border-0">Technology</th>
                  <th className="border-0">Description</th>
                  <th className="border-0"></th>
                </tr>
                {technologyData &&
                  isLoading === false &&
                  technologyData.map((data, id) => {
                    return (
                      <tr key={id}>
                        <td>{data.technologyName}</td>
                        <td>{data.technologyDescription}</td>
                        <td className="Width200">
                          <Link to={`/rates/technology/edit/${data._id}`}>
                            Edit Technology
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                {technologyData.length === 0 && isLoading === false ? (
                  <tr>
                    <td colSpan="3">
                      <Card>
                        <Card.Body className="py-3 px-4">
                          <div className="d-flex align-items-center justify-content-center py-5">
                            <div className="position-relative noDataFound">
                              <img src={NotFound} alt={NotFound} />
                              <h6 className="mb-0">No Data Available</h6>
                            </div>
                          </div>
                        </Card.Body>
                      </Card>
                    </td>
                  </tr>
                ) : (
                  ''
                )}
                {isLoading ? <TableLoader cols={3} /> : ''}
              </tbody>
            </Table>
          </Col>
        </Row>
        <TableFooter
          totalRecord={totalRecord}
          activePage={activePage}
          tableFilter={tableFilter}
          handlePageChange={handlePageChange}
          handleSearch={handleSearch}
        />
      </Container>
    </>
  );
};

export { TechnologyList };
