import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../../assets/css/Custom.css';

import Logo from '../../assets/img/logo.svg';
import Logo2 from '../../assets/img/logo-w.svg';
import HomeIcon from '../../assets/img/home.svg';
import UserIcon from '../../assets/img/user.svg';
import WarehouseIcon from '../../assets/img/warehouse.svg';
import RatesIcon from '../../assets/img/rates.svg';
import ReportsIcon from '../../assets/img/reports.svg';
import ChatIcon from '../../assets/img/chat.svg';
import CollapseIcon from '../../assets/img/collapse.svg';
import CollapseIcon2 from '../../assets/img/collapse_right.svg';

const Sidebar = (props) => {
  const {
    collapseSideBar,
    collapseClass,
    mobileScreen,
    toggleClass,
    handleHamburger,
  } = props;
  const history = useHistory();
  let path = history.location.pathname.split('/');
  let segment1 = path[1];
  return (
    <ul
      className={`navbar-nav bg-sidebar-primary sidebar sidebar-dark accordion ${collapseClass} ${mobileScreen} ${toggleClass}`}
      id="accordionSidebar"
      // ref={(el) => {
      //   if (el) {
      //     el.style.setProperty('width', sideBarCss, 'important');
      //   }
      // }}
    >
      <Link
        to=""
        className="sidebar-brand d-flex align-items-start justify-content-start"
      >
        <div className="sidebar-brand-text mx-3">
          <img className="img-responsive" src={Logo} alt={Logo} />
        </div>
        <div className="sidebar-brand-text-2">
          <img className="img-responsive" src={Logo2} alt={Logo2} />
        </div>
      </Link>

      <li
        className={`nav-item ${
          segment1 === 'home' || segment1 === '' ? 'active' : ''
        }`}
      >
        <Link to="/home" className="nav-link">
          <span className="sidebar_iconBox">
            <img src={HomeIcon} alt={HomeIcon} className="sidebar_icon" />
          </span>
          <span>Home</span>
        </Link>
      </li>

      <li className={`nav-item ${segment1 === 'users' ? 'active' : ''}`}>
        <Link to="/users" className="nav-link">
          <span className="sidebar_iconBox">
            <img src={UserIcon} alt={UserIcon} className="sidebar_icon" />
          </span>
          <span>Users</span>
        </Link>
      </li>

      <li
        className={`nav-item ${
          segment1 === 'warehouse' || segment1 === 'material' ? 'active' : ''
        }`}
      >
        <Link to="/warehouse" className="nav-link">
          <span className="sidebar_iconBox">
            <img
              src={WarehouseIcon}
              alt={WarehouseIcon}
              className="sidebar_icon"
            />
          </span>
          <span>Warehouse</span>
        </Link>
      </li>

      <li className={`nav-item ${segment1 === 'rates' ? 'active' : ''}`}>
        <Link to="/rates" className="nav-link">
          <span className="sidebar_iconBox">
            <img src={RatesIcon} alt={RatesIcon} className="sidebar_icon" />
          </span>
          <span>Rates</span>
        </Link>
      </li>

      <li className={`nav-item ${segment1 === 'reports' ? 'active' : ''}`}>
        <Link to="/reports" className="nav-link">
          <span className="sidebar_iconBox">
            <img src={ReportsIcon} alt={ReportsIcon} className="sidebar_icon" />
          </span>
          <span>Reports</span>
        </Link>
      </li>

      <li className={`nav-item ${segment1 === 'chat' ? 'active' : ''}`}>
        <Link to="/chat" className="nav-link">
          <span className="sidebar_iconBox">
            <img src={ChatIcon} alt={ChatIcon} className="sidebar_icon" />
          </span>
          <span>Chat</span>
        </Link>
      </li>

      <div className="text-center d-md-inline mt-5 pt-5 sideBarToggle_mobile d-none">
        <button
          className="rounded-circle border-0"
          id="sidebarToggle"
          onClick={handleHamburger}
        >
          <img
            className="mr-2 leftToggle"
            src={CollapseIcon}
            alt={CollapseIcon}
          />
          <span>Collapse</span>
        </button>
      </div>

      <div className="text-center d-md-inline mt-5 pt-5 sideBarToggle">
        <button
          className="rounded-circle border-0"
          id="sidebarToggle"
          onClick={collapseSideBar}
        >
          <img
            className="mr-2 leftToggle"
            src={CollapseIcon}
            alt={CollapseIcon}
          />
          <img
            className="ml-0 rightToggle"
            src={CollapseIcon2}
            alt={CollapseIcon2}
          />{' '}
          <span>Collapse</span>
        </button>
      </div>
    </ul>
  );
};

export default Sidebar;
