import React from 'react';
import { AppConfig } from "../../../utils/config";
import moment from "moment";
import userAvtar from "../../../assets/img/user_avtar.svg";

const RecentActiveChat = (props) => {
  const {receiverData} = props;
  let messageTime = moment(new Date());
  console.log('receiverData',receiverData);
  return (
    <>
    {receiverData && receiverData.length>0 ? receiverData.map((item, key) => {
      return(
    <div className="d-flex align-items-center pt-3 pb-3 border-bottom-custom2">
      <div className= {item.socketActive ? "mr-3 activeStatus online"  : "mr-3 activeStatus offline"} >
        <img src={item && item.profilePicture
              ? item.profilePicture.startsWith("https://")
                ? item.profilePicture
                : `${AppConfig.FILES_ENDPOINT}${item.profilePicture}`
              : userAvtar
          }
          onError={(e) => {
            e.target.onerror = null;
            e.target.src = userAvtar;
          }} />
      </div>
      <div className="conversation">
        <p className="m-0 time">{item.firstName && item.firstName !== undefined? item.firstName +" " : "" + item.lastName && item.lastName !== undefined ? item.lastName :""}</p>
        <h6 className="m-0 newMsg">
        {item.isTyping ? (<>Typing...</>
        ) : item.chats.length>0 && item.chats[0].message !== "" ? (
            <p className="chatItems mb-0">{item.chats[0].message}</p>
          ) : (
            <p className="">{item.chats.length>0 && item.chats[0].originalFileName ? item.chats[0].originalFileName:'attachment'}</p>
          )}
          
            
          
        </h6>
      </div>
      <div className="timeBox">
        <p className="m-0 time">
          { item.chats.length>0 ?
            messageTime.diff(item.chats[0].time, "days") === 0
              ? moment(item.chats[0].time).format("hh:mm A")
              : messageTime.diff(item.chats[0].time, "days") < 7 &&
                messageTime.diff(item.chats[0].time, "days") > 1
              ? moment(item.chats[0].time).format("dddd hh:mm A")
              : moment(item.chats[0].time).format("DD MMMM YYYY hh:mm A") :""}
          </p>
      </div>
      <div className="msgStatus">
        {/*<span className="online"></span>*/}
      </div>
    </div>
      )
    }) : ""
    } 
    </>
  );
};

export { RecentActiveChat };
