import React, { useEffect, useState } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import { Form, Col, Container, FormGroup, Row, Table } from "react-bootstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";

import InnerPageHeader from "../../components/User/InnerPageHeader";
import TableFooter from "../../components/TableFooter";
import NoDataCard from "../../components/NoDataCard";
import { TableLoader } from "../../components/loader/Resusable/Table";

import { FetchFromServer } from "../../../utils/helpers";
import { ApiRoutes } from "../../../utils/config";

const JobsHistory = (props) => {
  const { setUserName, setUserId } = props;
  const { id } = useParams();
  const [userData, setUserData] = useState();
  const [jobData, setJobData] = useState([]);
  const [totalRecord, setTotalRecord] = useState(0);
  const [activePage, setActivePage] = useState(1);
  const [isLoading, setIsLoading] = useState(true);
  const [technologyData, setTechnologyData] = useState(false);
  const [tableMessage, setTableMessage] = useState("No Data Available");
  const [tableFilter, setTableFilter] = useState({
    userId: id,
    search: "",
    technology: "",
    skip: 0,
    limit: 10,
    sortBy: "",
    order: "",
  });
  const history = useHistory();

  const getJobList = async () => {
    setIsLoading(true);
    const response = await FetchFromServer(
      ApiRoutes.JOB_LIST.service,
      ApiRoutes.JOB_LIST.url,
      ApiRoutes.JOB_LIST.method,
      ApiRoutes.JOB_LIST.authenticate,
      tableFilter,
      undefined
    );
    setIsLoading(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setTableMessage(response.messages[0]);
      setJobData(response.data.data);
      setTotalRecord(response.data.totalRecords);
    }
  };
  useEffect(() => {
    // setUserName('');
    const getUser = async () => {
      const response = await FetchFromServer(
        ApiRoutes.USER_INFO.service,
        ApiRoutes.USER_INFO.url.replace(":id", id),
        ApiRoutes.USER_INFO.method,
        ApiRoutes.USER_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: "/users",
        });
      } else {
        setUserData(response.data.data);
        setUserName(
          response.data.data.firstName + " " + response.data.data.lastName
        );
        setUserId(response.data.data._id);
      }
    };
    const getTechnologyList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.TECHNOLOGY_LIST.service,
        ApiRoutes.TECHNOLOGY_LIST.url,
        ApiRoutes.TECHNOLOGY_LIST.method,
        ApiRoutes.TECHNOLOGY_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTechnologyData(response.data.data);
      }
    };
    getUser();
    getTechnologyList();

    // eslint-disable-next-line
  }, []);
  useEffect(() => {
    getJobList();
    // eslint-disable-next-line
  }, [tableFilter]);

  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
    }
  };
  const handleSearch = async (e, date = "") => {
    let name, value;
    if (e === "date") {
      name = "date";
      value = date;
      console.log("date", date);
    } else {
      name = e.target.name;
      value = e.target.value;
    }
    setActivePage(1);
    if (name === "pageSize") {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
    }
    if (name === "search") {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        search: value,
      });
    }
    if (name === "technology") {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        technology: value,
      });
    }
    if (name === "date") {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        date: value,
      });
    }
  };
  return (
    <div>
      <InnerPageHeader
        id={id}
        email={userData ? userData.email : ""}
        mobile={userData ? userData.phoneNumber : ""}
        activeTab="jobsHistory"
      />
      <Container fluid className="jobHistoryPage inventory">
        <Row>
          <Col lg={12} className="mb-3 mt-4">
            <Row>
              <Col xs={12} sm={6} md={4} lg={3} xl={2}>
                <Form.Control
                  as="select"
                  name="technology"
                  onChange={handleSearch}
                >
                  <option value="">All</option>
                  {technologyData &&
                    technologyData.map((data, i) => (
                      <option value={data._id} key={i}>
                        {data.technologyName}
                      </option>
                    ))}
                </Form.Control>
                <label className="mb-0">
                  <span>Filter</span> by job type
                </label>
              </Col>

              <Col xs={12} sm={6} md={4} lg={3} xl={3}>
                <FormGroup className="mb-0 position-relative dateIcon">
                  {/* <DatePicker
                    wrapperClassName="form-control"
                    className="form-control"
                    placeholderText="Select Dates"
                    autoComplete={false}
                    dateFormat="dd/MM/yyyy"
                    onChange={(date) => {
                      if (date)
                        handleSearch('date', moment(date).format('YYYY-MM-DD'));
                      else handleSearch(date, '');
                    }}
                    selected={
                      tableFilter && tableFilter.date
                        ? new Date(
                            moment(tableFilter.date).format('YYYY-MM-DD')
                          )
                        : ''
                    }
                  /> */}
                  <DatePicker
                    wrapperClassName="form-control"
                    className="form-control"
                    placeholderText="Select Dates"
                    autoComplete={false}
                    dateFormat="dd/MM/yyyy"
                    onChange={(date) => {
                      if (date)
                        handleSearch("date", moment(date).format("YYYY-MM-DD"));
                      else handleSearch(date, "");
                    }}
                    selected={
                      tableFilter && tableFilter.date
                        ? new Date(
                            moment(tableFilter.date).format("YYYY-MM-DD")
                          )
                        : ""
                    }
                    peekNextMonth
                    showMonthDropdown
                    showYearDropdown
                    dropdownMode="select"
                    /*highlightDates={[
                      new Date(
                        moment('2021-07-12').format('YYYY-MM-DD')
                      ),
                      new Date(
                        moment('2021-07-21').format('YYYY-MM-DD')
                      )
                    ]}*/
                    /*excludeDates={[
                      new Date(
                        moment('2021-07-12').format('YYYY-MM-DD')
                      ),
                      new Date(
                        moment('2021-07-21').format('YYYY-MM-DD')
                      )
                    ]}*/
                  />
                </FormGroup>
                <label className="mb-0">
                  <span>Filter</span> by date
                </label>
              </Col>

              <Col xs={12} sm={12} md={4} lg={4} xl={3}>
                <FormGroup className="mb-0">
                  <input
                    type="text"
                    name="search"
                    placeholder="Search"
                    className="form-control"
                    onChange={handleSearch}
                  />
                </FormGroup>
                <label className="mb-0">
                  <span>Search</span> in all fields
                </label>
              </Col>

              <Col md={12}>
                <div className="table-responsive">
                  {isLoading ? (
                    <TableLoader columns={6} rows={10} />
                  ) : (
                    <Table hover className="mt-3">
                      <tbody>
                        <tr className="first-child">
                          <th className="border-0">JOB ADDRESS</th>
                          <th className="border-0">Task ID</th>
                          <th className="border-0">PARENT ID</th>
                          <th className="border-0">JOB TYPE</th>
                          <th className="border-0">Status</th>
                          <th className="border-0">DATE CLosed</th>
                          <th className="border-0"></th>
                        </tr>
                        {jobData &&
                          isLoading === false &&
                          jobData.map((item, key) => {
                            return (
                              <tr key={key}>
                                <td>{item.jobAddress}</td>
                                <td>{item.taskId}</td>
                                <td>{item.parentId}</td>
                                <td>{item.technologyName}</td>
                                <td>{item.status}</td>
                                <td>25/03/2018</td>
                                <td>
                                  <Link to={`/report/${item._id}`}>
                                    job report
                                  </Link>
                                </td>
                              </tr>
                            );
                          })}
                        {jobData.length === 0 && isLoading === false ? (
                          <tr>
                            <td colSpan="7">
                              <NoDataCard title={tableMessage} />
                            </td>
                          </tr>
                        ) : (
                          ""
                        )}
                      </tbody>
                    </Table>
                  )}
                </div>
              </Col>
            </Row>
            <TableFooter
              totalRecord={totalRecord}
              activePage={activePage}
              tableFilter={tableFilter}
              handlePageChange={handlePageChange}
              handleSearch={handleSearch}
            />
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export { JobsHistory };
