import React, { useEffect, useState, useRef } from 'react';
import { Link, useParams, useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';
import '../../../assets/css/Custom.css';

import popupClose from './../../../assets/img/popupClose.svg';
import gobackImg from '../../../assets/img/goback.svg';
import moment from 'moment';

import {
  Container,
  Row,
  Col,
  Card,
  Button,
  Tab,
  Form,
  Nav,
  Spinner,
  Modal,
} from 'react-bootstrap';

import { ToolInformation } from '../../components/User';

import { AddToolSchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes, AppConfig } from '../../../utils/config';

const EditTool = () => {
  const form = useRef(null);
  const { id } = useParams();
  const history = useHistory();
  const [toolData, setToolData] = useState();
  const [isBtnDisable, setDisable] = useState(false);
  const [isDeleteBtnDisable, setDeleteBtnDisable] = useState(false);
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const handleDeleteClose = () => setDeleteModalShow(false);

  const { handleSubmit, handleChange, values, errors, setFieldValue, touched } =
    useFormik({
      initialValues: {},
      validationSchema: AddToolSchema,
      onSubmit: async (values) => {
        setDisable(true);
        const formData = new FormData(form.current);
        formData.append('certification', values.certification);
        const response = await FetchFromServer(
          ApiRoutes.EDIT_TOOL.service,
          ApiRoutes.EDIT_TOOL.url.replace(':id', id),
          ApiRoutes.EDIT_TOOL.method,
          ApiRoutes.EDIT_TOOL.authenticate,
          undefined,
          formData
        );
        setDisable(false);
        if (response && response.isError) {
          toast.error(response.messages[0]);
        } else {
          history.push({
            pathname: '/users/tools/' + toolData.userId,
          });
        }
      },
    });

  useEffect(() => {
    async function getToolInfo() {
      const response = await FetchFromServer(
        ApiRoutes.TOOL_INFO.service,
        ApiRoutes.TOOL_INFO.url.replace(':id', id),
        ApiRoutes.TOOL_INFO.method,
        ApiRoutes.TOOL_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: '/users',
        });
      } else {
        let data = response.data.data;
        setToolData(data);
        values.manufacturer = data.manufacturer;
        values.description = data.description;
        values.serialNumber = data.serialNumber;
        values.lastCalibration = data.lastCalibration
          ? moment(data.lastCalibration).format('YYYY-MM-DD')
          : '';
        values.nextCalibration = data.nextCalibration
          ? moment(data.nextCalibration).format('YYYY-MM-DD')
          : '';
        values.profilePicture = data.toolPicture
          ? `${AppConfig.FILES_ENDPOINT}${data.toolPicture}`
          : '';
        values.certification = data.certification;
        values.naLastCalibration = data.naLastCalibration;
        values.naNextCalibration = data.naNextCalibration;
        setFieldValue({ firstName: '' });
      }
    }
    getToolInfo();
    // eslint-disable-next-line
  }, []);

  const deleteUser = async () => {
    setDeleteBtnDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.DELETE_TOOL.service,
      ApiRoutes.DELETE_TOOL.url.replace(':id', id),
      ApiRoutes.DELETE_TOOL.method,
      ApiRoutes.DELETE_TOOL.authenticate,
      undefined
    );
    setDeleteBtnDisable(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      toast.error(response.messages[0]);
      history.push({
        pathname: '/users/tools/' + toolData.userId,
      });
    }
  };

  const removeFile = async () => {
    // toolData.certification = '';
    // setToolData(toolData);
    setFieldValue('certification', '');
  };
  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link
          to={`/users/tools/${toolData ? toolData.userId : ''}`}
          className="btn btn_goback p-0"
          role="button"
        >
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>
      <Container fluid className="addNewUserPage">
        <Row>
          <Col
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            className="mb-3 mt-4 addToolPage"
          >
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form
                    ref={form}
                    enablereinitialize="true"
                    onSubmit={handleSubmit}
                  >
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="profile"
                    >
                      <Nav as="ul" className="nav-pills mb-3 position-relative">
                        <Nav.Item as="li">
                          <Nav.Link eventKey="profile">
                            Tool Information
                          </Nav.Link>
                        </Nav.Item>
                      </Nav>
                      <Tab.Content>
                        <Tab.Pane eventKey="profile">
                          <ToolInformation
                            handleChange={handleChange}
                            errors={errors}
                            values={values}
                            touched={touched}
                            removeFile={removeFile}
                            idAdd={false}
                            handleSubmit={handleSubmit}
                            isBtnDisable={isBtnDisable}
                            setDeleteModalShow={setDeleteModalShow}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
      <Modal
        className="profileEditModel two"
        show={deleteModalShow}
        onHide={handleDeleteClose}
        backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleDeleteClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <div className="secondSection px-5 pb-3 pt-5 text-center">
            <h3 className="font-weight-bold">
              Are you sure you want to delete this tool?
            </h3>
          </div>
          <div className="px-3 pb-5 d-flex align-items-center justify-content-center flex-wrap">
            <Button
              variant="light"
              className="mr-3 px-4 py-2 popupBtn"
              onClick={deleteUser}
              disabled={isDeleteBtnDisable}
            >
              {isDeleteBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Yes, Delete
            </Button>
            <Button
              variant=""
              className="btn-pink px-4 py-2 popupBtn"
              onClick={() => {
                history.push({
                  pathname: '/users/tools/' + toolData.userId,
                });
              }}
            >
              No, Go Back
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export { EditTool };
