import React, { useState, useRef, useEffect } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';

import {
  Nav,
  Tab,
  Form,
  Button,
  Spinner,
  Col,
  Modal,
  Container,
  Row,
  Card,
} from 'react-bootstrap';
import gobackImg from '../../../assets/img/goback.svg';
import popupClose from './../../../assets/img/popupClose.svg';

import { AddMaterialSchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { AddMaterial as AddMaterialComponent } from '../../components/Warehouse';

const EditMaterial = () => {
  const form = useRef(null);
  const { id } = useParams();
  const history = useHistory();
  // const [materialData, setMaterialData] = useState();
  const [isBtnDisable, setDisable] = useState(false);
  const [isDeleteBtnDisable, setDeleteBtnDisable] = useState(false);
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const handleDeleteClose = () => setDeleteModalShow(false);
  const [uomData, setUomData] = useState(false);

  const { handleSubmit, handleChange, values, errors, setFieldValue, touched } =
    useFormik({
      initialValues: {},
      validationSchema: AddMaterialSchema,
      onSubmit: async (values) => {
        setDisable(true);
        const formData = new FormData(form.current);
        const response = await FetchFromServer(
          ApiRoutes.EDIT_MATERIAL.service,
          ApiRoutes.EDIT_MATERIAL.url.replace(':id', id),
          ApiRoutes.EDIT_MATERIAL.method,
          ApiRoutes.EDIT_MATERIAL.authenticate,
          undefined,
          formData
        );
        setDisable(false);
        if (response && response.isError) {
          toast.error(response.messages[0]);
        } else {
          history.push({
            pathname: '/warehouse',
          });
        }
      },
    });

  useEffect(() => {
    const getUomList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.UOM_LIST.service,
        ApiRoutes.UOM_LIST.url,
        ApiRoutes.UOM_LIST.method,
        ApiRoutes.UOM_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setUomData(response.data.data);
      }
    };
    const getMaterial = async () => {
      const response = await FetchFromServer(
        ApiRoutes.MATERIAL_INFO.service,
        ApiRoutes.MATERIAL_INFO.url.replace(':id', id),
        ApiRoutes.MATERIAL_INFO.method,
        ApiRoutes.MATERIAL_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: '/warehouse',
        });
      } else {
        let data = response.data.data;
        values.materialImage = data.materialImage;
        values.salesPartNumber = data.salesPartNumber;
        values.materialDescription = data.materialDescription;
        values.inStock = data.inStock;
        values.uom = data.uom ? data.uom : '';
        values.lowMarginAdmin = data.lowMarginAdmin;
        values.averageMarginAdmin = data.averageMarginAdmin;
        values.lowMarginUser = data.lowMarginUser;
        // setMaterialData(data);
        setFieldValue({ materialImage: '' });
      }
    };
    getUomList();
    getMaterial();
    // eslint-disable-next-line
  }, []);

  const deleteMaterial = async () => {
    setDeleteBtnDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.DELETE_MATERIAL.service,
      ApiRoutes.DELETE_MATERIAL.url.replace(':id', id),
      ApiRoutes.DELETE_MATERIAL.method,
      ApiRoutes.DELETE_MATERIAL.authenticate,
      undefined
    );
    setDeleteBtnDisable(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      toast.error(response.messages[0]);
      history.push({
        pathname: '/warehouse',
      });
    }
  };

  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link to="/warehouse" className="btn btn_goback p-0" role="button">
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>

      <Container fluid className="addNewUserPage">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3 mt-4">
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form ref={form} onSubmit={handleSubmit}>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="materialInfo"
                    >
                      <Nav as="ul" className="nav-pills mb-3 position-relative">
                        <Nav.Item as="li">
                          <Nav.Link eventKey="materialInfo">
                            New Material Information
                          </Nav.Link>
                        </Nav.Item>
                      </Nav>
                      <Tab.Content>
                        <Tab.Pane eventKey="materialInfo">
                          <AddMaterialComponent
                            handleChange={handleChange}
                            errors={errors}
                            values={values}
                            touched={touched}
                            uomData={uomData}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12} className="mb-5 text-right pageButtomBtn">
            <Button
              variant=""
              className="px-5 py-2 rounded addcard form_btn"
              style={{ background: '#E5EAEE', fontSize: '14px' }}
              onClick={() => setDeleteModalShow(true)}
            >
              Delete Material
            </Button>
            <Button
              variant=""
              className="px-5 py-2 rounded btn-pink ml-3 form_btn"
              style={{ fontSize: '14px' }}
              onClick={handleSubmit}
              disabled={isBtnDisable}
            >
              {isBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Update Material
            </Button>
          </Col>
        </Row>
      </Container>
      <Modal
        className="profileEditModel two"
        show={deleteModalShow}
        onHide={handleDeleteClose}
        backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleDeleteClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <div className="secondSection px-5 pb-3 pt-5 text-center">
            <h3 className="font-weight-bold">
              Are you sure you want to delete this material?
            </h3>
          </div>
          <div className="px-3 pb-5 d-flex align-items-center justify-content-center flex-wrap">
            <Button
              variant="light"
              className="mr-3 px-4 py-2 popupBtn"
              onClick={deleteMaterial}
              disabled={isDeleteBtnDisable}
            >
              {isDeleteBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Yes, Delete
            </Button>
            <Button
              variant=""
              className="btn-pink px-4 py-2 popupBtn"
              onClick={() => {
                history.push({
                  pathname: '/warehouse',
                });
              }}
            >
              No, Go Back
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export { EditMaterial };
