import React, { useState, useEffect } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';

import {
  Nav,
  Tab,
  Form,
  Button,
  Spinner,
  Col,
  Container,
  Row,
  Card,
  Modal,
} from 'react-bootstrap';
import gobackImg from '../../../assets/img/goback.svg';
import popupClose from './../../../assets/img/popupClose.svg';

import { AddTaskSchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { TaskInformation } from '../../components/Rate';

const EditTask = () => {
  const { id } = useParams();
  const history = useHistory();
  const [isBtnDisable, setDisable] = useState(false);
  const [technologyData, setTechnologyData] = useState(false);
  const [uomData, setUomData] = useState(false);
  const [scheduleData, setScheduleData] = useState(false);
  const [categoryData, setCategoryData] = useState(false);
  const [businessRuleData, setBusinessRuleData] = useState(false);
  const [subcategoryData, setSubcategoryData] = useState(false);
  const [deleteModalShow, setDeleteModalShow] = useState(false);
  const handleDeleteClose = () => setDeleteModalShow(false);
  const [isDeleteBtnDisable, setDeleteBtnDisable] = useState(false);
  const { handleSubmit, handleChange, values, errors, setFieldValue, touched } =
    useFormik({
      initialValues: {},
      validationSchema: AddTaskSchema,
      onSubmit: async (values) => {
        setDisable(true);
        const response = await FetchFromServer(
          ApiRoutes.EDIT_TASK.service,
          ApiRoutes.EDIT_TASK.url.replace(':id', id),
          ApiRoutes.EDIT_TASK.method,
          ApiRoutes.EDIT_TASK.authenticate,
          undefined,
          values
        );
        setDisable(false);
        if (response && response.isError) {
          toast.error(response.messages[0]);
        } else {
          history.push({
            pathname: '/rates',
          });
        }
      },
    });

  const getCategoryList = async (paramData) => {
    const response = await FetchFromServer(
      ApiRoutes.CATEGORY_LIST.service,
      ApiRoutes.CATEGORY_LIST.url,
      ApiRoutes.CATEGORY_LIST.method,
      ApiRoutes.CATEGORY_LIST.authenticate,
      paramData,
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setCategoryData(response.data.data);
    }
  };
  const getBusinessRuleList = async (paramData) => {
    const response = await FetchFromServer(
      ApiRoutes.BUSINESS_RULE_ALL.service,
      ApiRoutes.BUSINESS_RULE_ALL.url,
      ApiRoutes.BUSINESS_RULE_ALL.method,
      ApiRoutes.BUSINESS_RULE_ALL.authenticate,
      paramData,
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setBusinessRuleData(response.data.data);
    }
  };
  const getSubcategoryList = async (paramData) => {
    const response = await FetchFromServer(
      ApiRoutes.SUBCATEGORY_LIST.service,
      ApiRoutes.SUBCATEGORY_LIST.url,
      ApiRoutes.SUBCATEGORY_LIST.method,
      ApiRoutes.SUBCATEGORY_LIST.authenticate,
      paramData,
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setSubcategoryData(response.data.data);
    }
  };
  const getTechnologyList = async (schedule = '') => {
    const response = await FetchFromServer(
      ApiRoutes.TECHNOLOGY_LIST.service,
      ApiRoutes.TECHNOLOGY_LIST.url,
      ApiRoutes.TECHNOLOGY_LIST.method,
      ApiRoutes.TECHNOLOGY_LIST.authenticate,
      { schedule: schedule },
      undefined
    );
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setTechnologyData(response.data.data);
    }
  };

  const deleteTask = async () => {
    setDeleteBtnDisable(true);
    const response = await FetchFromServer(
      ApiRoutes.DELETE_TASK.service,
      ApiRoutes.DELETE_TASK.url.replace(':id', id),
      ApiRoutes.DELETE_TASK.method,
      ApiRoutes.DELETE_TASK.authenticate,
      undefined
    );
    setDeleteBtnDisable(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      toast.error(response.messages[0]);
      history.push({
        pathname: '/rates',
      });
    }
  };

  useEffect(() => {
    const getUomList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.UOM_LIST.service,
        ApiRoutes.UOM_LIST.url,
        ApiRoutes.UOM_LIST.method,
        ApiRoutes.UOM_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setUomData(response.data.data);
      }
    };
    const getScheduleList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.SCHEDULE_LIST.service,
        ApiRoutes.SCHEDULE_LIST.url,
        ApiRoutes.SCHEDULE_LIST.method,
        ApiRoutes.SCHEDULE_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setScheduleData(response.data.data);
      }
    };
    const getTaskInfo = async () => {
      const response = await FetchFromServer(
        ApiRoutes.TASK_INFO.service,
        ApiRoutes.TASK_INFO.url.replace(':id', id),
        ApiRoutes.TASK_INFO.method,
        ApiRoutes.TASK_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: '/warehouse',
        });
      } else {
        let data = response.data.data;
        await getCategoryList({
          technology: data.technology,
          schedule: data.schedule,
        });
        await getTechnologyList(data.schedule);
        getSubcategoryList({
          category: data.category,
        });
        getBusinessRuleList({
          technology: data.technology,
          schedule: data.schedule,
        });
        for (let x in data) {
          values[x] = data[x];
        }
        values.uom = data.uom ? data.uom : '';
        setFieldValue({ itemId: '' });
      }
    };
    // getTechnologyList();
    getUomList();
    getScheduleList();
    getTaskInfo();
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link to="/rates" className="btn btn_goback p-0" role="button">
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>

      <Container fluid className="addNewUserPage">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3 mt-4">
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form onSubmit={handleSubmit}>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="profile"
                    >
                      <Nav as="ul" className="nav-pills mb-3 position-relative">
                        <Nav.Item as="li">
                          <Nav.Link eventKey="profile">
                            Edit Task Information
                          </Nav.Link>
                        </Nav.Item>
                      </Nav>
                      <Tab.Content>
                        <Tab.Pane eventKey="profile">
                          <TaskInformation
                            handleChange={handleChange}
                            technologyData={technologyData}
                            uomData={uomData}
                            scheduleData={scheduleData}
                            categoryData={categoryData}
                            subcategoryData={subcategoryData}
                            businessRuleData={businessRuleData}
                            getCategoryList={getCategoryList}
                            getSubcategoryList={getSubcategoryList}
                            getBusinessRuleList={getBusinessRuleList}
                            getTechnologyList={getTechnologyList}
                            errors={errors}
                            values={values}
                            touched={touched}
                            setFieldValue={setFieldValue}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12} className="mb-5 text-right pageButtomBtn">
            <Button
              variant=""
              className="px-5 py-2 rounded addcard form_btn"
              style={{ background: '#E5EAEE', fontSize: '14px' }}
              onClick={() => setDeleteModalShow(true)}
            >
              Delete Task
            </Button>
            <Button
              variant=""
              className="px-5 py-2 rounded btn-pink ml-3 form_btn"
              style={{ fontSize: '14px' }}
              onClick={handleSubmit}
              disabled={isBtnDisable}
            >
              {isBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Save Updates
            </Button>
          </Col>
        </Row>
      </Container>
      <Modal
        className="profileEditModel two"
        show={deleteModalShow}
        onHide={handleDeleteClose}
        backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleDeleteClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <div className="secondSection px-5 pb-3 pt-5 text-center">
            <h3 className="font-weight-bold">
              Are you sure you want to delete this task?
            </h3>
          </div>
          <div className="px-3 pb-5 d-flex align-items-center justify-content-center flex-wrap">
            <Button
              variant="light"
              className="mr-3 px-4 py-2 popupBtn"
              onClick={deleteTask}
              disabled={isDeleteBtnDisable}
            >
              {isDeleteBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Yes, Delete
            </Button>
            <Button
              variant=""
              className="btn-pink px-4 py-2 popupBtn"
              onClick={() => {
                history.push({
                  pathname: '/rates',
                });
              }}
            >
              No, Go Back
            </Button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
};

export { EditTask };
