import React, { useEffect, useState } from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import { toast } from "react-toastify";

import {
  Card,
  Col,
  Container,
  Row,
  Button,
  Modal,
  Table,
  Carousel,
  Accordion
} from "react-bootstrap";

import "bootstrap/dist/css/bootstrap.min.css";
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';
import TableFooter from '../../components/TableFooter';
import moment from "moment";


const Notification = () => { 

  const [notificationsList, setNotificationsList] = useState([]);
  const [tableFilter, setTableFilter] = useState({
    search: '',
    skip: 0,
    limit: 10,
  });
  const [totalRecord, setTotalRecord] = useState(0);
  const [activePage, setActivePage] = useState(1);

  useEffect(() => {
    getNotificationsList();
  }, []);

  useEffect(() => {
    getNotificationsList();
  }, [tableFilter]);


  const handleUserSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    if (name === 'search') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        search: value,
      });
    }
  };

  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
	  }
  };

  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === 'pageSize') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
    }
  }

  const getNotificationsList = async (id) => {
      const response = await FetchFromServer(
        ApiRoutes.NOTIFICATIONS_LIST.service,
        ApiRoutes.NOTIFICATIONS_LIST.url,
        ApiRoutes.NOTIFICATIONS_LIST.method,
        ApiRoutes.NOTIFICATIONS_LIST.authenticate,
        tableFilter,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setNotificationsList(response.data.data);
        setTotalRecord(response.data.totalRecords);
      }
  };


  return (
    <>
     
     <Container fluid className="notificationPage">
        <Row className="mt-4">

          <Col sm={12} md={12} lg={12} xl={12}>
            <div className="notifyCard">
                <div className="headerCard mb-4 pb-1">
                  <p>Manage Notification</p>
                  {/*<p>Manage notification from here</p>*/}
                </div>

                <div className="d-flex justify-content-between">
                  <div className="d-flex sorting">
                    {/*<p>Show</p>
                    <select>
                      <option>10</option>
                      <option>20</option>
                      <option>30</option>
                    </select>
                    <p>entries</p>*/}
                  </div>
                  <div className="d-flex align-items-center searchBox">
                    <p className="mb-0">Search:</p>
                    <input type="text" name="search" placeholder="Message"
                        onChange={handleUserSearch}/>
                  </div>
                </div>


                <Table className="mt-3">
                  <tr>
                    <th>Title</th>
                    <th>Message</th>
                    <th>Date</th>
                  </tr>
                  {notificationsList && notificationsList.map((item, key) => {
                  return (
                    <tr key={key}>
                      <td>{item.title}</td>
                      <td><span className="overflowText">{item.message}</span></td>
                      <td>{item.createdAt ? moment(item.createdAt).format("DD MMMM YYYY hh:mm A") : ""}</td>
                    </tr>
                  );
                })}
                </Table>
                <p className="sortingText mt-2">showing rows 1 to 10 of 11</p>
                <TableFooter
                totalRecord={totalRecord}
                activePage={activePage}
                tableFilter={tableFilter}
                handlePageChange={handlePageChange}
                handleSearch={handleSearch}
                
              />


            </div>
            
          </Col>
          
        </Row>
      </Container>

    </>
  );
};

export { Notification };
