import React from 'react';
import { Form, Row, Col } from 'react-bootstrap';

const SubcategoryInformation = (props) => {
  const { handleChange, errors, values, technologyData, touched } = props;
  return (
    <Row>
      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group>
          <label className="mb-0">Technology Application</label>
          <Form.Control
            as="select"
            name="technology"
            onChange={handleChange}
            value={values.technology ? values.technology : ''}
          >
            <option hidden value="">
              Select job type
            </option>
            {technologyData &&
              technologyData.map((data, i) => (
                <option value={data._id} key={i}>
                  {data.technologyName}
                </option>
              ))}
          </Form.Control>
          <span className="errorMsg">
            {' '}
            {errors.technology && touched.technology ? errors.technology : null}
          </span>
        </Form.Group>
      </Col>

      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group>
          <label className="mb-0">Sub Category</label>
          <Form.Control
            type="text"
            name="categoryName"
            onChange={handleChange}
            value={values.categoryName ? values.categoryName : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.categoryName && touched.categoryName
              ? errors.categoryName
              : null}
          </span>
        </Form.Group>
      </Col>
    </Row>
  );
};

export { SubcategoryInformation };
