import React, { useEffect, useState } from "react";
import { useParams, useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import InnerPageHeader from "../../components/User/InnerPageHeader";
import "../../../assets/css/Custom.css";

import Revenue from "../../../assets/img/revenue.svg";
import Scheduled_job from "../../../assets/img/scheduled_job.svg";
import Complete_jobs from "../../../assets/img/complete_jobs.svg";
import userAvtar from "../../../assets/img/user_avtar.svg";
import NotFound from "../../../assets/img/on_data_found.svg";
import { Link } from "react-router-dom";
import { Container, Row, Col, Card, Table } from "react-bootstrap";
import { RecentActivity } from "../../components/Home";
import OpenJobs from "../../components/User/OpenJob";
import JobLoader from "./JobLoader";

import ClosedJob from "../../components/User/ClosedJob";
import NoDataCard from "../../components/NoDataCard";
import topGeenArrow from "../../../assets/img/green_arrowTop.svg";
import downloadArrow from "../../../assets/img/downloadArrow.svg";

import { FetchFromServer } from "../../../utils/helpers";
import { ApiRoutes, AppConfig } from "../../../utils/config";
import useSocket from "../../../utils/socket/soket";
import moment from "moment";
import LogIn from "../../../assets/img/recentActivity_login.svg";
import LogOut from "../../../assets/img/recentActivity_logout.svg";
import Report from "../../../assets/img/recentActivity_report.svg";
import OpenJob from "../../../assets/img/recentActivity_openJob.svg";
import Complete from "../../../assets/img/recentActivity_completeJob.svg";
import Incomplete from "../../../assets/img/recentActivity_IncompleteJob.svg";
import Skeleton from "react-loading-skeleton";

// import NotFound from "../../../assets/img/on_data_found.svg";

const activityDataArray = [];
const UserOverview = (props) => {
  const { setUserName, setUserId } = props;
  const { id } = useParams();
  const [userData, setUserData] = useState();
  const [openJobData, setOpenJobData] = useState([]);
  const [closeJobData, setCloseJobData] = useState([]);
  const [scheduledJobs, setScheduledJobs] = useState(0);
  const [completedJobs, setCompletedJobs] = useState(0);
  const [activityData, setActivityData] = useState();
  const [jobLoading, setJobLoading] = useState(true);
  const [test, setTest] = useState();

  const history = useHistory();

  const socket = useSocket();

  useEffect(() => {
    socket.on("connect", () => {
      console.log("Socket is connected", id);
    });
  }, [socket]);

  useEffect(() => {
    socket.on("recentActivity", (data) => {
      if (activityData?.length === 3) {
        const activityArray = [...activityData];
        activityArray.splice(0, 0, data);
        activityArray.pop();

        setActivityData(activityArray);
      } else if (activityData?.length < 3) {
        const temp2 = [...activityData];
        temp2.splice(0, 0, data);
        setActivityData(temp2);
      }
    });
  }, [activityData, socket]);

  useEffect(() => {
    // setUserName('');
    window.scrollTo(0, 0);
    const getUser = async () => {
      const response = await FetchFromServer(
        ApiRoutes.USER_OVERVIEW_INFO.service,
        ApiRoutes.USER_OVERVIEW_INFO.url.replace(":id", id),
        ApiRoutes.USER_OVERVIEW_INFO.method,
        ApiRoutes.USER_OVERVIEW_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: "/users",
        });
      } else {
        setUserData(response.data.data);
        setUserName(
          response.data.data.firstName + " " + response.data.data.lastName
        );
        setUserId(response.data.data._id);
      }
    };
    const getJobList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.JOB_LIST.service,
        ApiRoutes.JOB_LIST.url,
        ApiRoutes.JOB_LIST.method,
        ApiRoutes.JOB_LIST.authenticate,
        { userId: id },
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        let openJob = [];
        let closeJob = [];
        for (let x in response.data.data) {
          if (
            response.data.data[x].status === "Pending" ||
            response.data.data[x].status === "inProgress" ||
            response.data.data[x].status === "Paused"
          ) {
            openJob.push(response.data.data[x]);
          } else {
            closeJob.push(response.data.data[x]);
          }
        }
        setScheduledJobs(openJob.length);
        setCompletedJobs(closeJob.length);
        setOpenJobData(openJob);
        setCloseJobData(closeJob);
        setJobLoading(false);
      }
    };
    getUser();
    getJobList();
    // eslint-disable-next-line
  }, [activityData]);

  useEffect(() => {
    const getChatList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.ACTIVITY_USER.service,
        ApiRoutes.ACTIVITY_USER.url.replace(":id", id),
        ApiRoutes.ACTIVITY_USER.method,
        ApiRoutes.ACTIVITY_USER.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTest(response.data.data.length);
        setActivityData(response.data.data);
      }
    };
    getChatList();
  }, [test]);

  return (
    <div>
      <InnerPageHeader
        id={id}
        email={userData ? userData.email : ""}
        mobile={userData ? userData.phoneNumber : ""}
      />
      <Container fluid className="userProfilePage">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={8} className="mt-4">
            <Row>
              <Col xs={12} sm={12} md={12} lg={12} xl={12}>
                <Card className="one mb-0">
                  <Card.Body>
                    <div className="p-24 d-flex align-items-center justify-content-between custom-wrap userCardfirstOverview">
                      <div className="d-flex align-items-center mb-xs-1">
                        <div className="position-relative activeStatus">
                          <div className="userPicture">
                            <img
                              src={
                                userData && userData.profilePicture
                                  ? userData.profilePicture.startsWith(
                                    "https://"
                                  )
                                    ? userData.profilePicture
                                    : `${AppConfig.FILES_ENDPOINT}${userData.profilePicture}`
                                  : userAvtar
                              }
                              onError={(e) => {
                                e.target.onerror = null;
                                e.target.src = userAvtar;
                              }}
                              alt={userData && userData.profilePicture}
                            />
                          </div>
                        </div>
                        <div className="d-flex align-items-start justify-content-start flex-column ml-3">
                          <p className="m-0">Login at {userData?.lastLogin}</p>
                          <h6 className="mb-0 nameText">
                            {userData ? userData.firstName : ""}{" "}
                            {userData ? userData.lastName : ""}
                          </h6>
                        </div>
                      </div>
                      <div className="d-flex align-items-start justify-content-start flex-column mb-xs-1">
                        <small className="m-0">enAble ID</small>
                        <h6 className="mb-0">
                          {userData ? userData.enableID : ""}
                        </h6>
                      </div>
                      <div className="d-flex align-items-start justify-content-start flex-column mr-sm-4 pr-sm-5">
                        <small className="m-0">CONTRACTOR ID</small>
                        <h6 className="mb-0">
                          {userData ? userData.contractorID : ""}
                        </h6>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </Col>

              <Col xs={12} sm={12} md={12} lg={6} xl={6} className="mt-4">
                <Card>
                  <Card.Body>
                    <div className="card_flex h-177px">
                      <div className="d-flex align-items-center justify-content-between flex-wrap flex-wrap innerBox box1">
                        <img src={Revenue} alt={Revenue} />

                        <div className="price">
                          <h5 className="mb-0 d-flex align-items-center justify-content-between">
                            ${userData ? userData.thisWeekRevenue : 0}
                            <span>
                              {userData ? (
                                userData.upDown === "Up" ? (
                                  <img
                                    src={topGeenArrow}
                                    className="mr-1"
                                    alt={topGeenArrow}
                                  />
                                ) : (
                                  <img
                                    src={downloadArrow}
                                    className="mr-1"
                                    alt={downloadArrow}
                                  />
                                )
                              ) : (
                                ""
                              )}{" "}
                              {userData ? userData.percentage : 0}%
                            </span>
                          </h5>
                          <p className="mb-0">Week Revenue</p>
                        </div>
                      </div>

                      <hr />

                      <div className="d-flex align-items-center justify-content-between lastWeek_amount">
                        <p className="m-0">Last Week</p>
                        <h5 className="m-0">
                          ${userData ? userData.previousWeekRevenue : 0}
                        </h5>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </Col>

              <Col xs={12} sm={6} md={6} lg={3} xl={3} className="mt-4">
                <Card>
                  <Card.Body>
                    <div className="card_flex h-177px">
                      <div className="d-flex align-items-center justify-content-center flex-column scheduled_job text-black">
                        <img
                          className="mb-3"
                          src={Scheduled_job}
                          alt={Scheduled_job}
                        />
                        <h5 className="mb-3">
                          {userData ? userData.scheduledJobs : 0}
                        </h5>
                        <h6 className="m-0">Scheduled Jobs</h6>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </Col>

              <Col xs={12} sm={6} md={6} lg={3} xl={3} className="mt-4">
                <Card>
                  <Card.Body>
                    <div className="card_flex h-177px">
                      <div className="d-flex align-items-center justify-content-center flex-column scheduled_job text-black">
                        <img
                          className="mb-3"
                          src={Complete_jobs}
                          alt={Complete_jobs}
                        />
                        <h5 className="mb-3">
                          {userData ? userData.completedJobs : 0}
                        </h5>
                        <h6 className="m-0">Completed Jobs</h6>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Col>

          <Col xs={12} sm={12} md={12} lg={12} xl={4} className="mb-4 mt-4">
            <Card className="recentActivity_box useroverview">
              <Card.Header className="py-3 bg-transparent d-flex align-items-center justify-content-between pink_btn">
                <h6 className="m-0">Recent Activity</h6>
              </Card.Header>
              <Card.Body>
                {test > 0 ? (
                  activityData?.map((item, key) => {
                    let activityImg = "";
                    if (item.type === "loggedIn") activityImg = LogIn;
                    if (item.type === "loggedOut") activityImg = LogOut;
                    if (item.type === "reportIssue") activityImg = OpenJob;
                    if (item.type === "openJob") activityImg = OpenJob;
                    if (item.type === "completeJob") activityImg = OpenJob;
                    if (item.type === "incompleteJob") activityImg = Incomplete;
                    return (
                      <RecentActivity
                        id={item.createdBy}
                        img={activityImg}
                        message={item.message}
                        alt={activityImg}
                        type={item.type}
                        time={item.createdAt}
                        profilePicture={item.profilePicture}
                        data="yes"
                      />
                    );
                  })
                ) : test === 0 ? (
                  <div className="d-flex align-items-center justify-content-center my-5">
                    <div className="position-relative noDataFound">
                      <img src={NotFound} alt={NotFound} />
                      <h6 className="mb-0">No Data Available</h6>
                      <p>The data you're looking for is not available</p>
                    </div>
                  </div>
                ) : (
                  <RecentActivity data="no" />
                )}
                {/* <div className="d-flex align-items-center justify-content-center my-5">
                  <div className="position-relative noDataFound">
                    <img src={NotFound} alt={NotFound} />
                    <h6 className="mb-0">No Data Available</h6>
                    <p>The data you're looking for is not available</p>
                  </div>
                </div> */}
                {/* <RecentActivity
                  img={LowStockAlert}
                  dayTime="1 day, 7:32am"
                  activity="Low stock alert - Brushes"
                  msg="Please check the stock quantity for this item."
                />
                <RecentActivity
                  img={Calendar}
                  dayTime="28 August 2020, 8:44am"
                  activity="Rick Sanchez updated his calendar"
                  msg="Check the availibility changes by clicking in"
                />
                <RecentActivity
                  img={warning}
                  dayTime="12 August 2020, 1:32pm"
                  activity="Tool calibration warning"
                  msg="This product calibration will expire in"
                /> */}
              </Card.Body>
            </Card>
          </Col>

          <Col md={12}>
            <h5 className="title mt-4 mb-4">
              <span>Open Jobs</span>
            </h5>
          </Col>

          {jobLoading ? (
            <JobLoader />
          ) :
            openJobData.length ? (
              openJobData.map((item, key) => {
                let startTime = new Date(item.startTime);
                let endTime = new Date();
                let difference = endTime.getTime() - startTime.getTime();
                let resultInMinutes = Math.round(difference / 60000);
                let countInt = 100;
                let barPercentage = '100%';
                if (resultInMinutes < item.totalTime) {
                  countInt = (((item.totalTime - resultInMinutes) * 100) / item.totalTime);
                  barPercentage = (((item.totalTime - resultInMinutes) * 100) / item.totalTime) + '%';
                }
                let color = '';
                if (countInt <= 30) {
                  color = 'green';
                } else if (countInt > 30 && countInt <= 60) {
                  color = 'orange';
                } else {
                  color = 'red';
                }
                return (
                  <Col xs={12} sm={12} md={12} lg={6} xl={4} key={key}>
                    <Card className="openJobs">
                      <Card.Body>
                        <OpenJobs
                          jobID={item.jobId}
                          amount={item.totalAmount}
                          title={item.jobAddress}
                          parentID={item.parentId}
                          taskID={item.taskId}
                          jobType={item.jobType =='Special'? item.technologyName +'- SCHEDULE 3' :item.technologyName}
                          time={item.startTime}
                          totalTime={item.totalTime}
                          timePercentage={barPercentage}
                          spentTime={resultInMinutes}
                          color={color}
                          id={item._id}
                        />
                      </Card.Body>
                    </Card>
                  </Col>
                );
              })
            ) : (
              <Col xs={12} sm={12} md={12} lg={6} xl={12} key="2">
                <NoDataCard />
              </Col>
            )}

          <Col md={12}>
            <h5 className="title mt-4 mb-4">
              <span>Closed Jobs</span>
            </h5>
          </Col>
          {jobLoading ? (
            <JobLoader />
          ) : closeJobData.length ? (
            closeJobData.map((item, key) => {
              return (
                <Col xs={12} sm={12} md={12} lg={6} xl={4} key={key}>
                  <Card className="closeJobs">
                    <Card.Body>
                      <ClosedJob
                        jobID={item.jobId}
                        amount={item.totalAmount}
                        title={item.jobAddress}
                        parentID={item.parentId}
                        taskID={item.taskId}
                        jobType={item.jobType =='Special'? item.technologyName +'- SCHEDULE 3' :item.technologyName}
                        time={item.startTime}
                        timeTaken={item.timeTaken}
                        id={item._id}
                      />
                    </Card.Body>
                  </Card>
                </Col>
              );
            })
          ) : (
            <Col xs={12} sm={12} md={12} lg={6} xl={12} key="1">
              <NoDataCard />
            </Col>
          )}
        </Row>
      </Container>
    </div>
  );
};

export { UserOverview };
