import React, { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import Skeleton from 'react-loading-skeleton';

import { Col, Container, Card, Row } from 'react-bootstrap';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { InnerPageHeader } from '../../components/Rate';
import strokeBlue from '../../../assets/img/stroke_blue.svg';
import { Link } from 'react-router-dom';

const RuleList = () => {
  const [scheduleData, setScheduleData] = useState();
  const [isLoading, setIsLoading] = useState(true);
  useEffect(() => {
    const getScheduleList = async () => {
      setIsLoading(true);
      const response = await FetchFromServer(
        ApiRoutes.SCHEDULE_LIST.service,
        ApiRoutes.SCHEDULE_LIST.url,
        ApiRoutes.SCHEDULE_LIST.method,
        ApiRoutes.SCHEDULE_LIST.authenticate,
        undefined
      );
      setIsLoading(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setScheduleData(response.data.data);
      }
    };
    getScheduleList();
  }, []);
  return (
    <>
      <InnerPageHeader activeTab="businessRules" />
      <Container fluid>
        <Row className="pt-4 businessRules">
          {scheduleData &&
            scheduleData.map((data, i) => (
              <Col sm={12} md={6} lg={4} xl={4} className="rules-Grid">
                <Link to={`/rates/rules/schedule/${data._id}`}>
                  <Card
                    // bg={i === 0 ? 'primary' : ''}
                    key="1"
                    text={i === 0 ? 'white' : 'black'}
                    style={{ height: '14rem', fontSize: '18px' }}
                    className="mb-3 overflow-hidden"
                  >
                    <Card.Body className="d-flex align-items-center justify-content-center position-relative businessRuleCard">
                      <Card.Text className="mb-0">
                        {data.scheduleName}
                      </Card.Text>
                      <img
                        className="strokeImg"
                        src={strokeBlue}
                        alt={strokeBlue}
                      />
                    </Card.Body>
                  </Card>
                </Link>
              </Col>
            ))}

          {isLoading ? (
            <>
              <Col sm={12} md={6} lg={4} xl={4} className="rules-Grid">
                <Skeleton height={200} />
              </Col>
              <Col sm={12} md={6} lg={4} xl={4} className="rules-Grid">
                <Skeleton height={200} />
              </Col>
              <Col sm={12} md={6} lg={4} xl={4} className="rules-Grid">
                <Skeleton height={200} />
              </Col>
            </>
          ) : (
            ''
          )}
        </Row>
      </Container>
    </>
  );
};

export { RuleList };
