import React, { useState, useEffect } from 'react';
import { Form } from 'react-bootstrap';
import cameraChooseFileImg from '../../../assets/img/camera_chooseFile.svg';
import dummyImage from '../../../assets/img/tool-new.svg';
import { AppConfig } from '../../../utils/config';

const AddMaterial = (props) => {
  const { handleChange, errors, values, touched, uomData,setFieldValue } = props;
  const [profilePicture, setProfilePicture] = useState(dummyImage);
  const fileChangedHandler = (event) => {
    const file = event.target.files[0];
    const src = URL.createObjectURL(file);
    setProfilePicture(src);
  }; 
  useEffect(() => {
    if (values)
      if (values.materialImage)
        setProfilePicture(AppConfig.FILES_ENDPOINT + values.materialImage);
    // eslint-disable-next-line
  }, [values.materialImage]);
  return (
    <div className="row arrow_Dropdown">
      <div className="col-xs-12 col-sm-12 col-md-12 col-lg-8">
        <div className="row">
          <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6">
            <div className="input-file position-relative d-flex align-items-center">
              <div className="position-relative">
                <img
                  id="file_upload"
                  src={profilePicture}
                  alt={profilePicture}
                  className="upload-img"
                />
                <span className="upload-label">
                  <img src={cameraChooseFileImg} alt={cameraChooseFileImg} />
                </span>
              </div>
              <p className="mb-0">Click to change the material picture</p>
              <div className="input-file-upload">
                <input
                  type="file"
                  name="materialImage"
                  onChange={fileChangedHandler}
                  accept="image/*"
                />
              </div>
            </div>
          </div>

          <div className="col-xs-12 col-sm-12 col-md-6 col-lg-6 mt-4 mt-md-0">
            <div className="form-group">
              <Form.Label className="mb-0">Sales Part Number</Form.Label>
              <Form.Control
                type="text"
                name="salesPartNumber"
                placeholder="Select Job Type"
                onChange={handleChange}
                value={values ? values.salesPartNumber : ''}
              />
              <span className="errorMsg">
                {errors.salesPartNumber && touched.salesPartNumber
                  ? errors.salesPartNumber
                  : null}
              </span>
            </div>
          </div>

          <div className="col-xs-12 col-sm-6 col-md-6 col-lg-6">
            <div className="form-group">
              <label className="mb-0">Qty In Stock</label>
              <Form.Control
                type="number"
                name="inStock"
                placeholder="Add quantity"
                onChange={handleChange}
                value={values ? values.inStock : ''}
                min="1"
              />
              <span className="errorMsg">
                {errors.inStock && touched.inStock ? errors.inStock : null}
              </span>
            </div>
          </div>

          <div className="col-xs-12 col-sm-6 col-md-6 col-lg-6">
            <div className="form-group">
              <label className="mb-0">Unit Of Measurement</label>
              <Form.Control
                as="select"
                name="uom"
                onChange={handleChange}
                value={values ? values.uom : ''}
              >
                <option value="" disabled selected>Select Unit Of Measurement</option>
                {uomData &&
                  uomData.map((data, i) => (
                    <option value={data._id} key={i}>
                      {data.uomName}
                    </option>
                  ))}
              </Form.Control>
              <span className="errorMsg">
                {errors.uom && touched.uom ? errors.uom : null}
              </span>
            </div>
          </div>
        </div>
      </div>

      <div className="col-xs-12 col-sm-12 col-md-12 col-lg-4">
        <div className="row">
          <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
            <div className="form-group">
              <label className="mb-0">Material Description</label>
              <Form.Control
                as="textarea"
                name="materialDescription"
                onChange={handleChange}
                value={values ? values.materialDescription : ''}
              />
              <span className="errorMsg">
                {errors.materialDescription && touched.materialDescription
                  ? errors.materialDescription
                  : null}
              </span>
            </div>
          </div>
        </div>
      </div>

      <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
        <div className="row">
          <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
            <div className="form-group">
              <label className="mb-0">Set Low Margin Admin</label>
              <Form.Control
                as="select"
                name="lowMarginAdmin"
                onChange={handleChange}
                value={values ? values.lowMarginAdmin : ''}
              >
                <option value="" disabled selected>Set a range for stock control</option>
                <option value="500">500 or less</option>
                <option value="300">300 or less</option>
                <option value="100">100 or less</option>
              </Form.Control>
              <span className="errorMsg">
                {errors.lowMarginAdmin && touched.lowMarginAdmin
                  ? errors.lowMarginAdmin
                  : null}
              </span>
            </div>
          </div>

          <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
            <div className="form-group">
              <label className="mb-0">Set Average Margin Admin</label>
              <Form.Control
                as="select"
                name="averageMarginAdmin"
                onChange={handleChange}
                value={values ? values.averageMarginAdmin : ''}
              >
                <option value="" disabled selected>Set a range for stock control</option>
                <option value="1000">Upto 1000</option>
                <option value="800">Upto 800</option>
                <option value="600">Upto 600</option>
              </Form.Control>
              <span className="errorMsg">
                {errors.averageMarginAdmin && touched.averageMarginAdmin
                  ? errors.averageMarginAdmin
                  : null}
              </span>
            </div>
          </div>

          <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
            <div className="form-group">
              <label className="mb-0">Set Low Margin User</label>
              <Form.Control
                as="select"
                name="lowMarginUser"
                onChange={handleChange}
                value={values ? values.lowMarginUser : ''}
              >
                <option value="" disabled selected>Select Margin</option>
                <option>20 or less</option>
              </Form.Control>
              <span className="errorMsg">
                {errors.lowMarginUser && touched.lowMarginUser
                  ? errors.lowMarginUser
                  : null}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export { AddMaterial };
