import * as Yup from 'yup';
import moment from 'moment';

const AddUserSchema = Yup.object().shape({
  firstName: Yup.string()
    .required('First name is required')
    .test('length', 'Must have more than 1 character', (value) => {
      return value && value.length > 1;
    })
    .test('alphabets', 'First name must only contain alphabets', (value) => {
      return /^[A-Za-z ]+$/.test(value);
    })
    .max(15, 'Maximum 15 characters are allowed'),
  lastName: Yup.string()
    .required('Last name is required')
    .test('length', 'Must have more than 1 character', (value) => {
      return value && value.length > 1;
    })
    .test('alphabets', 'Last name must only contain alphabets', (value) => {
      return /^[A-Za-z ]+$/.test(value);
    })
    .max(15, 'Maximum 15 characters are allowed'),
  email: Yup.string().email('Invalid email').required('Email is required'),
  phoneNumber: Yup.number()
    .typeError('Only numbers are allowed')
    .required('Phone number is required')
    // .min(8, 'Invalid phone number')
    // .max(15, 'Invalid phone number')
    .test('len', 'Invalid phone number', (val) => {
      if (val) {
        if (val.toString().length < 8 || val.toString().length > 15) {
          return false;
        } else {
          return true;
        }
      }
      // val.toString().length <= 6
    }),
  dateOfBirth: Yup.date()
    .required('Date of birth is required')
    .test('date', 'Invalid date of birth', (value) => {
      let fromDate = moment().subtract(100, 'years').format('YYYY/MM/DD');
      // let toDate = moment().subtract(18, 'years').format('YYYY/MM/DD');
      let currentDate = moment(value).format('YYYY/MM/DD');
      if (currentDate < fromDate) {
        return false;
      } else {
        return true;
      }
      // return /^[A-Za-z ]+$/.test(value);
    }),
  contractorID: Yup.string()
    .required('Contractor id is required')
    .test('alphabets', 'Only alphanumeric characters are allowed', (value) => {
      return /^[A-Za-z0-9 ]+$/.test(value);
    })
    .max(15, 'Maximum 15 characters are allowed'),
  enableID: Yup.string()
    .required('Enable id is required')
    .test('alphabets', 'Only alphanumeric characters are allowed', (value) => {
      return /^[A-Za-z0-9]+$/.test(value);
    })
    .max(15, 'Maximum 15 characters are allowed'),

  emergency: Yup.array().of(
    Yup.object().shape({
      firstName: Yup.string()
        .test('length', 'Must have more than 1 character', (value) => {
          if (value) {
            return value && value.length > 1;
          } else {
            return true;
          }
        })
        .test(
          'alphabets',
          'First name must only contain alphabets',
          (value) => {
            return /^[A-Za-z ]+$/.test(value);
          }
        )
        .max(15, 'Maximum 15 characters are allowed'),
      lastName: Yup.string()
        .test('length', 'Must have more than 1 character', (value) => {
          if (value) {
            return value && value.length > 1;
          } else {
            return true;
          }
        })
        .test('alphabets', 'Last name must only contain alphabets', (value) => {
          return /^[A-Za-z ]+$/.test(value);
        })
        .max(15, 'Maximum 15 characters are allowed'),
      email: Yup.string().email('Invalid email'),
      phoneNumber: Yup.number()
        .typeError('Only numbers are allowed')
        .test('len', 'Invalid phone number', (val) => {
          if (val) {
            if (val.toString().length < 8 || val.toString().length > 15) {
              return false;
            } else {
              return true;
            }
          } else {
            return true;
          }
        }),
    })
  ),
});

export { AddUserSchema };
