import React, { useState, useEffect } from 'react';
import { Form } from 'react-bootstrap';
import cameraChooseFileImg from '../../../assets/img/camera_chooseFile.svg';
import userAvtar from '../../../assets/img/user_avtar.svg';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment';

const ProfileInformation = (props) => {
  const { handleChange, errors, values, touched, setFieldValue } = props;
  const [isFirst, setIsFirst] = useState(true);
  const [profilePicture, setProfilePicture] = useState(userAvtar);
  
  const fileChangedHandler = (event) => {
    const file = event.target.files[0];
    const src = URL.createObjectURL(file);
    setProfilePicture(src);
  };

  useEffect(() => {
    console.log(values,'values');
    if (values)
      if (values.profilePicture) setProfilePicture(values.profilePicture);
      
    // eslint-disable-next-line
  }, [values.profilePicture]);
  return (
    <div className="row">
      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="input-file position-relative d-flex align-items-center">
          <div className="position-relative">
            <img
              id="file_upload"
              src={profilePicture}
              alt={profilePicture}
              className="upload-img"
            />
            <span className="upload-label">
              <img src={cameraChooseFileImg} alt={cameraChooseFileImg} />
            </span>
          </div>
          <p className="mb-0">Click to add a profile picture</p>
          <div className="input-file-upload">
            <input
              type="file"
              name="profilePicture"
              onChange={fileChangedHandler}
              accept="image/*"
            />
          </div>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <Form.Label className="mb-0">First Name</Form.Label>
          <Form.Control
            type="text"
            name="firstName"
            onChange={handleChange}
            value={values ? values.firstName : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.firstName && touched.firstName ? errors.firstName : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Last Name</label>
          <Form.Control
            type="text"
            name="lastName"
            onChange={handleChange}
            value={values ? values.lastName : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.lastName && touched.lastName ? errors.lastName : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Email</label>
          <Form.Control
            type="text"
            name="email"
            onChange={handleChange}
            value={values ? values.email : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.email && touched.email ? errors.email : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Phone</label>
          <Form.Control
            type="text"
            name="phoneNumber"
            onChange={handleChange}
            value={values ? values.phoneNumber : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.phoneNumber && touched.phoneNumber
              ? errors.phoneNumber
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">D.O.B</label>
          {/* <DatePicker
            name="dateOfBirth"
            maxDate={new Date()}
            minDate={new Date(moment().subtract(100, 'years'))}
            wrapperClassName="form-control"
            className="form-control"
            placeholderText="Select D.O.B"
            value={values.dateOfBirth}
            onChange={(date) => {
              console.log('here');
              if (isFirst) {
                setIsFirst(false);
              }
              values.dateOfBirth = date;
              setFieldValue({ dateOfBirth: date });
            }}
            selected={isFirst ? '' : values.dateOfBirth}
            peekNextMonth
            showMonthDropdown
            showYearDropdown
            dropdownMode="select"
          /> */}
          {/* <Form.Control
            className="date"
            type={values && values.dateOfBirth ? 'date' : 'text'}
            name="dateOfBirth"
            placeholder="Select D.O.B"
            onChange={handleChange}
            value={values ? values.dateOfBirth : ''}
            onFocus={(e) => {
              e.currentTarget.type = 'date';
              // e.currentTarget.value = moment(e.currentTarget.value).format(
              //   'YYYY/MM/DD'
              // );
            }}
            onBlur={(e) => {
              if (e.currentTarget.value === '') {
                e.currentTarget.type = 'text';
                // e.currentTarget.value = moment(e.currentTarget.value).format(
                //   'DD/MM/YYYY'
                // );
              }
            }}
          />
          <label></label> */}
          <DatePicker
            wrapperClassName="form-control date-icon"
            maxDate={new Date()}
            minDate={new Date(moment().subtract(100, 'years'))}
            className="form-control"
            placeholderText="dd/mm/yyyy"
            autoComplete={false}
            name="dateOfBirth"
            dateFormat="dd/MM/yyyy"
            onChange={(date) => {
              if (date)
                handleChange('dateOfBirth')(moment(date).format('YYYY-MM-DD'));
              else handleChange('dateOfBirth')('');
            }}
            selected={
              values && values.dateOfBirth
                ? new Date(moment(values.dateOfBirth).format('YYYY-MM-DD'))
                : ''
            }
            peekNextMonth
            showMonthDropdown
            showYearDropdown
            dropdownMode="select"
          />
          <span className="errorMsg">
            {' '}
            {errors.dateOfBirth && touched.dateOfBirth
              ? errors.dateOfBirth
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">Contractor ID</label>
          <Form.Control
            type="text"
            placeholder="ID82738273"
            name="contractorID"
            onChange={handleChange}
            value={values ? values.contractorID : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.contractorID && touched.contractorID
              ? errors.contractorID
              : null}
          </span>
        </div>
      </div>

      <div className="col-xs-12 col-sm-6 col-md-6 col-lg-4">
        <div className="form-group">
          <label className="mb-0">enAble ID - NBN</label>
          <Form.Control
            type="text"
            placeholder="ID928392"
            name="enableID"
            onChange={handleChange}
            value={values ? values.enableID : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.enableID && touched.enableID ? errors.enableID : null}
          </span>
        </div>
      </div>
    </div>
  );
};

export { ProfileInformation };
