import React, { useEffect, useState } from 'react';
import { Link, useParams, useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import {
  Col,
  Container,
  Form,
  FormGroup,
  Row,
  Table,
  Modal,
} from 'react-bootstrap';

import InnerPageHeader from '../../components/User/InnerPageHeader';
import TableFooter from '../../components/TableFooter';
import NoDataCard from '../../components/NoDataCard';
import { TableLoader } from '../../components/loader/Resusable/Table';

import previewEye from '../../../assets/img/previewEye.svg';
import editTool from '../../../assets/img/edit-tool.svg';
import toolIcon from './../../../assets/img/tool.svg';
import dummyImage from '../../../assets/img/noimage.jpeg';
import popupClose from './../../../assets/img/popupClose.svg';
import pdfDownload from './../../../assets/img/pdf_download.svg';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes, AppConfig } from '../../../utils/config'; 

const Tools = () => {
  const { id } = useParams();
  const [userData, setUserData] = useState();
  const [toolData, setToolData] = useState([]);
  const [imagePreviewPath, setImagePreviewPath] = useState('');
  const [show, setShow] = useState(false);
  const [totalRecord, setTotalRecord] = useState(0);
  const [activePage, setActivePage] = useState(1);
  const [isLoading, setIsLoading] = useState(true);
  const [tableMessage, setTableMessage] = useState('No Data Available');
  const [tableFilter, setTableFilter] = useState({
    search: '',
    skip: 0,
    limit: 10,
    sortBy: '',
    order: '',
  });
  const history = useHistory();
  const getToolList = async () => {
    setIsLoading(true);
    const response = await FetchFromServer(
      ApiRoutes.TOOL_LIST.service,
      ApiRoutes.TOOL_LIST.url.replace(':id', id),
      ApiRoutes.TOOL_LIST.method,
      ApiRoutes.TOOL_LIST.authenticate,
      tableFilter,
      undefined
    );
    setIsLoading(false);
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      setTableMessage(response.messages[0]);
      setToolData(response.data.data);
      setTotalRecord(response.data.totalRecords);
    }
  };
  useEffect(() => {
    const getUser = async () => {
      const response = await FetchFromServer(
        ApiRoutes.USER_INFO.service,
        ApiRoutes.USER_INFO.url.replace(':id', id),
        ApiRoutes.USER_INFO.method,
        ApiRoutes.USER_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
        history.push({
          pathname: '/users',
        });
      } else {
        setUserData(response.data.data);
      }
    };
    getUser();
    getToolList();
    // eslint-disable-next-line
  }, []);
  useEffect(() => {
    getToolList();
    // eslint-disable-next-line
  }, [tableFilter]);
  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
    }
  };
  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === 'pageSize') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
    }
    if (name === 'search') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        search: e.target.value,
      });
    }
  };
  const handleClose = () => setShow(false);
  const handleImagePreview = (key) => {
    setImagePreviewPath(key);
    setShow(true);
  };
  return (
    <div>
      <InnerPageHeader
        id={id}
        email={userData ? userData.email : ''}
        mobile={userData ? userData.phoneNumber : ''}
        activeTab="tools"
      />
      <Container fluid className="jobHistoryPage inventory">
        <Row>
          <Col lg={12} className="mb-3 mt-4">
            <Row>
              <Col xs={12} sm={12} md={10} lg={7} xl={5}>
                <FormGroup className="mb-0 d-flex">
                  <Form.Control
                    type="text"
                    placeholder="Search tools"
                    style={{ height: '45px', flex: '2' }}
                    name="search"
                    onChange={handleSearch}
                    className="toolFormControl"
                  />
                  <Link
                    to={`/users/tools/add/${id}`}
                    variant="button"
                    className="btn-pink btn_addTools ml-3 d-flex align-items-center justify-content-center"
                    type="submit"
                    style={{
                      height: '45px',
                      fontsize: '14px',
                      flex: '1',
                      borderRadius: '5px',
                    }}
                  >
                    <img className="mr-2" src={toolIcon} alt={toolIcon} /> Add
                    Tool
                  </Link>
                </FormGroup>
                <Form.Label className="mb-0">
                  <span>Search</span> in all fields
                </Form.Label>
              </Col>
            </Row>
          </Col>

          <Col md={12}>
            {isLoading ? <TableLoader columns={7} rows={10} /> : ''}
            <Table hover responsive className="mt-3 toolTable">
              <tbody>
                <tr className="first-child">
                  <th className="border-0">Image</th>
                  <th className="border-0">Description</th>
                  <th className="border-0">Manufacturer</th>
                  <th className="border-0">Serial Number</th>
                  <th className="border-0">Status</th>
                  <th className="border-0"><span className="dateExpiryTH">Date Expiry</span></th>
                  <th className="border-0"><span className="editToolTH"></span></th>
                </tr>
                {toolData &&
                  isLoading === false &&
                  toolData.map((data, i) => {
                    return (
                      <tr key={i}>
                        <td className="productItem position-relative">
                          <img
                            src={
                              data.toolPicture
                                ? data.toolPicture.startsWith('https://')
                                  ? data.toolPicture
                                  : `${AppConfig.FILES_ENDPOINT}${data.toolPicture}`
                                : dummyImage
                            }
                            onError={(e) => {
                              e.target.onerror = null;
                              e.target.src = dummyImage;
                            }}
                            alt={data.toolPicture}
                          />
                          <div
                            onClick={() => handleImagePreview(data.toolPicture)}
                          >
                            <img src={previewEye} alt={previewEye} />
                          </div>
                        </td>
                        <td>
                          <div className="description">{data.description}</div>
                        </td>
                        <td>{data.manufacturer}</td>
                        <td>{data.serialNumber}</td>
                        <td>Calibrated</td>
                        <td>25/03/2018</td>
                        <td className="Width200 text-primary font-weight-bold">
                          <div className="d-flex justify-content-end">
                              <Link to="#" className="mr-2" download>
                              <img                                
                                src={pdfDownload}
                                alt={pdfDownload}
                              />
                            </Link>
                            <Link to={`/users/tools/edit/${data._id}`}>
                              <img
                                className="mr-2"
                                src={editTool}
                                alt={editTool}
                              />{' '}
                              Edit tool
                            </Link>           
                          </div>                          
                        </td>
                      </tr>
                    );
                  })}
                {toolData.length === 0 && isLoading === false ? (
                  <tr>
                    <td colSpan="7">
                      <NoDataCard title={tableMessage} />
                    </td>
                  </tr>
                ) : (
                  ''
                )}
              </tbody>
            </Table>
          </Col>
        </Row>
        <TableFooter
          totalRecord={totalRecord}
          activePage={activePage}
          tableFilter={tableFilter}
          handlePageChange={handlePageChange}
          handleSearch={handleSearch}
        />
      </Container>
      <Modal
        className="profileEditModel two imgViewModal"
        show={show}
        onHide={handleClose}
        // backdrop="static"
        keyboard={false}
      >
        <span className="modelClose" onClick={handleClose}>
          <img src={popupClose} alt={popupClose} />
        </span>
        <Modal.Body className="p-0">
          <img
            src={
              imagePreviewPath
                ? imagePreviewPath.startsWith('https://')
                  ? imagePreviewPath
                  : `${AppConfig.FILES_ENDPOINT}${imagePreviewPath}`
                : dummyImage
            }
            onError={(e) => {
              e.target.onerror = null;
              e.target.src = dummyImage;
            }}
            width="100%"
            alt={imagePreviewPath}
          />
        </Modal.Body>
      </Modal>
    </div>
  );
};
export { Tools };
