import React, {useState} from 'react';
import { Form, Row, Col, Dropdown, DropdownButton } from 'react-bootstrap';

const TaskInformation = (props) => {
  const {
    handleChange,
    errors,
    values,
    technologyData,
    uomData,
    scheduleData,
    categoryData,
    subcategoryData,
    businessRuleData,
    getCategoryList,
    getSubcategoryList,
    getBusinessRuleList,
    getTechnologyList,
    touched,
    setFieldValue,
  } = props;

  const handleCheckbox = (e) => {
    const { checked, value } = e.target;

    if (checked) {
      setFieldValue("businessRules", [...values.businessRules, value]);
    } else {
      setFieldValue(
        "businessRules",
        values.businessRules.filter((v) => v !== value)
      );
    }
  };

  const [value,setValue]=useState('');
  const handleSelect=(e)=>{
    setValue(e)
  };

  return (
    <Row className="arrow_Dropdown">
      <Col xs={12} sm={12} md={12} lg={8}>
        <Row>
          <Col xs={12} sm={6} md={12} lg={6}>
            <Form.Group>
              <Form.Label className="mb-0">Item Id</Form.Label>
              <Form.Control
                type="text"
                name="itemId"
                placeholder="FTTN-ACT-01-01"
                onChange={handleChange}
                value={values ? values.itemId : ""}
              />
              <span className="errorMsg">
                {" "}
                {errors.itemId && touched.itemId ? errors.itemId : null}
              </span>
            </Form.Group>
          </Col>

          <Col xs={12} sm={6} md={6} lg={6}>
            <Form.Group>
              <label className="mb-0">Select Schedule</label>
              <Form.Control
                as="select"
                name="schedule"
                onChange={(e) => {
                  handleChange("schedule")(e);
                  getCategoryList({
                    schedule: e.target.value,
                    technology: values.technology,
                  });
                  getBusinessRuleList({
                    schedule: e.target.value,
                    technology: values.technology,
                  });
                  getTechnologyList(e.target.value);
                }}
                value={values.schedule ? values.schedule : ""}
              >
                {/* <option hidden value="">
                  Select the schedule
                </option> */}
                {scheduleData &&
                  scheduleData.map((data, i) => (
                    <option value={data._id} key={i}>
                      {data.scheduleName}
                    </option>
                  ))}
              </Form.Control>
              <span className="errorMsg">
                {" "}
                {errors.schedule && touched.schedule ? errors.schedule : null}
              </span>
            </Form.Group>
          </Col>

          <Col xs={12} sm={6} md={6} lg={6}>
            <Form.Group>
              <label className="mb-0">Technology Application</label>
              <Form.Control
                as="select"
                name="technology"
                onChange={(e) => {
                  handleChange("technology")(e);
                  getCategoryList({
                    technology: e.target.value,
                    schedule: values.schedule,
                  });
                  getBusinessRuleList({
                    technology: e.target.value,
                    schedule: values.schedule,
                  });
                }}
                value={values.technology ? values.technology : ""}
              >
                <option hidden value="">
                  Select job type
                </option>
                {technologyData &&
                  technologyData.map((data, i) => (
                    <option value={data._id} key={i}>
                      {data.technologyName}
                    </option>
                  ))}
              </Form.Control>
              <span className="errorMsg">
                {" "}
                {errors.technology && touched.technology
                  ? errors.technology
                  : null}
              </span>
            </Form.Group>
          </Col>

          <Col xs={12} sm={6} md={12} lg={6}>
            <Form.Group>
              <label className="mb-0">Select Category</label>
              <Form.Control
                as="select"
                name="category"
                onChange={(e) => {
                  handleChange("category")(e);
                  getSubcategoryList({
                    category: e.target.value,
                  });
                }}
                value={values.category ? values.category : ""}
              >
                <option hidden value="">
                  Select category
                </option>
                {categoryData &&
                  categoryData.map((data, i) => (
                    <option value={data._id} key={i}>
                      {data.categoryName}
                    </option>
                  ))}
              </Form.Control>
              <span className="errorMsg">
                {" "}
                {errors.category && touched.category ? errors.category : null}
              </span>
            </Form.Group>
          </Col>
        </Row>
      </Col>

      <Col xs={12} sm={12} md={12} lg={4}>
        <Row>
          <Col xs={12} sm={12} md={12} lg={12}>
            <Form.Group>
              <label className="mb-0">Description</label>
              <Form.Control
                as="textarea"
                name="description"
                onChange={handleChange}
                value={values ? values.description : ""}
                rows={5}
                maxLength={240}
              />
              <span className="errorMsg">
                {" "}
                {errors.description && touched.description
                  ? errors.description
                  : null}
              </span>
            </Form.Group>
          </Col>
        </Row>
      </Col>

      <Col xs={12} sm={12} md={12} lg={12}>
        <Row>
          <Col xs={12} sm={12} md={6} lg={4}>
            <Form.Group>
              <label className="mb-0">Add Sub-category</label>
              <Form.Control
                as="select"
                name="subcategory"
                onChange={handleChange}
                value={values.subcategory ? values.subcategory : ""}
              >
                <option hidden value="">
                  Select or create category
                </option>
                {subcategoryData &&
                  subcategoryData.map((data, i) => (
                    <option value={data._id} key={i}>
                      {data.subcategoryName}
                    </option>
                  ))}
              </Form.Control>
              <span className="errorMsg">
                {" "}
                {errors.subcategory && touched.subcategory
                  ? errors.subcategory
                  : null}
              </span>
            </Form.Group>
          </Col>
          <Col xs={12} sm={12} md={6} lg={4}>
            <Form.Group>
              <label className="mb-0">Select Work Hours</label>
              <Form.Control
                as="select"
                name="workHours"
                onChange={handleChange}
                value={values.workHours ? values.workHours : ""}
              >
                <option value="">Select working hours</option>
                <option>Weekday (8am - 5pm)</option>
                <option>Weekend (8am - 5pm)</option>
                <option>After Hours</option>
              </Form.Control>
              <span className="errorMsg">
                {" "}
                {errors.workHours && touched.workHours
                  ? errors.workHours
                  : null}
              </span>
            </Form.Group>
          </Col>
          <Col xs={12} sm={12} md={6} lg={4}>
            <Form.Group>
              <label className="mb-0">Select Unit of Measure (UoM)</label>
              <Form.Control
                as="select"
                name="uom"
                onChange={handleChange}
                value={values.uom ? values.uom : ""}
              >
                <option hidden value="">
                  Select or create UoM
                </option>
                {uomData &&
                  uomData.map((data, i) => (
                    <option value={data._id} key={i}>
                      {data.uomName}
                    </option>
                  ))}
              </Form.Control>
              <span className="errorMsg">
                {" "}
                {errors.uom && touched.uom ? errors.uom : null}
              </span>
            </Form.Group>
          </Col>
          <Col xs={12} sm={12} md={12} lg={12}>
            {businessRuleData.length ? (
              <>
                <div className="row">
                  <div class="col-lg-12 col-md-12 col-sm-12">
                    <label>Select Business Rule</label>
                  </div>
                </div>
                <div className="row taskCheckBox">
                  {businessRuleData &&
                    businessRuleData.map((data, i) => (
                      <Form.Group className="col-lg-4 col-md-6 col-sm-12">
                        <div className="tgl-group position-relative">
                          <input
                            class="tgl tgl-light"
                            id={`display-address-${i}`}
                            type="checkbox"
                            checked={
                              values.businessRules &&
                              values.businessRules.includes(data._id)
                            }
                            onChange={handleCheckbox}
                            value={data._id}
                          />
                          <label
                            className="tgl-btn"
                            for={`display-address-${i}`}
                          >
                            <span class="yes">Yes</span>
                            <span class="no">No</span>
                          </label>
                          <label>{data.name}</label>
                        </div>
                      </Form.Group>
                    ))}
                </div>
              </>
            ) : (
              ""
            )}
          </Col>
          <Col xs={12} sm={12} md={6} lg={4}>
            <Form.Group>
              <label className="mb-0">Add Rates</label>
              <Form.Control
                type="text"
                name="rates"
                placeholder="34.56"
                onChange={handleChange}
                value={values ? values.rates : ""}
              />
              <span className="errorMsg">
                {" "}
                {errors.rates && touched.rates ? errors.rates : null}
              </span>
            </Form.Group>
          </Col>

          

          {/* <Col xs={12} sm={12} md={4} lg={4}>
            <label className="mb-0">Demo Select</label>
            <DropdownButton
              alignLeft
              title={value}
              onSelect={handleSelect}
              className="customDropdown"
              variant=""
            >
              <Dropdown.Item eventKey="Per Visit to Pillar/Node">Per Visit to Pillar/Node</Dropdown.Item>
              <Dropdown.Item eventKey="Per Jumper">Per Jumper</Dropdown.Item>
              <Dropdown.Item eventKey="Per Central Splitter">Per Central Splitter</Dropdown.Item>
              <Dropdown.Item eventKey="Per Jumper">Per Jumper</Dropdown.Item>
          </DropdownButton>       


          </Col> */}
        </Row>
      </Col>
    </Row>
  );
};

export { TaskInformation };
