import React from "react";
import { Link } from "react-router-dom";
import Skeleton from "react-loading-skeleton";
import { Table,Col,Card } from "react-bootstrap";

const JobLoader = () => {
  return (
    <>
      <Col xs={12} sm={12} md={12} lg={6} xl={4}>
        <Card className="openJobs">
          <Card.Body>
            <div class="col-xl-4 col-lg-6 col-md-12 col-sm-12 col-12">
              <div className="p-16 position-relative">
                <Link to="">
                  <div className="tagStrip m-0">
                    <Skeleton style={{ width: 90, height: 10 }} />
                  </div>
                  <div className="d-flex align-items-center justify-content-end mb-3 one">
                    <h5 className="mb-0"><Skeleton style={{ width: 28, height: 24 }} /></h5>
                    <Skeleton className="ml-4" style={{ width: 24, height: 24 }} />
                  </div>
                  <Skeleton className="mb-3 two" style={{ width: 100, height: 15 }} />
                  <Table className="table">
                    <tbody>
                      <tr>
                        <td className="border-0"><Skeleton className="mb-0" style={{ width: 150, height: 20 }} /></td>
                        <td className="border-0">
                          <span><Skeleton className="mb-0" style={{ width: 50, height: 20 }} /></span>
                        </td>
                      </tr>
                      <tr>
                        <td><Skeleton className="mb-0" style={{ width: 150, height: 20 }} /></td>
                        <td>
                          <span><Skeleton className="mb-0" style={{ width: 50, height: 20 }} /></span>
                        </td>
                      </tr>
                      <tr>
                        <td><Skeleton className="mb-0" style={{ width: 150, height: 20 }} /></td>
                        <td>
                          <span><Skeleton className="mb-0" style={{ width: 50, height: 20 }} /></span>
                        </td>
                      </tr>
                      <tr>
                        <td><Skeleton className="mb-0" style={{ width: 150, height: 20 }} /></td>
                        <td>
                          <span>
                            <Skeleton className="mb-0" style={{ width: 50, height: 20 }} />
                          </span>
                        </td>
                      </tr>
                    </tbody>
                  </Table>
                  <div className="progressBox d-flex align-items-center justify-content-start">
                    <div className="progress w-100 position-relative" >
                      <Skeleton className="mb-3 two" style={{ width: 235, height: 16 }} />
                    </div>
                    <span className=""><Skeleton style={{ width: 44.44, height: 18 }} /></span>
                  </div>
                </Link>   
              </div>
            </div>
          </Card.Body>
        </Card>
      </Col>
    </>
  );
};
export default JobLoader;