import InnerPageHeader from './../../components/User/InnerPageHeader';
import { Link, useParams, useHistory } from 'react-router-dom';
import { toast } from "react-toastify";
import React, {useEffect, useState } from 'react';
import {
  Col,
  Container,
  Row,
} from 'react-bootstrap';
import DatePicker from "react-datepicker";
//import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";
import { FetchFromServer } from "../../../utils/helpers";
import { ApiRoutes } from "../../../utils/config";
import "../../../assets/css/Datepicker/datepicker.css";

const Calender = () => {
const { id } = useParams();
const [userData, setUserData] = useState([]);
const [userCal, setUserCal] = useState([]);
const selectedTodayDate = React.createRef();
const [isOpen, setIsOpen] = useState(true);
const getuser=(()=>{
  //selectedTodayDate.current.click();
})
useEffect(() => {
    console.log(selectedTodayDate,'selectedTodayDate');
    const getBlockDates = async () => {
      const response = await FetchFromServer(
        ApiRoutes.USER_CALENDER.service,
        ApiRoutes.USER_CALENDER.url.replace(":id", id),
        ApiRoutes.USER_CALENDER.method,
        ApiRoutes.USER_CALENDER.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        
        let data=[];
        
        console.log('data',data);
        console.log(response.data.data,'response.data.data');
        let u=0;
        //if(u===0){
          //u=1;
          let newData=[];
          let date;
        for(let i=0;i<response.data.data.length;i++){
          //console.log(response.data.data.length);
          date=new Date(
            moment(response.data.data[i].day).format('YYYY-MM-DD')
          );
          newData.push(date);
          console.log(i);
        }//}
        setUserData(newData); 
        setTimeout(function()
        { 
          console.log('userData',userData);
        }, 5000 );
      }
    };
    getBlockDates();
  }, []);

return (
    <div className="calenderPage">
      <InnerPageHeader 
      id={id}
      activeTab="calender"
      />
      <Container fluid className="addNewUserPage">
        <Row >
            <Col md={12} className="mb-3 mt-4">
            {(userData.length > 0 ) ? (
              
                <DatePicker
                onInputClick={() => setIsOpen(true)}
                onClickOutside={() => setIsOpen(true)}
                open={isOpen}
                autoFocus={true}
                wrapperClassName="form-control"
                className="form-control calenderDP"
                placeholderText="Select Dates"
                //autoComplete={false}
                dateFormat="dd/MM/yyyy"
                peekNextMonth
                showMonthDropdown
                showYearDropdown
                dropdownMode="select"
                /*excludeDates={[
                  new Date(
                    moment('2021-07-12').format('YYYY-MM-DD')
                  ),
                  new Date(
                    moment('2021-07-21').format('YYYY-MM-DD')
                  )
                ]}*/
                highlightDates={userData}
                
            />
            ) : (
              <DatePicker
                onInputClick={() => setIsOpen(true)}
                onClickOutside={() => setIsOpen(true)}
                open={isOpen}
                autoFocus={true}
                wrapperClassName="form-control"
                className="form-control calenderDP"
                placeholderText="Select Dates"
                dateFormat="dd/MM/yyyy"
                peekNextMonth
                showMonthDropdown
                showYearDropdown
                dropdownMode="select"
            />
                
            ) }
            
            </Col>
        </Row>   
        </Container>
    </div>
  );
};

export { Calender };
