import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import Skeleton from 'react-loading-skeleton';
import { Col, Container, Row, Spinner } from 'react-bootstrap';

import gobackImg from '../../../assets/img/goback.svg';
import NoDataCard from '../../components/NoDataCard';
import TableFooter from '../../components/TableFooter';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

const SetRule = () => {
  const { id } = useParams();
  const [isLoading, setIsLoading] = useState(true);
  const [ruleData, setRuleData] = useState([]);
  const [taskData, setTaskData] = useState([]);
  const [totalRecord, setTotalRecord] = useState(0);
  const [tableMessage, setTableMessage] = useState('No Data Available');
  const [activePage, setActivePage] = useState(1);
  const [isUpdating, setIsUpdating] = useState('');
  const [tableFilter, setTableFilter] = useState({
    businessRule: id,
    skip: 0,
    limit: 10,
    sortBy: '',
    order: '',
    status:'Completed',
  });

  const getTaskList = async () => {
    setIsLoading(true);
    
    const taskResponse = await FetchFromServer(
      ApiRoutes.TASK_LIST_BUSINESS.service,
      ApiRoutes.TASK_LIST_BUSINESS.url,
      ApiRoutes.TASK_LIST_BUSINESS.method,
      ApiRoutes.TASK_LIST_BUSINESS.authenticate,
      tableFilter,
      undefined
    );
    setIsLoading(false);
    if (taskResponse && taskResponse.isError) {
      toast.error(taskResponse.messages[0]);
    } else {
      setTableMessage(taskResponse.messages[0]);
      setTaskData(taskResponse.data.data);
      setTotalRecord(taskResponse.data.totalRecords);
    }
  };
  useEffect(() => {
    const getScheduleInfo = async () => {
      const response = await FetchFromServer(
        ApiRoutes.BUSINESS_RULE_INFO.service,
        ApiRoutes.BUSINESS_RULE_INFO.url.replace(':id', id),
        ApiRoutes.BUSINESS_RULE_INFO.method,
        ApiRoutes.BUSINESS_RULE_INFO.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        let data = response.data.data;
        setRuleData(data);
        setTableFilter({
          ...tableFilter,
          schedule: data.schedule,
          technology: data.technology,
        }); 
      }
    };
    getScheduleInfo();
  }, []);
  useEffect(() => {
    getTaskList();
  }, [tableFilter]);

  useEffect(() => {
    window.scrollTo(0, 0)
  }, []);

  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
    }
  };
  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === 'pageSize') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
    }
  };
  const handleChangeStatus = async (status, id) => {
    setIsUpdating(id);
    const response = await FetchFromServer(
      ApiRoutes.CHANGE_TASK_STATUS.service,
      ApiRoutes.CHANGE_TASK_STATUS.url.replace(':id', id),
      ApiRoutes.CHANGE_TASK_STATUS.method,
      ApiRoutes.CHANGE_TASK_STATUS.authenticate,
      undefined,
      { status: status ? 'Completed' : 'Pending' }
    );
    setIsUpdating('');
    if (response && response.isError) {
      toast.error(response.messages[0]);
    } else {
      getTaskList();
      toast.success(response.messages[0]);
    }
  };
  let categoryName = '';
  let showCategoryName = '';
  let subcategoryName = '';
  let showSubcategoryName = '';
  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link
          to={`/rates/rules/schedule/${ruleData.schedule}`}
          className="btn btn_goback p-0"
          role="button"
        >
          <img className="mr-3" src={gobackImg} alt={gobackImg} /> Go Back
        </Link>
      </div>

      <Container fluid>
        <Row className="mt-4 pb-5">
          <Col sm={12}>
            <h6 class="font-weight-bold mb-0 headTitle position-relative primary">
              {ruleData.name}
            </h6>
          </Col>

          <Col sm={12} className="jobHistoryPage SetRulesPage">
            <div className="table-responsive">
              <div className="table mt-3 rateTask_table setRule position-relative">
                <div className="tr bg-transparent">
                  <div className="th">ITEM ID</div>
                  <div className="th">UNIT OF MEASURE</div>
                  <div className="th">APPLICATION</div>
                  <div className="th">RATES($)</div>
                  <div className="th">SCHEDULE</div>
                  <div className="th" style={{ 'min-width': '100px' }}>
                    BUSINESS HOURS
                  </div>
                  <div className="th"></div>
                </div>
                {taskData &&
                  isLoading === false &&
                  taskData.map((data, id) => {
                    if (categoryName !== data.categoryName) {
                      categoryName = data.categoryName;
                      showCategoryName = data.categoryName;
                    } else {
                      showCategoryName = '';
                    }
                    if (subcategoryName !== data.subcategoryName) {
                      subcategoryName = data.subcategoryName;
                      showSubcategoryName = data.subcategoryName;
                    } else {
                      showSubcategoryName = '';
                    }
                    return (
                      <div
                        class={`tr position-relative ${
                          showCategoryName && showSubcategoryName
                            ? 'twoCategory'
                            : ''
                        } ${
                          (!showCategoryName && showSubcategoryName) ||
                          (showCategoryName && !showSubcategoryName)
                            ? 'oneCategory'
                            : ''
                        } ${
                          !showCategoryName && !showSubcategoryName
                            ? 'noCategory'
                            : ''
                        }`}
                        key={id}
                      >
                        <div className="td">{data.itemId}</div>
                        <div className="td">{data.uomName}</div>
                        <div className="td">{data.technologyName}</div>
                        <div className="td">{data.rates}</div>
                        <div className="td">{data.scheduleName}</div>
                        <div className="td">{data.workHours}</div>
                        <div className="td switchButton">
                          <div class="tgl-group position-relative">
                            {isUpdating === data._id ? (
                              <Spinner
                                as="span"
                                animation="border"
                                size="sm"
                                role="status"
                                aria-hidden="true"
                              />
                            ) : (
                              ''
                            )}
                            <input
                              class="tgl tgl-light"
                              id={`display-address-${id}`}
                              type="checkbox"
                              checked={true}
                              /*checked={data.status === 'Pending' ? false : true}
                              onChange={(e) => {
                                handleChangeStatus(e.target.checked, data._id);
                              }}*/
                              disabled={isUpdating === data._id ? true : false}
                            />
                            {/* <input
                              class="tgl tgl-light"
                              id={`display-address-${id}`}
                              type="checkbox"
                              checked={true}
                            /> */}
                            <label
                              class="tgl-btn"
                              for={`display-address-${id}`}
                            >
                              <span class="yes">Yes</span>
                              <span class="no">No</span>
                            </label>
                          </div>
                        </div>
                        {showSubcategoryName ? (
                          <span
                            className="title"
                            style={{ background: data.colorCode }}
                          >
                            {showSubcategoryName}
                          </span>
                        ) : (
                          ''
                        )}
                        {showCategoryName ? (
                          <span
                            className="title categoryName"
                            style={{ background: data.colorCode }}
                          >
                            {showCategoryName}
                          </span>
                        ) : (
                          ''
                        )}
                        <div className="description">
                          <p class="mb-0">
                            <span>Item Description:</span>
                            {data.description}
                          </p>
                        </div>
                      </div>
                    );
                  })}

                {taskData.length === 0 && isLoading === false ? (
                  <tr>
                    <td colSpan="7">
                      <NoDataCard title={tableMessage} />
                    </td>
                  </tr>
                ) : (
                  ''
                )}
                {isLoading ? (
                  <>
                    <div class="tr position-relative">
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Link to="#">
                          <Skeleton />
                        </Link>
                      </div>
                      <span className="title" style={{ width: 300 }}>
                        <Skeleton />
                      </span>
                      <div className="description">
                        <p class="mb-0">
                          <Skeleton />
                        </p>
                      </div>
                    </div>
                    <div class="tr position-relative">
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Link to="#">
                          <Skeleton />
                        </Link>
                      </div>
                      <span className="title" style={{ width: 300 }}>
                        <Skeleton />
                      </span>
                      <div className="description">
                        <p class="mb-0">
                          <Skeleton />
                        </p>
                      </div>
                    </div>
                    <div class="tr position-relative">
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Skeleton />
                      </div>
                      <div className="td">
                        <Link to="#">
                          <Skeleton />
                        </Link>
                      </div>
                      <span className="title" style={{ width: 300 }}>
                        <Skeleton />
                      </span>
                      <div className="description">
                        <p class="mb-0">
                          <Skeleton />
                        </p>
                      </div>
                    </div>
                  </>
                ) : (
                  ''
                )}
              </div>
            </div>
          </Col>
        </Row>
        <TableFooter
          totalRecord={totalRecord}
          activePage={activePage}
          tableFilter={tableFilter}
          handlePageChange={handlePageChange}
          handleSearch={handleSearch}
        />
      </Container>
    </>
  );
};

export { SetRule };
