import React from 'react';
import { Form, Row, Col } from 'react-bootstrap';

const TechnologyInformation = (props) => {
  const { handleChange, errors, values, touched } = props;
  return (
    <Row>
      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group>
          <Form.Label className="mb-0">Technology Name</Form.Label>
          <Form.Control
            type="text"
            name="technologyName"
            placeholder="FTTN"
            onChange={handleChange}
            value={values ? values.technologyName : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.technologyName && touched.technologyName
              ? errors.technologyName
              : null}
          </span>
        </Form.Group>
      </Col>
      <Col xs={12} sm={6} md={6} lg={4}>
        <Form.Group>
          <Form.Label className="mb-0">Technology Description</Form.Label>
          <Form.Control
            type="text"
            name="technologyDescription"
            placeholder="Fibre to the curb"
            onChange={handleChange}
            value={values ? values.technologyDescription : ''}
          />
          <span className="errorMsg">
            {' '}
            {errors.technologyDescription && touched.technologyDescription
              ? errors.technologyDescription
              : null}
          </span>
        </Form.Group>
      </Col>
    </Row>
  );
};

export { TechnologyInformation };
