import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Col, Container, Row, Table } from 'react-bootstrap';

import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { InnerPageHeader } from '../../components/Rate';
import TableFooter from '../../components/TableFooter';
import NoDataCard from '../../components/NoDataCard';
import { TableLoader } from '../../components/loader/Resusable/Table';

const SubcategoryList = () => {
  const [subcategoryData, setSubcategoryData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [totalRecord, setTotalRecord] = useState(0);
  const [activePage, setActivePage] = useState(1);
  const [tableFilter, setTableFilter] = useState({
    skip: 0,
    limit: 10,
  });

  useEffect(() => {
    const getSubcategoryList = async () => {
      setIsLoading(true);
      const response = await FetchFromServer(
        ApiRoutes.SUBCATEGORY_LIST.service,
        ApiRoutes.SUBCATEGORY_LIST.url,
        ApiRoutes.SUBCATEGORY_LIST.method,
        ApiRoutes.SUBCATEGORY_LIST.authenticate,
        undefined
      );
      setIsLoading(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setSubcategoryData(response.data.data);
        setTotalRecord(response.data.totalRecords);
      }
    };
    getSubcategoryList();
  }, [tableFilter]);

  const handlePageChange = async (pageNumber) => {
    if (pageNumber !== activePage) {
      setActivePage(pageNumber);
      setTableFilter({
        ...tableFilter,
        skip: (pageNumber - 1) * tableFilter.limit,
      });
    }
  };
  const handleSearch = async (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setActivePage(1);
    if (name === 'pageSize') {
      setTableFilter({
        ...tableFilter,
        skip: 0,
        limit: value,
      });
    }
  };
  return (
    <>
      <InnerPageHeader
        activeTab="subCategory"
        btnText="Add New Subcategory"
        btnLink="/rates/subcategory/add"
      />
      <Container fluid>
        <Row>
          <Col md={12} className="jobHistoryPage">
            {isLoading ? <TableLoader columns={3} rows={10} /> : ''}
            <Table hover responsive className="mt-3">
              <tbody>
                <tr className="first-child">
                  <th className="border-0">TECHNOLOGY</th>
                  <th className="border-0">SUB CATEGORY</th>
                  <th className="border-0"></th>
                </tr>
                {subcategoryData &&
                  isLoading === false &&
                  subcategoryData.map((data, id) => {
                    return (
                      <tr key={id}>
                        <td>{data.technologyName}</td>
                        <td>{data.categoryName}</td>
                        <td className="Width200">
                          <Link to={`/rates/subcategory/edit/${data._id}`}>
                            Edit Rates
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                {subcategoryData.length === 0 && isLoading === false ? (
                  <tr>
                    <td colSpan="3">
                      <NoDataCard />
                    </td>
                  </tr>
                ) : (
                  ''
                )}
              </tbody>
            </Table>
          </Col>
        </Row>
        <TableFooter
          totalRecord={totalRecord}
          activePage={activePage}
          tableFilter={tableFilter}
          handlePageChange={handlePageChange}
          handleSearch={handleSearch}
        />
      </Container>
    </>
  );
};

export { SubcategoryList };
