import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { useFormik } from 'formik';

import {
  Nav,
  Tab,
  Form,
  Button,
  Spinner,
  Col,
  Container,
  Row,
  Card,
} from 'react-bootstrap';
import gobackImg from '../../../assets/img/goback.svg';

import { AddBusinessRuleSchema } from '../../../validators';
import { FetchFromServer } from '../../../utils/helpers';
import { ApiRoutes } from '../../../utils/config';

import { RuleInformation } from '../../components/Rate';

const AddRule = () => {
  const history = useHistory();
  const [isBtnDisable, setDisable] = useState(false);
  const [technologyData, setTechnologyData] = useState(false);
  const [scheduleData, setScheduleData] = useState(false);
  const { handleSubmit, handleChange, values, errors, touched } = useFormik({
    initialValues: { schedule: '', technology: '', name: '' },
    validationSchema: AddBusinessRuleSchema,
    onSubmit: async (values) => {
      setDisable(true);
      const response = await FetchFromServer(
        ApiRoutes.ADD_BUSINESS_RULE.service,
        ApiRoutes.ADD_BUSINESS_RULE.url,
        ApiRoutes.ADD_BUSINESS_RULE.method,
        ApiRoutes.ADD_BUSINESS_RULE.authenticate,
        undefined,
        values
      );
      setDisable(false);
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        toast.success(response.messages[0]);
        history.push({
          pathname: '/rates/rules',
        });
      }
    },
  });

  useEffect(() => {
    const getTechnologyList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.TECHNOLOGY_LIST.service,
        ApiRoutes.TECHNOLOGY_LIST.url,
        ApiRoutes.TECHNOLOGY_LIST.method,
        ApiRoutes.TECHNOLOGY_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setTechnologyData(response.data.data);
      }
    };
    const getScheduleList = async () => {
      const response = await FetchFromServer(
        ApiRoutes.SCHEDULE_LIST.service,
        ApiRoutes.SCHEDULE_LIST.url,
        ApiRoutes.SCHEDULE_LIST.method,
        ApiRoutes.SCHEDULE_LIST.authenticate,
        undefined
      );
      if (response && response.isError) {
        toast.error(response.messages[0]);
      } else {
        setScheduleData(response.data.data);
      }
    };
    getTechnologyList();
    getScheduleList();
  }, []);

  return (
    <>
      <div className="inner_subheader justify-content-end">
        <Link to="/rates/rules" className="btn btn_goback p-0" role="button">
          <img className="mr-3" src={gobackImg} alt={gobackImg} />
          Go Back
        </Link>
      </div>

      <Container fluid className="addNewUserPage">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12} xl={12} className="mb-3 mt-4">
            <Card>
              <Card.Body>
                <div className="p-24">
                  <Form onSubmit={handleSubmit}>
                    <Tab.Container
                      id="left-tabs-example"
                      defaultActiveKey="profile"
                    >
                      <Nav
                        as="ul"
                        className="nav-pills mb-4 position-relative "
                      >
                        <Nav.Item as="li">
                          <Nav.Link eventKey="profile">
                            New Business Rule
                          </Nav.Link>
                        </Nav.Item>
                      </Nav>
                      <Tab.Content>
                        <Tab.Pane eventKey="profile">
                          <RuleInformation
                            handleChange={handleChange}
                            technologyData={technologyData}
                            scheduleData={scheduleData}
                            errors={errors}
                            values={values}
                            touched={touched}
                          />
                        </Tab.Pane>
                      </Tab.Content>
                    </Tab.Container>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col md={12} className="mb-5 text-right pageButtomBtn">
            <Button
              variant=""
              className="px-5 py-2 rounded btn-pink ml-3 form_btn"
              style={{ fontSize: '14px' }}
              onClick={handleSubmit}
              disabled={isBtnDisable}
            >
              {isBtnDisable ? (
                <Spinner
                  as="span"
                  animation="border"
                  size="sm"
                  role="status"
                  aria-hidden="true"
                />
              ) : (
                ''
              )}{' '}
              Add Business Rule
            </Button>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export { AddRule };
