import React from 'react';
import { Link } from 'react-router-dom';

const redirect = () => {
	localStorage.setItem('renderStatus', 1); 
}

const InnerPageHeader = (props) => {
  const { activeTab = 'rates', btnLink = '', btnText = '' } = props;

  return (
    <div className="inner_subheader">
      <div className="d-flex align-items-center tabBtn">
        <Link
          to="/rates"
          className={`btn ${activeTab === 'rates' ? 'active' : ''}`}
        >
          Tasks
        </Link>
        <Link
          to="/rates/rules"
          className={`btn ml-2 ${
            activeTab === 'businessRules' ? 'active' : ''
          }`}
        >
          Business Rules
        </Link>
        <Link
          to="/rates/uom"
          className={`btn ml-2 ${activeTab === 'UoM' ? 'active' : ''}`}
        >
          Unit Of Measurement (UoM)
        </Link>
        {/* <Link
          to="/rates/technology"
          className={`btn ml-2 ${
            activeTab === 'technologyApplication' ? 'active' : ''
          }`}
        >
          Technology Application
        </Link> */}
        {/* <Link
          to="/rates/subcategory"
          className={`btn ml-2 ${activeTab === 'subCategory' ? 'active' : ''}`}
        >
          Sub Category
        </Link> */}
      </div>

      {btnText ? (
        <Link to={btnLink} onClick={redirect} className="btn btn-pink btn-pink_rates">
          {btnText}
        </Link>
      ) : (
        ''
      )}
    </div>
  );
};

export { InnerPageHeader };
